// A simple Host object model
export class DellHostOverview {
    modelName: string;
    hostName: string;
    serviceTag: string;
    powerState: string;
    IDRACIPAddress: string;
    NICIPAddress: string;
    assetTag: string;
    warranty: string;
    warrantyStatus: string;
    warrantyNotificationEnabled: string;
    operatingSystem: string;
    operatingSystemVersion: string;
    BIOSFirmware: string;
    IDRACFirmware: string;
    connectionProfileName: string;
    inventoryStatus: string;
    systemIDExt: string;
    isBlade: string;
    chassisInfo: ChassisInfo;
    vCenterIPAddress: string;
    frmState: string;

constructor(clone: DellHostOverview = null) {
      this.modelName = clone ? clone.modelName : "";
      this.hostName = clone ? clone.hostName : "";
      this.serviceTag = clone ? clone.serviceTag : "NA";
      this.powerState = clone ? clone.powerState : "";
      this.IDRACIPAddress = clone ? clone.IDRACIPAddress : "NA";
      this.NICIPAddress = clone ? clone.NICIPAddress : "";
      this.assetTag = clone ? clone.assetTag : "";
      this.warranty = clone ? clone.warranty : "";
      this.warrantyNotificationEnabled = clone ? clone.warrantyNotificationEnabled : "";
      this.operatingSystem = clone ? clone.operatingSystem : "";
      this.operatingSystemVersion = clone ? clone.operatingSystemVersion : "NA";
      this.NICIPAddress = clone ? clone.NICIPAddress : "";
      this.BIOSFirmware = clone ? clone.BIOSFirmware : "";
      this.IDRACFirmware = clone ? clone.IDRACFirmware : "";
      this.connectionProfileName = clone ? clone.connectionProfileName : "";
      this.inventoryStatus = clone ? clone.inventoryStatus : "";
      this.systemIDExt = clone ? clone.systemIDExt : "NA";
      this.isBlade = clone ? clone.isBlade : "";
      this.chassisInfo = clone ? clone.chassisInfo : null;
      this.vCenterIPAddress = clone ? clone.vCenterIPAddress : "";
      this.frmState = clone ? clone.frmState : "";
   }
}


export class ChassisInfo 
{
    cmcURL: string;
    chassisServiceTag: string;
    bladeLocation: string;
    constructor(clone: ChassisInfo = null) 
    {
      this.cmcURL = clone ? clone.cmcURL : "";
      this.chassisServiceTag = clone ? clone.chassisServiceTag : "";
      this.bladeLocation = clone ? clone.bladeLocation : "NA";
    }
}





