
import { Component, OnInit, OnDestroy, ViewChild,ChangeDetectorRef, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { TemplateRef } from '@angular/core';
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";

import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionTestRequest, DellVCenterService,ConnectionProfile, DellConnectionProfileService, NavService,ModalConfigService,ModalConfig } from "../../services/index";
import {MonitorWizardComponent} from "../wizard/monitor-wizard.component";


@Component({
   selector: "connection-profile",
   styleUrls: ["./connection-profile.component.scss"],
   templateUrl:"./connection-profile.component.html"
})


export class ConnectionProfileComponent  implements OnInit, OnDestroy
{
  private refreshViewSub: Subscription;
  @Input() monitorParam:string;
  profiles: ConnectionProfile[];
  openval=false;
  mainNavStatus="Compliance";
  instance: ConnectionProfileComponent;
  TotalProfile:number=0;
  profileData=[];
  status_row=1;
  selectedRowNum:number;
  multipleSelect=[];
  allexpand:boolean=false;
  FormVal:{};
  gridVal:any;
  loading: boolean = true;
  error:any;
  testStatus:boolean=false; 
  vcenterNodes:VCenterRootNode[];
  vcenterHost:any=[];
  //@ViewChild(BasicWizardTestComponent) wizardtest: BasicWizardTestComponent;
 
  constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               public ref:ChangeDetectorRef,
               public modalService:ModalConfigService,
               private refreshService: RefreshService,
               private dellVCenterService:DellVCenterService,
               private route: ActivatedRoute) {
                  this.instance = this;
                  this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                   () => this.refreshView());
   }

   ngOnInit(): void {  
      this.nav.setViewType('summary');
      //this.wizardtest.ngOnInit();
      this.refreshView();
      this.getvCenterTree();
      //let infoMsg="this is the good one concept";
      //this.appAlertService.showInfo(infoMsg);
      
   }
 
   ngOnDestroy(): void { this.refreshViewSub.unsubscribe();}
   
   addrow(){  
	  //this.openval=true; 
    this.FormVal ="not defined";
    this.openModel();
  }

  openModel(){
    let tempCustomData={};
    tempCustomData["FormVal"]=this.FormVal;
    tempCustomData['profiles']=this.profiles;
    let config: ModalConfig = this.modalService.createAddWizardConfig();
       config.contextObjects=[{ id: "urn:vri:samples:ChassisA:chassis-3" },{ id: "urn:vri:samples:ChassisA:chassis-2" }];
       config.customData=tempCustomData; 
    this.gs.getHtmlPlatform().modal.open(config);
  }


   showHost(val,i) {
      val.indexPosition=val.profileName+''+val.networkAddress;
      this.multipleSelect[i]=val;
	    this.allexpand=false;
   }

   statusChange(val){
     this.openval=val;
  }

   testWizard(){  this.testStatus=true; }

   testWizardEmit(status){  this.testStatus=status;  }

   hideHost(val,i) {
	   val.indexPosition="";
	   this.allexpand=false;
   }

   allExpandicon(param){
	    if(param==='expand'){
	    	return this.allexpand=true;   
		  }
      else{
			  return this.allexpand=false;
		  }
   }
   
  
   private refreshView(): void { 
     console.log("comminf");
    this.profileData=[]; 
    this.getProfiledata();
  }

   
   private getProfiledata(): void {
      this.profileData.splice(0, this.profileData.length);
      this.loading = true;
      this.profileData=[];
      this.dellConnectionProfileService.getConnectionProfiles()
            .subscribe(profiles => {
              this.profiles = profiles;
               this.loading = false;
               this.multipleSelect.length = 0;
               //alert("no ");
               if(profiles.length !=undefined && profiles.length != 0){
                 this.loading=false;
                this.updateProfilesForRendering(profiles);
               }
              

            },error=>{
                  this.error=error;
                  console.log(this.error);
                  this.loading = false;
            });

         
  }

   updateProfilesForRendering(profiles)
   {  

     if (profiles.length === 0) {
       this.profileData=[]; 
    }else{
        this.profileData=[]; 
        var refIndex=0;
      this.profileData.splice(0, this.profileData.length);
      for(let profile of profiles) 
      {
	      var temp={ 		
     
          profileId       : profile.profileId,
                    profileName     : profile.profileName,
                    networkAddress	:	profile.console.networkAddress,
                   description:profile.description, 
                     hostIp	    	  :	profile.console.consoleEntity[0] ? profile.console.consoleEntity[0].name : "",
                    hostIpList      : profile.console.consoleEntity,
				            dropdown			  :	profile.console.consoleEntity.length > 1 ?'yes' : 'no',
				            idracCredential	:	profile.idracCredential.certificateCheck,
				            hostCredential	:	profile.hostCredential.certificateCheck,
				            creationDate		:	profile.creationDate,
				            updatedDate		  :	profile.updatedDate,
						        referenceIndex  : refIndex,
				            updatedBy		    :	profile.updatedBy,
							idracObject         :   profile.idracCredential,
							hostObject          :  profile.hostCredential
				          };
       this.profileData.push(temp); 	
       //this.refreshService.refreshView();
       var first:boolean = true;
       for(let entity of profile.console.consoleEntity)
       {
          if(!first)
           {
				      var tmpProfile={ hostIp : entity.name,
									  reference : profile.profileName+''+profile.console.networkAddress,
									  referenceIndex:refIndex,child:1}
			        this.profileData.push(tmpProfile);	
			     }
           first = false;	
			 }
		   refIndex++;	
      }   
      this.loading = false;
      //this.refreshService.refreshView();
    }
  }

	onUpdate(message:any):void { }
  
  editRow(obj,i){ 
      this.gridVal=obj;
      this.selectedRowNum = i;
      console.log(this.vcenterHost);
      console.log(this.gridVal);
      document.getElementById("editprofile").classList.remove('disableIcon');
      document.getElementById("deleteprofile").classList.remove('disableIcon');
      if(obj.hostIpList.length != 0 ){
        document.getElementById("testprofile").classList.remove('disableIcon');
      }
     
    if(obj.profileId !==undefined){
           this.selectedRowNum = i;
           this.gridVal=obj;
           document.getElementById("editprofile").classList.remove('disableIcon');
           document.getElementById("deleteprofile").classList.remove('disableIcon');
           if(obj.hostIpList.length != 0){
            document.getElementById("testprofile").classList.remove('disableIcon');
           }else{
            document.getElementById("testprofile").classList.add('disableIcon');
           }
     }else{
          this.selectedRowNum = undefined;
          this.gridVal={};
           document.getElementById("editprofile").classList.add('disableIcon');
           document.getElementById("deleteprofile").classList.add('disableIcon');
           document.getElementById("testprofile").classList.add('disableIcon');
       
      }

  }

  deleteProfile(){
        this.dellConnectionProfileService.deleteConnectionProfile(this.gridVal.profileId,this.gridVal.networkAddress).
        subscribe(response => 
        {
          if(response){ 
            this.refreshView(); 
          }
        }); 
  }

  editGridrow(){  
    this.FormVal = this.gridVal;
    this.openval=true; 
  }

  
  getvCenterTree(){

    	this.dellVCenterService.getVCenterTree([], "true")
		.then(response => {
		   if (response.length === 0) {
			  return;
		   }
		   console.log(response);
//       this.selectedHost(response);
       this.vcenterNodes = response;
    // this.getServiceTag(response)
    
		   
		});
  }




  selectedHost(arg)
  {  
   // alert("test inside selectedHost")
    console.log(this.vcenterHost);
   for(let i of arg )
     {
    //   alert(JSON.stringify(i));
     if(i.type == "HOST_SYSTEM"){
             this.vcenterHost[i.name]=i.serviceTag;
        }
       if(i.rootNode){
        this.selectedHost(i.rootNode.children); 
      }else{
        console.log(JSON.stringify(i.children));
           this.selectedHost(i.children);
      }
     // this.refreshService.refreshView();
    }
    
  } 
  
}


