import { Component, Input,Output, OnInit, ViewChild,EventEmitter,SimpleChanges,ChangeDetectorRef } from "@angular/core";
import { Wizard } from "clarity-angular";
import { GlobalsService, RefreshService, I18nService,APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity,DellVCenterService,DellConnectionProfileService} from "../../services/index";

@Component({
   selector: "monitor-wizard-host",
   styleUrls: ["./monitor-wizard-associatedhost.component.scss"],
   templateUrl: "./monitor-wizard-associatedhost.component.html"  
})

export class AssociatedHost implements OnInit {
    flag:boolean;
    @Input() openModal1:boolean;
	@Output() update= new EventEmitter<any>();
     vcenterNodes:VCenterRootNode[];
	@Input() EditedassociateHosts=[];
	@Input() hostsAssociated=[];
	@Input() hostEnable:boolean;
    @Input() hostProfiles=[];
    hostcontent:string;
    @Output() notify = new EventEmitter<boolean>();
    selectedHosts:ConsoleEntity[]= [];
	index:number=0;
	item:any;
    modelName:any; 
    vcIP:string="";
    temp=[];
	countval:number;
    conditionTrue:boolean=true;
	conditionFalse:boolean=false;
    selection:any[]=[];
    warningNotification:boolean=false;
    hostProfilesVal:any;


    constructor( private dellVCenterService: DellVCenterService,
                 private dellConnectionProfileService: DellConnectionProfileService,
                 private refreshService: RefreshService,
                 private ref:ChangeDetectorRef, 
                 public i18n:I18nService ) {}
    ngOnChanges(changes: SimpleChanges){
        this.hostProfilesVal=this.hostProfiles;
    }
    
      ngOnInit() { 
         
        this.getAssociatedHost();
        this.refreshView();
    }      

    private refreshView(): void { this.getAssociatedHost(); }

    getAssociatedHost(){
        this.hostProfilesVal=this.hostProfiles;
         this.dellVCenterService.getVCenterTree([], "true")
            .then(vcenterNodes => {
               if (vcenterNodes.length === 0) {
                  return;
               }
               this.vcenterNodes = vcenterNodes;
             //  this.refreshService.refreshView();
          });
        
  }  
    
     getProfileName(host){
                if(this.hostProfilesVal){
                    for(let profileName of this.hostProfilesVal){
                        for(let profile of profileName.console.consoleEntity){
                            if(profile.name===host){
                                return profileName.profileName;
                            }
                        
                        }  
                    }
                }else{
                    return "";
                } 
                
  }
   
    openwizard()
    {  
       this.selectedHosts.length=0;
       
       console.log(JSON.stringify(this.vcenterNodes));
	   if(this.vcenterNodes.length>0)
       {    
            this.item=this.vcenterNodes;
			this.selectedHost(this.vcenterNodes);
			if(this.temp && this.vcIP)
            {
                for(let child of this.temp)
                {
                   let ce:ConsoleEntity  = new ConsoleEntity();
                   ce.name = child.name;
                   ce.entityId = child.managedObjectId;
                   ce.type = "HOST_SYSTEM";
                   ce.serviceTag = child.serviceTag;
                   ce.vCenterIP = this.modelName;
				   this.selectedHosts.push(ce);
				}
            }
			
		    document.getElementById("gridData").style.visibility="visible";
            document.getElementById("gridDataheader").style.visibility="visible";
		    document.getElementById("hostcontent").style.visibility="hidden";
			this.flag=false;
			this.update.emit(this.selectedHosts);
        }
        else
        {
             document.getElementById("hostcontent").style.visibility="visible";
			
			  document.getElementById("gridData").style.visibility="hidden";
              document.getElementById("gridDataheader").style.visibility="hidden";
			  this.selectedHosts.length = 0;
			  this.flag=false;
			
        }
	    this.notify.emit(this.flag);
        document.getElementById("modal-window").style.visibility="hidden";
        this.temp.length=0;
	   this.hostEnable=false;
	 }

	 
selectedHost(arg)
{
   if(arg){
    for(let i of arg)
        {
            this.vcIP=i.vcenterIp;
            console.log(JSON.stringify(i));
            for(let j of i.rootNode.children){
            this.callBackChildrenFunction(j.children);
            }	 
        }
   }

}

callBackChildrenFunction(arg){
   if(arg){
			for(let x of arg){
				console.log("loop condition for loop contines");
				console.log(JSON.stringify(x));
				
				if(x.type == "HOST_SYSTEM")
				{
					console.log(x.model);
					this.temp.push(x);
				}
				
				console.log("this is the children functions");

				 if(x.hasOwnProperty("children")){
					this.callBackChildrenFunction(x.children);				 
				}else{
					console.log("else call back function");
					console.log(JSON.stringify(x));
					

				}
			  
		}
   }
}


}



