import { Component, Input,Output, OnInit, ViewChild,EventEmitter,OnChanges,SimpleChanges,ChangeDetectorRef,Directive,ChangeDetectionStrategy  } from "@angular/core";
import { Wizard } from "clarity-angular";
import { VCenterRootNode, ConsoleEntity} from "../../services/index";
import { DellHostService,DellChassis,DellChassisService,ChassisCredentialProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {Profile,ChassisTestResult} from "../../services/index";
import {FormGroup, FormControl, Validators,ReactiveFormsModule,FormsModule } from "@angular/forms";

@Component({
   selector: "monitor-wizard-chasishost",
   styleUrls: ["./monitor-wizard-chasisassociatedhost.component.scss"],
   templateUrl: "./monitor-wizard-chasisassociatedhost.component.html"  
})

export class ChasisAssociatedHost implements OnInit {
    @Input() chassisModal:boolean;
    @Input() selectedRow:any;
    @Input() selectedVal=[];
    @Output() notify = new EventEmitter<boolean>();
    @Output() update= new EventEmitter<any>();
    @Output() showgrid= new EventEmitter<boolean>();
    @Output() cancelchassis= new EventEmitter<boolean>();
    @Output() selectedchassis = new EventEmitter<boolean>();
    private refreshViewSub: Subscription;
    flag:boolean;
    selection=[];
    selectval=[];
    chassis=[];
    loading:boolean;
    cancelval:boolean=true;
    oldVal:boolean;
    chassisListData=[];
    chassisListDataval=[];
    index:number=0;
    chassisSel:any;
    arr=[];
    indexval=[];
    @Input() editchassis:string;
    associatedChassis:DellChassis[]=[];
    warningNotification:boolean=false;

    constructor(    private gs: GlobalsService,
                    public  i18n: I18nService,
                    private location: Location,
                    public  nav: NavService,
                    private refreshService: RefreshService,
                    private dellChassisService:DellChassisService,
                    private hostService:DellHostService,  
                    private route: ActivatedRoute,
                    private ref:ChangeDetectorRef   ) {
          this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
      }
    ngOnInit() { 
        this.refreshView();
    }      

    private refreshView(): void {this.getAssociatedHost(); }
   
     selectedChanged(selected){
          if( selected.length>0 ){
            this.oldVal=true;
            this.showgrid.emit(true);
           }
          else{
            this.oldVal=false;
          }
          if(this.editchassis=="add"){
            for(var i=0;i<selected.length;i++){ 
                if(selected[i].chassisProfileName==="" || selected[i].chassisProfileName===null ){
                    this.warningNotification=false;
                }else{
                    this.warningNotification=true;
                }
            } 
          }else{
            this.warningNotification=false;
          }  

            this.ref.detectChanges(); 
            
  }
  ngAfterViewChecked(){
    if(this.selection.length>0 ){
        this.showgrid.emit(true);
    }else{  
      this.warningNotification=false;
      this.showgrid.emit(true);
    }
     
    
 }  
    
  saveData(arg){       
     this.cancelchassis.emit(false);
      this.update.next(arg);
      if(arg.length>0){
        this.showgrid.emit(true);
      }
      else{
         this.showgrid.emit(false);
      }
      this.flag=false;
      this.chassisSel=arg;
      this.notify.emit(this.flag);
      this.selectedchassis.emit(this.chassisSel); 
      document.getElementById("modal-window1").style.visibility="hidden"; 
  }
     
   cancel(){ 
        this.cancelchassis.emit(true);
        this.flag=false;
		   	this.notify.emit(this.flag);       
        document.getElementById("modal-window1").style.visibility="hidden";
    }

    selectRow(arg,index,selection){
    this.chassis.push(arg);
    this.arr.push(index);
    }   

    getAssociatedHost(){
     
      this.hostService.getDellChassis()
      .then(response =>{
                        if (response.length === 0) {
                          this.ref.markForCheck(); 
                          return [];
                        }
                        if(response.length != undefined){
                          this.associatedChassis=response;
                          this.getSelectedCHassis(response);
                        }
                         this.ref.markForCheck(); 
                        
                  },
                  error=>{
                        console.log("error");
                  }
              )   
              
              
             console.log(this.associatedChassis);
             
  }
  
 getSelectedCHassis(res){
          if(this.editchassis=="edit"){
                this.showgrid.emit(true); 
            for(var i=0;i<this.selectedRow.length;i++){
                 if(res[i].chassisProfileName === this.selectedRow[i].chassisProfileName){
                  this.selection.push(res[i]);
                  this.ref.markForCheck(); 
                 }
             }
           }
    
   }
}



