
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellLogService, LogMessage , DellConnectionProfileService,LogRequestData, NavService } from "../../services/index";

@Component({
   selector: "system-log",
   styleUrls: ["./system-log.component.scss"],
   templateUrl:"./system-log.component.html"
  })

export class SystemLogComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() jobNavStatus:string;
  instance: SystemLogComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  logData:LogMessage;
  todayDate:any=new Date();
  filterCategory:string=null;
  selected:string="all";
  selectedRange:string="lastWeek"
  errorDate:string;
  customFromDate:Date;
  customToDate:Date;
  startDate:any=new Date(this.todayDate-(7*86400000)).getTime().toString();
  endDate:any  =new Date(this.todayDate-(-1*86400000)).getTime().toString();

   USER_FACING_SUCCESS:string = 'USER_FACING_SUCCESS';
   USER_FACING_WARNING:string = 'USER_FACING_WARNING';
   USER_FACING_INFO:string    = 'USER_FACING_INFO';
   USER_FACING_ERROR:string   = 'USER_FACING_ERROR';
       
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private dellLogService:DellLogService,  
               private route: ActivatedRoute) {
      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void 
   {  
      this.nav.setViewType('summary');
      this.refreshView();
   }
  
   private refreshView(): void {
      this.loading=true;
      this.getLogs(this.filterCategory);
   }

   filterDateRange(range){
        this.selectedRange=range;
          if(range !='custom'){
            switch(range){
              case 'lastWeek':
                this.startDate  =  new Date(this.todayDate-(7*86400000)).getTime().toString();
                this.endDate    =  new Date(this.todayDate-(-1*86400000)).getTime().toString();
              break;
              
              case 'lastMonth':
                 this.startDate  =  new Date(this.todayDate-(30*86400000)).getTime().toString();
              break;
              
              case 'lastYear':
                this.startDate  =  new Date(this.todayDate-(365*86400000)).getTime().toString();
              break;
            
               } 

               this.refreshView();  
          }
          
   }
   filterCustomRange(startDate,endDate){
      if(startDate !== endDate){
          if(startDate){
            this.startDate     =  new Date(startDate).getTime().toString();
            this.customFromDate= startDate;
          }
          if(endDate){
            this.endDate      =  new Date(endDate).getTime().toString(); 
            this.customToDate = endDate;
          }
        this.errorDate=undefined;
        this.refreshView();
      }else{
       this.errorDate="Both date not same";
      }
   }
   
   categoryChange(val){
    this.selected=val;
      switch(val){
            case 'all':						 
              this.filterCategory = null;
            break;					 
            case 'info':
              this.filterCategory =this.USER_FACING_SUCCESS;						 
            break;
            
            case 'warning':						 
              this.filterCategory =this.USER_FACING_WARNING;
            break;
            
            case 'error':						 
              this.filterCategory =this.USER_FACING_ERROR;
            break;
    }
     this.refreshView();
   }        

   getLogs(category){
   this.todayDate=new Date();
    let  request: LogRequestData = new LogRequestData();
    request.pageSize =1000;
    request.currentPage=1;
    request.sortBy='Date';
    request.sortOrder='Desc';
    request.filterCategory=category;
    request.startDate =this.startDate
    request.endDate   =this.endDate;
    this.dellLogService.getLog(request).
          then(response => 
          {
            this.logData=response;
            this.loading=false;
          },
          error=>{
              console.log(error);
          });
  }
}


