import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import {Wizard} from "clarity-angular";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";

@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "connectionprofile-wizard",
   styleUrls: ["./monitor-connectionprofile-wizard.component.scss"],
   templateUrl: "./monitor-connectionprofile-wizard.component.html"

})

export class ConnectionProfileWizardComponent implements OnInit {
   @ViewChild("wizard") wizard: Wizard;
   @Input() openStatus:boolean;
   vcenterNodes: VCenterRootNode[];
   vcenters: string[];
   show = true;
   @Input() openModal1: boolean;
   @Input() editFormVal: any;
   @Output() statusChange = new EventEmitter<boolean>();
   @Output() formValue = new EventEmitter<any>();
   instance: ConnectionProfileWizardComponent;
   private refreshViewSub: Subscription;
   @Input() submitted;
   @Input() vcenterIP;
   showMessage:boolean=false;
   vCenterIP:any="";

   constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellVCenterService: DellVCenterService,
			   private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {

      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

  ngOnInit() 
  {
		this.refreshView();
  }

	   WizardFormData = new FormGroup({
       profileName: new FormControl('', Validators.required),
       vcenterIP  :new FormControl(''),
       description: new FormControl('', ),   
	   checkbox:new FormControl('',),
       userId: new FormControl('', Validators.required),
       password: new FormControl('', Validators.required),
       idracverifypassword: new FormControl('', Validators.required),
	   certificateCheck:new FormControl('', ),
	   useActiveDirectory:new FormControl('', ),
	   hostpassword: new FormControl('', Validators.required),
       hostusername: new FormControl('root', ),
       hostverifypwdinput: new FormControl('', Validators.required),
       
       
    });

  private refreshView(): void {}

  formStatusChanged(){
    if(this.WizardFormData.valid){
       this.WizardFormData.controls['vcenterIP'].setValue(this.vcenterIP);
        this.statusChange.emit(true);
		    this.formValue.emit(this.WizardFormData);
	  }else{
      this.statusChange.emit(false);
    }
  }

}



