import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";

import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {DellServerFirmware, SelLog, ServerHardware, DellHostHardwareService,DellDatacenterHardwareService,
        NavService, Controller, Connector, Enclosure, PhysicalDisk, VirtualDisk
       } from "../../services/index";

@Component({
   selector: 'host-hardware',
   styleUrls: ['./host-hardware.component.scss'],
   templateUrl: './host-hardware.component.html'
   })
  

export class HostHardwareComponent implements OnInit {
    private refreshViewSub: Subscription;
	hostId: string;
	hardware: ServerHardware
	@Input() selLog: SelLog;
    @Input() firmware: DellServerFirmware;
    @Input() error:any;
    selected = 'Virtual Disks';
    hardwareStatus="FRUs";
    titleKey: string;
    rowActive="";
    instance: HostHardwareComponent;
    errorMessage:any;
    // Note: use @ViewChildren in case you have more than one dialogBox.

    constructor(private gs: GlobalsService,
                private appAlertService: AppAlertService,
                private dellHardwareService: DellHostHardwareService,
                private dellHostGroupService:DellDatacenterHardwareService,
                public  i18n: I18nService,
                private location: Location,
                public  nav: NavService,
                private refreshService: RefreshService,
                private route: ActivatedRoute) {
        this.instance = this;
        // Subscribe to refreshService to handle the global refresh action
        this.refreshViewSub = refreshService.refreshObservable$.subscribe(() => this.refreshView());
    }

        
        storageChanged(selected){ 
            this.selected = selected;
			this.rowActive="";
        }


    selectRow(selectedrow){
		this.rowActive=selectedrow;
	}
	    
    ngOnInit(): void {
        this.route.params.forEach((params: Params) => {
            this.hostId = params["id"];
        });

        this.nav.setViewType('summary');
        this.refreshView();
//        console.log(this.hardware);
    }

    ngOnDestroy(): void {
        this.refreshViewSub.unsubscribe();
    }

    hostHardware(val){
           this.hardwareStatus=val;
    }
    /**
        * refreshView is called when clicking the Refresh button (in plugin or dev mode)
        * or when toggling the Use Live Data button (dev mode only)
        */
    private refreshView(): void {
        // First we find the current host id at the end of the url
        // Then we check if that id still belongs to the hosts list, else revert to first host
       this.dellHardwareService.getDellHostHardware(this.hostId)
                .then(hardware => 
                {
                this.hardware = hardware;
                console.log(hardware);
                },error=>{
                  this.errorMessage=error;
                 
                });
    }           
    



}



