export class ChassisHealth {
  
   activeErrorsSupported: boolean;
   health:string;
   associatedChassis: AssociatedDellChassis[];
   modules:ChassisModule[];
   errors: ChassisActiveError[];

   constructor(clone: ChassisHealth = null) {
        this.activeErrorsSupported  = clone ? clone.activeErrorsSupported : false;
        this.health  = clone ? clone.health : "";
        this.associatedChassis  = clone ? clone.associatedChassis :[];
        this.modules              = clone ? clone.modules :[];
        this.errors              = clone ? clone.errors :[];
     
     
   }
}
export class ChassisModule {
  
   moduleType:string;
   ChassisModuleInfo: ChassisModuleInfo[];
   constructor(clone: ChassisModule = null) {
       this.moduleType  = clone ? clone.moduleType : "";
       this.ChassisModuleInfo = clone ? clone.ChassisModuleInfo :[];  
   }
}
export class ChassisModuleInfo {
    name:string;
    module:string;
    health:string;
    present:boolean;
    powerState:string;
    serviceTag:string;
   
   constructor(clone: ChassisModuleInfo = null) {
       this.name  = clone ? clone.name : "";
       this.module              = clone ? clone.module :"";
       this.health  = clone ? clone.name : "";
       this.present              = clone ? clone.present : false;
       this.powerState  = clone ? clone.powerState : "";
       this.serviceTag              = clone ? clone.serviceTag :"";  
   }
}



