	chassisKvm:ChassisKvmInfo[] ;
	enhancedCoolingMode:string;
    fanOffsetn:string;
    fans:ChassisFanInfo[];
    temperatureSensors:TemperatureSensorInfo[];
    storageEnclosures:StorageEnclosureInfo[] ;
    raidControllers:ChassisRaidControllerInfo[];
    equallogicMembers:EquallogicMemberDevice[];
