import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { Server,ProvisioningRequest } from "./index";

/**
 * This HostServiceBase interface is optional for HostService but it helps
 * define a simple FakeHostService for testing purposes
 */
export interface DellBareMetalServiceBase {
   //getDellAppliances(): Promise<DellApplianceInfo[]>;
   //getBuildPhaEvent():Promise<PHAEventsUI[]>;
}


@Injectable()
export class DellBareMetalService implements DellBareMetalServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService) {
   }

   /**
    * Build the REST url endpoint for retrieving a list of properties.
    * This is mapped to the DataAccessController on the java side.
    */
   buildAllAppliancesUrl (): string {
      return this.gs.getWebContextPath() + "/rest/services/dellappliances";
   };

   getDellAppliances(): Promise<Server[]> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.buildAllAppliancesUrl(), headers)
            .toPromise()
            // Normal response has a data field, mock response from db.json doesn't
            .then(response => (response.json().appliances  as Server[]))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

   buildPhaEventUrl (): string {
      return this.gs.getWebContextPath() + "/rest/services/dellphaevents";
   };
   getPhaEventRefreshUrl():string{
         return this.gs.getWebContextPath() + "/rest/services/dellPhaeventRefresh";
   }

   phaEventChangeUrl (request:Server): string {
      let url: string;
      let jsonString:string = JSON.stringify(request);
      url = this.gs.getWebContextPath() + "/rest/services/dellphaeventchanges?"
              + "request=" + jsonString;
       
          return url;
   };

   getDellBareMetalsUrl(request:ProvisioningRequest):string{
      let url: string;
      let jsonString:string = JSON.stringify(request);
      url = this.gs.getWebContextPath() + "/rest/services/dellBareMetals?"
              + "request=" + jsonString;
       
          return url;   
   }

   getBuildPhaEvent(): Promise<Server[]> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.buildPhaEventUrl(), headers)
            .toPromise()
            .then(response => (response.json().pha as Server[]))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

  getPhaEventChange(request:Server): Observable<any[]> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.phaEventChangeUrl(request), headers)
            .map(response => (response.json() as any[]))
            .catch(error => this.errorHandler.httpPromiseError(error));
  }

  getPhaEventRefresh():Observable<any[]>{
        let headers =this.gs.getHttpHeaders();
        return this.http.get(this.getPhaEventRefreshUrl(),headers)
             .map(response =>(response.json() as any[] ))
             .catch(error => this.errorHandler.httpPromiseError(error));     

  }


  getDellBareMetals(request:ProvisioningRequest):Observable<Server>{
      let headers =this.gs.getHttpHeaders();
      return this.http.get(this.getDellBareMetalsUrl(request),headers)
           .map(response =>(response.json().baremetals as any[] ))
           .catch(error => this.errorHandler.httpPromiseError(error));     

  }



  

   

}



