@Component({
  selector: "action1-modal",
  // styleUrls: ["./plugin-modal.scss"],
  styleUrls: ["./action1-wizard.component.scss"],
   templateUrl: "./action1-modal.component.html"
})
 
export class Action1ModalComponent implements OnInit {

 private actionUid: string;
   private webPlatform: WebPlatform;
   private webContextPath: string;
   public formData = {
      param1: "",
      param2: "some default value",
      param3: "typeA"
   };
   preRequiData=preRequi;
   Discovery=Discovery;
   Monitoring=Monitoring;
   Configuration=Configuration;

   idrac:boolean = true;
   vcenter:boolean = true;

   public inspectDataFromVcenter: InspectDataFromVcenter;
   public inspectDataFromIdrac: InspectDataFromIdrac;

   public pluginFormClass: string;

   private host: Host;

   // Flag for Clarity modal
   // @Input() opened: boolean;

   constructor(private injector: Injector,
               public gs: GlobalsService,
               private appAlertService: AppAlertService,
               private hostService: HostService,
               private complianceService: DellComplianceService,
               private consoleService: DellConsoleSDKService,
               private route: ActivatedRoute) {
      this.webPlatform = this.gs.getWebPlatform();
      this.webContextPath = this.gs.getWebContextPath();

      // in dev mode host is passed down from the dialog container
      if (!gs.isPluginMode()) {
         this.host = this.injector.get('context');
      }

      // css adjustment
      this.pluginFormClass = this.gs.isPluginMode() ? "plugin-modal-form" : "";
   }

   ngOnInit(): void {
   
     //document.getElementById("registeredUser").firstChild.innerHTML;
      if (this.gs.isPluginMode()) {
        this.webPlatform.setDialogTitle("Inspect Settings");
         // this.opened = true;
         // Get host objectId and actionUid from query parameters:
         // - actionUid could also be hard-coded if this modal is only used for 1 action
         // - retrieve host name in order to change the dialog title
         this.route.params.forEach((params: Params) => {
            this.actionUid = params["actionUid"];
            let hostId = params["id"];
            this.hostService.getHostName(hostId, hostResult => {
               this.host = hostResult;
               this.getInspectDataIdrac();
               this.getInspectDataVcenter();
               this.webPlatform.setDialogTitle("Inspect Settings for " + this.host.name);
            });
         });

      } else {
         // dev mode: modal dialog is opened from the Host monitor view,
         // host is already known and we set actionUid to the value in plugin.xml
         this.actionUid = APP_CONFIG.packageName + ".sampleAction1";
      }
   }

   /**
    * Function handler for the modal dialog submit button.
    * It must be defined by all components injected in DialogBox
    */
   public handleSubmit(): void {
      let actionUrl = this.webContextPath + "/rest/actions.html?actionUid=" + this.actionUid;
      let data = JSON.stringify(this.formData);
      this.webPlatform.callActionsController(actionUrl, data, this.host.id);

      if (!this.gs.isPluginMode()) {
         this.appAlertService.showInfo(this.actionUid + " called with " + data);
      }
   }

   onSubmit(): void {
      // this.opened = false;
      this.webPlatform.closeDialog();
      this.handleSubmit();
   }

   onCancel(): void {
      // this.opened = false;
      this.webPlatform.closeDialog();
   }

   getInspectDataIdrac(){
    //alert(this.gridVal.profileId+''+this.gridVal.networkAddress);
    this.complianceService.getInspectDataFromSpectre(this.host.id,this.host.name).
		then(response => 
		{
      if(response){
         this.inspectDataFromIdrac = response; 
         this.idrac = false;
         console.log(response); 
      }
				 
		}); 
  }

  getInspectDataVcenter(){

    this.consoleService.getInspectDataFromVcenter(this.host.id,this.host.name).
		then(response => 
		{
      if(response){
         this.inspectDataFromVcenter = response; 
         this.vcenter = false;
         console.log(response); 
      }
				 
		}); 
  }















 } 


