import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy,SimpleChanges,ChangeDetectorRef} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { FirmwareWizardUpdateSourceComponent }   from "./index";
import {Wizard} from "clarity-angular";
import { WebPlatform }  from "../../shared/vSphereClientSdkTypes";

import {FirmwareWizardSelectServer} from "./firmware-wizard-selectServer.component";


import { GlobalsService, RefreshService, I18nService,
	APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellFirmwareUpdateService,UpdateSettings} from "../../services/index";

import { VCenterRootNode,ConnectionProfile, ConsoleEntity,DellVCenterService,DellConnectionProfileService} from "../../services/index";

	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "firmware-wizard",
   styleUrls: ["./firmware-wizard.component.scss"],
   templateUrl: "./firmware-wizard.component.html"
})

export class FirmwareWizardComponent implements OnInit {
	openFirmware:boolean=true;
	vcenterNodes:VCenterRootNode[];
	closeFirmware:boolean=false;
	Username:boolean=false;
	password:boolean=false;
	verifypassword:boolean=false;
	fileLocation:boolean=false;
	Checkboxes:boolean=false;
	jobName:boolean=false;
	updatedJobName:boolean=false;
	updatedCheckboxes:boolean=false;
	firmwareUsername:boolean=false;
	firmwarepassword:boolean=false;
	firmwareverifypassword:boolean=false;
	firmwareLocation:boolean=false;
	objectId:any;
	displayMode:string="";
	driverbundle:boolean=true;
	profiles:ConnectionProfile[];
	selectedHosts:any;
	isFrench=false;
	

	private webPlatform: WebPlatform;
	
	@ViewChild('firmwareupdate') firmwareupdate;
	@ViewChild("wizard") wizard: Wizard;
    SingleDUP:boolean;
	constructor(private gs: GlobalsService,
		private appAlertService: AppAlertService,
		private dellFirmwareUpdate: DellFirmwareUpdateService,
		private location: Location,
		private refreshService: RefreshService,
		private dellVCenterService: DellVCenterService,
		private dellConnectionProfileService: DellConnectionProfileService,
		private ref:ChangeDetectorRef,  
		private route: ActivatedRoute,
		public  i18n: I18nService
		
	   ) {
	   }

    ngOnInit(){
	//	this.selectServer.getConnectionProfile();
		this.route.params.forEach((params: Params) => {
			this.objectId = params["id"];
			
	   }); 
        if(this.objectId){
			let objectSplit = this.objectId.split(':');
			if(objectSplit[2]=="ClusterComputeResource"){
				   this.displayMode="ClusterComputeResource";
			}else{
					this.displayMode="HostSystem";
			}	
		}
	   console.log(this.objectId);
		this.wizard.open();  this.refreshView();}
    formStatuscheck() {
      this.firmwareupdate.formStatus();
	}
    handlefirmwareUpdated(event){ 
     this.openFirmware=event;
	}	
    handlefirmwareUpdatedCompleted(event){ 
     this.closeFirmware=event;
	}
	
	getAssociatedHost(){
		this.dellVCenterService.getVCenterTree([], "true")
		.then(vcenterNodes => {
			if (vcenterNodes.length === 0) {
				this.ref.detectChanges(); 
				return;
			}
			this.vcenterNodes = vcenterNodes;
			console.log(this.vcenterNodes);
			this.ref.detectChanges();
		});
	
	}


	getConnectionProfile(){
		this.dellConnectionProfileService.getConnectionProfiles()
			.subscribe(profiles => {
			this.profiles = profiles;
			console.log(this.profiles);
			},error=>{
				console.log(error);
				
			});
	}


    private refreshView(): void {
		this.dellFirmwareUpdate.getUpdateSettings().subscribe(
			(response)=>{
				console.log(response);
			},
			(err)=>{
				console.log(err)
			},
			()=> console.log("firmware update getsettings completed")
		); 

	}

	doCustom(event){
		if("custom-next" === event){
			this.wizard.next();
			console.log("this the first step");
	}
	
	}
	
	doCustomClick(event){
	 if ("custom-previous" === event){
        this.wizard.previous();
     }  
	else if("custom-next" === event){
	if(this.openFirmware){
	this.wizard.next();
	this.SingleDUP=false;
	this.Username=false;
	this.password=false;
	this.verifypassword=false;
	}
	
	
    this.fileLocation=this.firmwareLocation;
    if(!this.fileLocation){
	this.Username=this.firmwareUsername;
	this.password=this.firmwarepassword;
	this.SingleDUP=true;
	}
	else{
	this.Username=false;
	this.password=false;
	this.SingleDUP=true;
	}
	if(!this.Username && !this.password && !this.fileLocation && !this.openFirmware ){
	this.verifypassword=this.firmwareverifypassword;
    this.SingleDUP=true;
	}
	}
  }
	
  	handlefirmwareUserName(event){this.firmwareUsername=event;}
	handlefirmwarePassword(event){this.firmwarepassword=event;}
	handlefirmwareJobName(event){this.updatedJobName=event;}
	handlefirmwareVerifyPwd(event){this.firmwareverifypassword=event;}
	handlefirmwareFileLocation(event){this.firmwareLocation=event;}
	handlefirmwareCheckboxes(event){this.updatedCheckboxes=event;}
	handlefirmwarecheckrads1(event){
	if(event==true)
	this.SingleDUP=false;
	else
	this.SingleDUP=true;
	}
	
	doCustomClicks(event){
	if ("custom-previous" === event){
          this.wizard.previous();
    }  
	else if("custom-next" === event){
    if(this.closeFirmware){
	  this.wizard.next();
	  this.jobName=false;
	  this.Checkboxes=false;
	}
	else{
	  this.jobName=this.updatedJobName;
	  this.Checkboxes=this.updatedCheckboxes;
	 }
	}
  }

  checkHostSelection(event){
    if ("custom-previous" === event){
		  this.wizard.previous();
		  console.log(this.selectedHosts);
		  console.log("check Event");
		  
       }  
    else if("custom-next" === event){
		this.wizard.next();
		console.log(this.selectedHosts);
        console.log("elese check");
     }
	}
	
	selectHostServer(event){
		this.selectedHosts=event;
	}	
  
  doCustomSelectComponet(event){
			if ("custom-previous" === event){
				this.wizard.previous();
		}  
		else if("custom-next" === event){
			this.wizard.next();
			this.jobName=false;
			this.Checkboxes=false;
		}
		else{
			this.jobName=this.updatedJobName;
			this.Checkboxes=this.updatedCheckboxes;
		}
	}
 
  
 onCancel(): void {
	this.webPlatform.closeDialog();
 }

 getDialog(){
			this. webPlatform.setDialogSize(1000,400);
 }


	
 
}


