// A simple Host object model
export class DellHost {
   hostName: string;
   vcenterName: string;
   clusterName: string;
   datacenterName: string;
   connectionProfileName: string;
   serverGuid: string;
   hostId: string;
   clusterId: string;
   datacenterId: string;

   constructor(clone: DellHost = null) {
      this.hostName = clone ? clone.hostName : "";
      this.vcenterName = clone ? clone.vcenterName : "";
      this.clusterName = clone ? clone.clusterName : "NA";
      this.datacenterName = clone ? clone.datacenterName : "";
      this.connectionProfileName = clone ? clone.connectionProfileName : "NA";
      this.serverGuid = clone ? clone.serverGuid : "";
      this.hostId = clone ? clone.hostId : "";
      this.clusterId = clone ? clone.clusterId : "";
      this.datacenterId = clone ? clone.datacenterId : "";
   }
}




export class DellChassis {
        chassisName:string;
        hostCount  : number;
        chassisIp  :string;
        chassisServiceTag:string;
        cmcUrl      : string;
        chassisModel : string;
        chassisLocation : string;
        chassisLastInventory:string;
        availableSlots : number;
        chassisProfileName: string;
    constructor(clone: DellChassis = null) {
       this.chassisName     = clone ? clone.chassisName : "";
       this.hostCount       = clone ? clone.hostCount : 0;
       this.chassisIp       = clone ? clone.chassisIp : "";
       this.chassisServiceTag   = clone ? clone.chassisServiceTag : "";
       this.cmcUrl              = clone ? clone.cmcUrl : "NA";
       this.chassisModel        = clone ? clone.chassisModel : "";
       this.chassisLocation     = clone ? clone.chassisLocation : "";
       this.chassisLastInventory = clone ? clone.chassisLastInventory : "";
       this.availableSlots       = clone ? clone.availableSlots :0;
    }
 }
   


