import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";

@Component({
   selector: 'monitor-iomodule',
   styleUrls: ['./monitor-iomodule.component.scss'],
   templateUrl: './monitor-iomodule.component.html'
  })
  
export class MonitorIOModuleComponent implements OnInit {
    private refreshViewSub: Subscription;
    @Input() chassisIOModules:any;
    selectedObj:any;
    selectedRow:number;
    rowselected:boolean=false;
   
    constructor() {}

    ngOnInit(): void { }

    private refreshView(): void {}

    setClickedRow = function(obj,index){  
    this.rowselected=true;
    this.selectedObj=obj;  
    this.selectedRow = index;  
  } 
}



