// A simple Host object model
import {ProfileCredentials,Profile} from  "../index";

export class ChassisCredentialProfile extends Profile {
   credentials: ProfileCredentials;
   certificateCheckEnabled: boolean;
   associatedChassis: AssociatedDellChassis[];
   


   constructor(clone: ChassisCredentialProfile= null) {
       super();
      this.credentials              = clone ? clone.credentials :new ProfileCredentials();
      this.certificateCheckEnabled  = clone ? clone.certificateCheckEnabled : false;
      this.associatedChassis        = clone ? clone.associatedChassis :[];
     
   }
}



export class AssociatedDellChassis{
    chassisName :string;
	chassisIp   :string;
	chassisServiceTag  : string;
	chassisModel       : string;
    chassisProfileName : string;
    
    constructor(clone:AssociatedDellChassis = null){
        this.chassisName    = clone ? clone.chassisName:"";
        this.chassisIp      = clone ? clone.chassisIp:"";
        this.chassisServiceTag  = clone ? clone.chassisServiceTag :"";
        this.chassisModel       = clone ? clone.chassisModel : "";
        this.chassisProfileName = clone ? clone.chassisProfileName : "";
    }

}

export class ChassisTestResult {
	
	passed : boolean;
	message : string;
    serviceTag : string;

    constructor(clone:ChassisTestResult = null){
        this.passed  = clone ? clone.passed : false;
        this.message = clone ? clone.message: "";
        this.serviceTag = clone ? clone.serviceTag : ""; 
    }
    
} 


export class Chassis {
    id: string;
    name: string;
    dimensions: string;
    serverType: string;
    isActive: boolean;
    healthStatus: number;
    complianceStatus: number;
 }
 




