import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { ChassisCredentialProfile,Profile,ChassisTestResult } from "../index";

/**
 * This HostServiceBase interface is optional for HostService but it helps
 * define a simple FakeHostService for testing purposes
 */
export interface DellChassisServiceBase {
   getDellChassis(): Observable<ChassisCredentialProfile[]>;
   createChassisProfile(chassis:ChassisCredentialProfile):Observable<Profile>;
}


@Injectable()
export class DellChassisService  implements DellChassisServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService) {
   }

   private  SerivceUrl(){
         return "/rest/chassisProfiles/";
   }

   /**
    * Build the REST url endpoint for retrieving a list of properties.
    * This is mapped to the DataAccessController on the java side.
    */
   private getDellChassisUrl (): string {
      return this.gs.getWebContextPath() +this.SerivceUrl()+"dellchassis";
   };


   private createChassisProfileUrl(chassis:ChassisCredentialProfile): string {
      let url: string;
      let jsonString:string = JSON.stringify(chassis);
     // console.log(jsonString);
      url = this.gs.getWebContextPath() +this.SerivceUrl()+"createChassis?cp="+jsonString;
	return url;
   }

   
   createChassisProfile(cp:ChassisCredentialProfile): Observable<Profile> {
      let headers = this.gs.getHttpHeaders;
      return this.http.get(this.createChassisProfileUrl(cp), headers)
            .map(response => response.json().data as Profile)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
    
   /* createChassisProfile(chassis:ChassisCredentialProfile): Promise<string> {
      let headers = this.gs.getHttpHeaders;
      
      return this.http.put(this.createChassisProfileUrl(chassis), headers)
            .toPromise()
            .then(response => (response.json().status) as string)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }*/
      


    
   private deleteChassisProfileUrl(profileId:number){
      let url: string;
      url = this.gs.getWebContextPath() +this.SerivceUrl() +"deletechassis?"
               + "profileId=" + profileId;
      return url;
   }
   private testChassisProfileUrl(testrequest:ChassisCredentialProfile){
      let url: string;
       let jsonString:string = JSON.stringify(testrequest);
      console.log("value of testrequest is"+JSON.stringify(testrequest));
      url = this.gs.getWebContextPath() +this.SerivceUrl() +"testchassis?testrequest="+jsonString;
      return url;
   }

   getDellChassis(): Observable<ChassisCredentialProfile[]> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getDellChassisUrl(), headers)
            .map(response => (response.json().data  as ChassisCredentialProfile[]))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }


   deleteChassisProfile(profileId:number): Observable<string>{
      let headers = this.gs.getHttpHeaders;
      return this.http.get(this.deleteChassisProfileUrl(profileId), headers)
            .map(response => (response.json() as string))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }   
  testChassisProfile(testrequest:ChassisCredentialProfile): Observable<ChassisTestResult[]>{
      let headers = this.gs.getHttpHeaders;
      return this.http.get(this.testChassisProfileUrl(testrequest), headers)
            .map(response => (response.json() as string))
            .catch(error => this.errorHandler.httpPromiseError(error));
   } 
      


  

}



