import { NavService } from "../../services/index";
import {Observable}from "rxjs";
import {  Component, Input, OnInit, OnDestroy, ViewChild, Inject,forwardRef } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { UserSettingService } from "../../shared/user-settings.service";
         


@Component({
   selector: 'monitor-profile-main',
   styleUrls: ['./monitor-profile-main.component.scss'],
   templateUrl: './monitor-profile-main.component.html'
})

export class MonitorProfileMainComponent implements OnInit,OnDestroy {
      hostId: string;
      loader = false;
      tabval="Getting";
      instance: MonitorProfileMainComponent;
      private refreshSubscription: Subscription;
      currentTab: string;
   
    
      constructor(public gs: GlobalsService,
                  private appAlertService: AppAlertService,
                  public  i18n: I18nService,
                  private location: Location,
                  public  nav: NavService,
                  private refreshService: RefreshService,
                  private userSettingService: UserSettingService,
                  
                  private route: ActivatedRoute) {

          this.instance = this;
          this.refreshSubscription = refreshService.refreshObservable$.subscribe(
                () => this.refreshView());
                
      }

      ngOnInit() {

            let savedTab: string = this.userSettingService.getSetting("mainTab");
            if (savedTab !== "Monitor" && savedTab !== "Manage" && savedTab !== "Licensing" && savedTab !== "Help" ) {
               savedTab = "Getting";
            }
            this.currentTab = savedTab; 

            this.route.params.forEach((params: Params) => {
              this.hostId = params["id"];
            });
            this.nav.setViewType('monitor');
      }
      

      ngOnDestroy(): void {
            this.refreshSubscription.unsubscribe();
      }


      isActiveTab(id: string): boolean {
            return (id === this.currentTab);
         }
      
         onNavTabSelected(event: boolean,param:string) {
            if (event) {
               this.currentTab = param;
               this.onTabSelected(this.currentTab);
            }
         }
      
         onTabSelected(id: string) {
            this.appAlertService.closeAlert();
            this.userSettingService.setSetting("mainTab", id);
            this.refreshView();
         }

      navTap(val:string){ console.log(val);  this.tabval=val;  }
      
      private refreshView(): void {}
 
 }



