import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";

import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { InspectDataFromIdrac,HostComplianceData} from "./index";


export interface DellComplianceServiceBase 
{
   getInspectDataFromSpectre(hostId:string, hostIp:string): Promise<InspectDataFromIdrac>;
}

@Injectable()
export class DellComplianceService implements DellComplianceServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   

   private getInspectSettingUrl(hostId:string, hostIp:string): string {
      let url: string;
      url = this.gs.getWebContextPath() + "/rest/services/inspectdataidrac/" + hostId
               + "?hostIp=" + hostIp;
      return url;
   }

   private getHostComplianceDataUrl(): string {
      let url: string;
      url = this.gs.getWebContextPath() + "/rest/services/hostcomplianceData";
      return url;
   } 
   
  
    getInspectDataFromSpectre(hostId:string, hostIp:string): Promise<InspectDataFromIdrac>{
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();

      return this.http.get(this.getInspectSettingUrl(hostId, hostIp), headers)
            .toPromise()
            .then(response => (useLiveData ? response.json().inspectdata : response.json()) as InspectDataFromIdrac)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
 
 
    getHostComplianceData()
    {
          return this.http.get(this.getHostComplianceDataUrl())
         .map(response => response.json()as HostComplianceData).
         catch(error=>this.errorHandler.httpPromiseError(error));
    }
       

   

}



