import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import {Wizard} from "clarity-angular";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";


@Component({
    changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "monitor-config-vcenter",
  styleUrls: ["./monitor-config-vcenter.component.scss"],
  templateUrl:"./monitor-config-vcenter.component.html",
})

export class MonitorConfigVcenterComponent implements OnInit {
   
  @ViewChild("wizard") wizard: Wizard;
  @Input() openStatus:boolean;
  vcenterNodes: VCenterRootNode[];
  vcenters: string[];
  show = true;
  @Output() vCenterValue = new EventEmitter<any>();
  @Input() editFormVal: any;
  @Input() vCeneterFormValid:boolean;
  @Output() statusChange = new EventEmitter<boolean>();
  instance: MonitorConfigVcenterComponent;
  private refreshViewSub: Subscription;
  pwd: any;
  hostpwdValid = false;
  profile: ConnectionProfile = new ConnectionProfile();
  selectedVcenter: string = "Select vCenter";
  selectedHosts:ConsoleEntity[] = [];
  HostuserId:string="";
  hostList:any=[];
  showMessage:boolean=false;
  vCenterIP:string;
  
  constructor(private gs: GlobalsService,
              private appAlertService: AppAlertService,
              private dellVCenterService: DellVCenterService,
              private dellConnectionProfileService: DellConnectionProfileService,
              public  i18n: I18nService,
              private location: Location,
              public  nav: NavService,
              private refreshService: RefreshService,
              private route: ActivatedRoute) {
      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
      () => this.refreshView());
}

ngOnInit() {	this.refreshView();	}

WizardFormVcenter = new FormGroup({
                 selectedVcenter: new FormControl('', Validators.required)
});


private refreshView(): void {
    this.dellVCenterService.getVCenterTree([], "true")
       .then(vcenterNodes => {
        if (vcenterNodes.length === 0) {   return;   }
            this.vcenterNodes = vcenterNodes;
            this.vCenterIP=(vcenterNodes[0])?vcenterNodes[0].vcenterIp:'';
            this.vCenterValue.emit(this.vCenterIP);
            this.vcenters = [];
            this.vcenters.push("Registered vCenters");
            for(let vcRootNode of vcenterNodes)
            {
                this.vcenters.push(vcRootNode.vcenterHostName);
            }
    });
}


onCancel()
{
    this.selectedHosts=[];
    this.openStatus=false;
    this.statusChange.emit(this.openStatus);
    this.HostuserId=this.profile.hostCredential.userId;
    this.WizardFormVcenter.reset();
    this.wizard.reset();
    this.WizardFormVcenter.controls['hostusername'].setValue(this.HostuserId);
}

checkval()
{
        if(this.show==true && this.selectedHosts.length<1){
        document.getElementById("modal-window").style.visibility="visible";
        }
        else 
        document.getElementById("modal-window").style.visibility="hidden";
        document.getElementById("gridData").style.visibility="visible";
        document.getElementById("gridDataheader").style.visibility="visible";
}



openmodal(){
      this.show=true;
      document.getElementById("modal-window").style.visibility="visible";
}


vcenterChanged(selectedVcenter){ 
          this.selectedVcenter = selectedVcenter;
          if(selectedVcenter == 'Registered vCenters')
          {  this.showMessage=true;  }  
          else
              this.showMessage=false;	
}

formStatusChanged(){
    if(this.WizardFormVcenter.valid){
        let vCenterIP=(this.WizardFormVcenter.get('selectedVcenter').value)?this.WizardFormVcenter.get('selectedVcenter').value:'';
        this.vCenterValue.emit(vCenterIP);
        this.statusChange.emit(true);
    }
	else{
      this.statusChange.emit(false);

     }
  }

onSubmit(data)  {  this.profile=data;    }

}



