// Connection Profiles
export class ConnectionProfile
{
    profileId: number;
    profileName: string;
    description: string;
    creationDate: string;
    updatedDate: string;
    createdBy: string;
    updatedBy: string;
    hostCredential: WsmanCredential;
    idracCredential: WsmanCredential ;
    console: Console ;

    
//  
    constructor(clone: ConnectionProfile = null) 
    {
        this.profileId = clone ? clone.profileId : 0;
        this.profileName = clone ? clone.profileName : "";
        this.description = clone ? clone.description : "";
        this.creationDate = clone ? clone.creationDate : "";
        this.updatedDate = clone ? clone.updatedDate : "";
        this.createdBy = clone ? clone.createdBy : "";
        this.updatedBy = clone ? clone.updatedBy : "";
        this.hostCredential = clone ? clone.hostCredential : new WsmanCredential();
        this.hostCredential.userId = clone ? clone.hostCredential.userId : "root";
        this.idracCredential = clone ? clone.idracCredential : new WsmanCredential();
        this.console = clone ? clone.console : new Console();
     }
}


export class WsmanCredential
{
    wsmanId: number;
    type: string;
    userId: string;
    password: string;
    certificateCheck: string;
    useActiveDirectory: string;

    constructor(clone: WsmanCredential = null) 
    {
        this.wsmanId = clone ? clone.wsmanId : 0;
        this.type = clone ? clone.type : "";
        this.userId = clone ? clone.userId : "";
        this.password = clone ? clone.password : "";
        this.certificateCheck = clone ? clone.certificateCheck : "";
        this.useActiveDirectory = clone ? clone.useActiveDirectory : "";
     }
}

export class Console
{
    consoleId: number;
    networkAddress: string;
    consoleEntity: ConsoleEntity[];
    constructor(clone: Console = null) 
    {
        this.consoleId = clone ? clone.consoleId : 0;
        this.networkAddress = clone ? clone.networkAddress : "";
        this.consoleEntity = clone ? clone.consoleEntity : [];
     }
}


export class ConsoleEntity
{
    consoleEntityId: number;
    type: string;
    name: string;
    entityId: string;
    serverGuid: string;
    vCenterIP: string;
    serviceTag:string;
    inBandTestSucceeded: boolean;
    outOfBandTestSucceeded: string;
    inBandTestStatus: string;
    outOfBandTestStatus: string;
    constructor(clone: ConsoleEntity = null) 
    {
        this.consoleEntityId = clone ? clone.consoleEntityId : 0;
        this.type = clone ? clone.type : "";
        this.name = clone ? clone.name : null;
        this.entityId = clone ? clone.entityId : "";
        this.serverGuid = clone ? clone.serverGuid : "";
        this.vCenterIP = clone ? clone.vCenterIP : "";
        this.serviceTag = clone ? clone.serviceTag : "";
        this.inBandTestStatus = clone ? clone.inBandTestStatus : "";
        this.outOfBandTestStatus = clone ? clone.outOfBandTestStatus : "";
		this.inBandTestSucceeded = clone ? clone.inBandTestSucceeded : false;
        this.outOfBandTestSucceeded = clone ? clone.outOfBandTestSucceeded : "";
     }
}

export class ConnectionTestRequest
{
    idracCredential: WsmanCredential;
	hostCredential: WsmanCredential;
	requestId: number;
	testHosts: string[];
    constructor(clone: ConnectionTestRequest = null) 
    {
        this.idracCredential = clone ? clone.idracCredential : new WsmanCredential();
	    this.hostCredential = clone ? clone.hostCredential : new WsmanCredential();
		this.requestId   	=	clone ? clone.requestId:0;
		this.testHosts   	=	clone ? clone.testHosts:[];
     }
}

export class ConnectionTestResult
{
	requestId:number;
    hostName: string;
    inBandTestSucceeded: boolean;
    outOfBandTestSucceeded: string;
    inBandTestStatus: string;
    outOfBandTestStatus: string;
    constructor(clone: ConnectionTestResult = null) 
    {
        this.requestId				= clone ? clone.requestId:0;
		this.hostName 				= clone ? clone.hostName : "";
        this.inBandTestSucceeded 	= clone ? clone.inBandTestSucceeded : false;
        this.outOfBandTestSucceeded = clone ? clone.outOfBandTestSucceeded : "";
        this.inBandTestStatus		= clone ? clone.inBandTestStatus : "";
        this.outOfBandTestStatus	= clone ? clone.outOfBandTestStatus : "";
     }
}
















