
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
  APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellLogService, LogMessage ,DellApplianceService,PHAEventsUI, DellConnectionProfileService,LogRequestData, NavService } from "../../services/index";


@Component({
   selector: "Ha-events",
   styleUrls: ["./ha-events.component.scss"],
  templateUrl:"./ha-events.component.html"
})

export class HaEventsComponent  implements OnInit 
{
dataCollection=[];
selectedRowNum:number;
selectedObject:any;
loading: boolean = true;
showDropdown:boolean=false;
private refreshViewSub: Subscription;
selectedOption:string;
constructor(
          private gs: GlobalsService,
          public  i18n: I18nService,
          private location: Location,
          public  nav: NavService,
          private refreshService: RefreshService,
          private dellLogService:DellLogService,
          private dellApplianceService:DellApplianceService,  
          private route: ActivatedRoute  ) {

       this.refreshViewSub = refreshService.refreshObservable$.subscribe(
             () => this.refreshView());
  }

  
  ngOnInit(): void{ this.refreshView();}

   private refreshView(): void {
     this.dellApplianceService.getBuildPhaEvent().
     then(response => 
     {
      this.dataCollection=response;
       this.loading=false;
     },
     error=>{
         console.log(error);
     });
  }
  

  
  selectedRow(obj,i){ 
    this.selectedObject = obj;
    this.selectedRowNum = obj.eventId;
  }

  applyChanges(){
    if(this.selectedObject != undefined){
        this.dellApplianceService.getPhaEventChange(this.selectedObject)
        .subscribe( (response) => {
        },
          (err) => console.error(err),
          () => console.log("observable complete")
        ); 
    }

  }

  onClickDropDown(id):void{

    document.getElementById("ha-"+id).classList.remove('disable');
    document.getElementById("ha-"+id).classList.add('enable');
      this.showDropdown=true;
  }
  changeOptions(val,id){
   if(val === 'Warning'){
    this.selectedObject.overriddenSeverity="Warning";
   }else{
    this.selectedObject.overriddenSeverity="Critical";
   }
    document.getElementById("ha-"+id).classList.remove('enable');
    document.getElementById("ha-"+id).classList.add('disable');
  }
  cancelChanges(){
    this.selectedObject=[];
  }

  resetPhaevents(){
    this.dellApplianceService.getPhaEventRefresh().subscribe(
              (response) => {
                this.refreshView();
              },
              (err)=>console.log(err),
       );
  }
}


