
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellConnectionProfileService, NavService ,DellTaskschedulerService,ServerJob } from "../../services/index";

@Component({
   selector: "system-lockdown-job",
   styleUrls: ["./system-lockdown-job.component.scss"],
   templateUrl:"./system-lockdown-job.component.html"
})

export class SystemLockdownJobComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() jobNavStatus:string;
  profiles: ConnectionProfile[];
  childNavStatus="";
  chassisStatus=false;
  instance: SystemLockdownJobComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  lockdownData:ServerJob;  
  selected_job=[]; 
  jOBTYPE_CONFIGURELOCKDOWN:string = "CONFIGURELOCKDOWN"; 
 
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
               private _dellTaskschedulerService:DellTaskschedulerService
              ) {
                   this.instance = this;
                   this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                   () => this.refreshView());
   }

   ngOnInit(): void 
   {  
      this.nav.setViewType('summary');
      this.refreshView();
   }
 
   connectionNav(linkVal)
   {
		  this.jobNavStatus=linkVal;
		  this.childNavStatus="";
   }
   
  private refreshView(): void {
      this._dellTaskschedulerService.getDeployment(this.jOBTYPE_CONFIGURELOCKDOWN).
          then(response => 
          {
           this.lockdownData=response;
          },
          error=>{
              console.log(error);
          });

   }
}


