
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {Wizard} from "clarity-angular";
@Component({
   selector: "monitor-fixnoncompliance",
   styleUrls: ["./monitor-fixnoncompliance.component.scss"],
  templateUrl:"./monitor-fixnoncompliance.component.html"
})

export class MonitorFixNonComplianceComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
   @ViewChild("wizard") wizard: Wizard;
   @Input() openWizard:boolean;
   @Input() vsphere:any;
   selectedHosts:any;
   @Output() wizardStatus = new EventEmitter<boolean>();
  
  constructor() {}

  ngOnInit(): void {  this.selectedHosts=this.vsphere;}
  
  private refreshView(): void {}

  pageCustomCancel(){
    this.openWizard=false;
    this.wizardStatus.emit(this.openWizard);
    this.wizard.reset();
   }

  reset(){
   this.openWizard=false;
   this.wizardStatus.emit(this.openWizard);
   this.wizard.reset();
  }
}


