import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import {ServerContainer,ProfileRequest,Profile,TemplateRequest,Template } from "./index";


export interface DellDeploymentServiceBase {
  
}

@Injectable()
export class DellDeploymentService implements DellDeploymentServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

    private getReferenceServersUrl(): string {
        let url: string;
        url = this.gs.getWebContextPath() + "/rest/services/ReferenceServerData";
        return url;
    }
  
    private getDeploymentProfilesUrl(request:ProfileRequest): string {
        let url: string;
        let jsonString:string = JSON.stringify(request);
        url = this.gs.getWebContextPath() + "/rest/services/deploymentProfiles?"
                + "profilerequest=" + jsonString;
        return url;
    }

    
    private getCreateTemplateProfileUrl(request:Template): string {
        let url: string;
        let jsonString:string = JSON.stringify(request);
        url = this.gs.getWebContextPath() + "/rest/services/createTemplate?"
                + "cp=" + jsonString;
        return url;
    }
    private getDeployTemplateDataUrl(request:TemplateRequest):string{
        let url: string;
        let jsonString:string = JSON.stringify(request);
        url = this.gs.getWebContextPath() + "/rest/services/getTemplates?"
                + "templaterequest=" + jsonString;
        return url;
    }

    private deleteTemplateProfileUrl(templateId:number){
        let url: string;
        url = this.gs.getWebContextPath() + "/rest/services/deleteTemplates?"+"id=" +templateId ;
        return url;
     }
 
    getReferenceServers():Observable<ServerContainer>{   
           return this.http.get(this.getReferenceServersUrl())
                .map(response => response.json().ReferenceServerData as ServerContainer)
                .catch(error=>this.errorHandler.httpPromiseError(error));
    }

    getDeploymentProfiles(request:ProfileRequest): Observable<Profile> {
        let headers = this.gs.getHttpHeaders;
        return this.http.get(this.getDeploymentProfilesUrl(request), {headers:this.gs.getHttpHeadersContent()})
              .map(response => (response.json()).profiles  as Profile)
              .catch(error => this.errorHandler.httpPromiseError(error));
     }   
      

    getDeployTemplateData(request:TemplateRequest):Observable<Template>{
        let headers = this.gs.getHttpHeaders;
        return this.http.get(this.getDeployTemplateDataUrl(request), {headers:this.gs.getHttpHeadersContent()})
              .map(response => (response.json()).profiles  as Template)
              .catch(error => this.errorHandler.httpPromiseError(error));
    }   

    getCreateTemplateProfile(request:Template):Observable<Template>{
        let headers = this.gs.getHttpHeaders;
        return this.http.get(this.getCreateTemplateProfileUrl(request), headers)
              .map(response => (response.json())  as Template)
              .catch(error => this.errorHandler.httpPromiseError(error));
    }  
    
    
    deleteTemplateProfile(templateId:number): Observable<string>{
        let headers = this.gs.getHttpHeaders;
        return this.http.get(this.deleteTemplateProfileUrl(templateId), headers)
              .map(response =>(response.json()) as string )
              .catch(error => this.errorHandler.httpPromiseError(error));
     }
    


}



