// A simple Host object model
export class UpdateSettings {
    updateMethod  : string;
    sharePath     : string;
    shareUser     : string;
    sharePassword : string;
    shareDomain   : string;
    url           : string;
    protocol      : string;
    
   constructor(clone: UpdateSettings = null) {
        this.updateMethod    =  clone ? clone.updateMethod:"";
        this.sharePath       =  clone ? clone.sharePath :"";
        this.shareUser       =  clone ? clone.shareUser:"";
        this.sharePassword   =  clone ? clone.sharePassword:"";
        this.shareDomain     =  clone ? clone.shareDomain:"";
        this.url             =  clone ? clone.url:"";
        this.protocol        =  clone ? clone.protocol:"";
        
   }
}

export class RepositoryDataObject{

    repositoryLocation      : string;
    repositoryLastChecked   : string;
    managedObjectId         : string;
    managedObjectType       : string;
    managedObjectServerGuid : string;
    modelBundleList         : ModelUpdateBundleObject[];  

    constructor(clone:RepositoryDataObject = null){
        this.repositoryLocation     =  clone ? clone.repositoryLocation:"";
        this.repositoryLastChecked  = clone ? clone.repositoryLastChecked:"";
        this.managedObjectId        = clone ? clone.managedObjectId:"";
        this.managedObjectType      = clone ? clone.managedObjectType : "";
        this.managedObjectServerGuid= clone ? clone.managedObjectServerGuid:"";
        this.modelBundleList        = clone ? clone.modelBundleList:[];     
    }
    
}


export class ModelUpdateBundleObject {
    
    hostCount    : string;
    hostModel    : string;
    bundleList   : string[];
   
   constructor(clone:ModelUpdateBundleObject = null){
       this.hostCount        =  clone ? clone.hostCount:"";
       this.hostModel        =  clone ? clone.hostModel :"";
       this.bundleList       =  clone ? clone.bundleList :[];
       
   }

}

export class SelectedUpdateBundleObject {
    
    
    hostModelName        : number;
    selectedUpdateBundle : string;
    
   constructor(clone:SelectedUpdateBundleObject = null){
       this.hostModelName          =  clone ? clone.hostModelName:0;
       this.selectedUpdateBundle   =  clone ? clone.selectedUpdateBundle :"";
    }

}


class PagedFirmwareUpdateComponentsData {
	
	count   : number;
	next    : number;
    managedObjectId   : string;
    managedObjectType : string;
    managedObjectServerGuid : string;
    lastInventoryDate : string;
    updateComponents  : PagedFirmwareUpdateComponent[] ;
    
    constructor(clone:PagedFirmwareUpdateComponentsData=null){
        this.count  = clone ? clone.count :0;
        this.next   = clone ? clone.next:0;
        this.managedObjectId  = clone ? clone.managedObjectId:"";
        this.managedObjectServerGuid = clone ? clone.managedObjectServerGuid:"";
        this.lastInventoryDate= clone ? clone.lastInventoryDate:"";
        this.updateComponents = clone ? clone.updateComponents : [];
    }
}

class FirmwareUpdateJobMinimalRequest {
    objectId    : string;
    objectType  : string;
    serverGuid  : string;
    scheduledDateTimeUtc : string;
    jobName        : string;
    jobDescription : string;
    jobType        : string;
    removeHostFromMM  : boolean;
    updateComponents  : PagedFirmwareUpdateComponent[] ;
    
    constructor(clone:FirmwareUpdateJobMinimalRequest = null){
        this.objectId           = clone ? clone.objectId:"";
        this.objectType         = clone ? clone.objectType:"";
        this.serverGuid         = clone ? clone.serverGuid:"";
        this.scheduledDateTimeUtc = clone ? clone.scheduledDateTimeUtc:"";
        this.jobDescription     = clone ? clone.jobDescription:"";
        this.jobName            = clone ? clone.jobName:"";
        this.jobType            = clone ? clone.jobType: "";
        this.removeHostFromMM   = clone ? clone.removeHostFromMM:false;
        this.updateComponents   = clone ? clone.updateComponents : [];         

    }
	
}

export class PagedFirmwareUpdateComponent{
    pageId  : number;
	cluster : string;
    hostname: string;
    serviceTag : string;
    modelName  : string;
    component  : string;
    currentVersion   : string;
    availableVersion : string;
    componentState   : string;
    description      : string;
    rebootRequired   : string;
	
    constructor(clone:PagedFirmwareUpdateComponent = null){
        this.pageId        =  clone ? clone.pageId :0;
        this.cluster       =  clone ? clone.cluster : "";
        this.hostname      =  clone ? clone.hostname :"";
        this.serviceTag    =  clone ? clone.serviceTag:"";
        this.modelName     =  clone ? clone.modelName : "";
        this.component     =  clone ? clone.component : "";
        this.currentVersion     = clone ? clone.currentVersion: "";
        this.availableVersion   = clone ? clone.availableVersion:"";
        this.componentState     = clone ? clone.componentState:"";
        this.description        =  clone ? clone.description:"";
        this.rebootRequired     = clone ? clone.rebootRequired:"";
    }
}



