import { Component, Input,Output, OnInit, ViewChild,EventEmitter,OnChanges,SimpleChanges } from "@angular/core";
import { Wizard } from "clarity-angular";
@Component({
   selector: "monitor-wizard-chassisprofiledelete",
   styleUrls: ["./monitor-wizard-chassisprofiledelete.component.scss"],
   templateUrl: "./monitor-wizard-chassisprofiledelete.component.html"  
})

export class ChassisProfileDelete implements OnInit {
    @Input()chassisDelete:boolean;
    @Output() closedeletemodal=new EventEmitter<boolean>();
     @Input() chassisprofileVal:any;
    constructor() {}
    ngOnInit() {} 
    closeModal(){
        this.chassisDelete=false;
        this.closedeletemodal.emit(this.chassisDelete);
    }
}


