
import { Component, OnInit, OnDestroy, TemplateRef,ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellConnectionProfileService, NavService } from "../../services/index";

@Component({
   selector: "profile-deployment",
   styleUrls: ["./profile-deployment.component.scss"],
   templateUrl:"./profile-deployment.component.html"
})

export class ProfileDeploymentComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() inputParam:string;
  profiles: ConnectionProfile[];
  jobNavStatus="Deployment Jobs";
  chassisStatus=false;
  instance: ProfileDeploymentComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  tabval:string;
 
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {
    this.instance = this;
    this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void  { this.refreshView(); }
 
   connectionNav(linkVal) { this.jobNavStatus=linkVal;}
   
   tagNav(param){ this.tabval=param;}   
 
   private refreshView(): void {}
  }


