import { Component, OnInit,ViewChild,AfterViewInit,ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Params } from "@angular/router";
import { GlobalsService, RefreshService, I18nService,APP_CONFIG, AppAlertService }   from "../../shared/index";
import { Location }     from "@angular/common";
import { Subscription } from "rxjs";
import { DellHostService,DellChassis,DellChassisService,ChassisCredentialProfile, NavService,DellChassisEMCService } from "../../services/index";

const data = [
  {  "components":[	]    }
 ];


@Component({
   selector: "monitor-profile-chassis",
   styleUrls: ["./monitor-profile-chassis.component.scss"],
   templateUrl:"./monitor-profile-chassis.component.html",
})

export class MonitorProfileChassisComponent implements OnInit {
   hostId: string;
   ProfileObject = data;
   status_row=1;
   selectedRow:number;
   multipleSelect=[];
   wizardStatus:boolean=false;
   private refreshViewSub: Subscription;
   chassisList:any=[];
   chassisValue:any=[];
   selectedObj:any=[];
   associatedChassisCollection:DellChassis[];
   loading :boolean=true;
   editchassis:string;
   addProfile:boolean;
   testchassis:boolean=false;
   staticData:ChassisCredentialProfile;
   testVal:any;
   selectedRowNum:number;
   allexpand:boolean=false;
  
  constructor(  private gs: GlobalsService,
                  public  i18n: I18nService,
                  private location: Location,
                  public  nav: NavService,
                  private refreshService: RefreshService,
                  private hostService:DellHostService,
                  private dellChassisService:DellChassisService,
                  private dellChassisEMCService:DellChassisEMCService,  
                  private ref:ChangeDetectorRef,
                  private route: ActivatedRoute  ) {
              this.refreshViewSub = refreshService.refreshObservable$.subscribe(
              () => this.refreshView());
  }

  showHost(val,i) {
      val.indexPosition=val.profileName+''+val.serviceTag;
      this.multipleSelect[i]=val;
      this.allexpand=false;
  }

  hideHost(val,i) {
	   val.indexPosition="";
	   this.allexpand=false;
  }

   allExpandicon(param){
	    if(param==='expand'){
	    	return this.allexpand=true;   
		  }
      else{
			  return this.allexpand=false;
		  }
   }

    openWizard(val){   
      this.wizardStatus=true;
      this.editchassis=val;
     }

   chassisStatus(status):void{  
        this.wizardStatus=status;
        this.refreshView(); 
        this.selectedObj=[];
        document.getElementById("editchassis").classList.add('disableIcon');
        document.getElementById("deletechassis").classList.add('disableIcon');
        document.getElementById("testchassis").classList.add('disableIcon');
        document.getElementById("chassisprofile").classList.remove('active');
    }

    selectedActiveRow = function(obj,index){  
        this.selectedObj=obj;  
        this.selectedRow = index;
        this.testVal=this.selectedObj.chassisObj.associatedChassis;
        document.getElementById("editchassis").classList.remove('disableIcon');
        document.getElementById("deletechassis").classList.remove('disableIcon');
        document.getElementById("testchassis").classList.remove('disableIcon');
       
    } 

    disableIcons(){
      this.selectedObj=[];
      document.getElementById("editchassis").classList.remove('disableIcon');
      document.getElementById("deletechassis").classList.remove('disableIcon');
      document.getElementById("testchassis").classList.remove('disableIcon');
    }
 
   ngOnInit():void{
      this.route.params.forEach((params: Params) => {
         this.hostId = params["id"];
      });
      this.refreshView();
   }

   private refreshView(): void {
     this.chassisList=[];
    this.loading=true;
    this.dellChassisService.getDellChassis(). subscribe((response) => {
                               this.chassisValue=response;
                               
                               if(response.length !=undefined){
                                console.log(response.length);
                                 console.log("comming");
                                 this.loading=false;
                                this.updateProfilesForRendering(response);
                               }
                                
                                this.loading=false;
                               },
                                (err)=>{
                              console.log(err);
                                  this.loading=false;
                              },
                              ()=>console.log("chassis get completed")
                            );
                            
    }

   deleteChassis():void{
      this.loading=true;
      if(this.selectedObj.profileID){
         this.dellChassisService.deleteChassisProfile(this.selectedObj.profileID).
                subscribe((response) => {
                    this.refreshView();
                    this.loading=false;
                    this.disableIcons();
                },
                (err)=>{
              console.log(err);
              },
              ()=>console.log("chassis delete completed")
            );
    
     }else{
         this.refreshView();
     }
   
   }
   testconnection(){	this.testchassis=true;	 }
   
   closetestmodal(event){	this.testchassis=event;	}

   updateProfilesForRendering(chassies):any
   {  
   
    if (chassies.length === 0) {
       this.chassisList=[]; 
    }else{
      this.chassisList=[];
      this.loading=false;
        var refIndex=0;
      this.chassisList.splice(0, this.chassisList.length);
      for(let chassis of chassies) 
      {
        var temp={ 
                    chassisObj      : chassis,		
                    profileID       : chassis.profileID,
                    credentials     : chassis.credentials,  
                    profileName     : chassis.profileName,
				            profileDesc		  :	chassis.profileDesc,
                    chassisIp	    	:	(chassis.associatedChassis.length > 0)?((chassis.associatedChassis[0].chassisIp)? chassis.associatedChassis[0].chassisIp :""):"",
                    chassisServiceTag:(chassis.associatedChassis.length > 0)?((chassis.associatedChassis[0].chassisServiceTag)? chassis.associatedChassis[0].chassisServiceTag : ""):"",
                    associtedChassis: chassis.associatedChassis,
                    modifiedDate    : chassis.modifiedDate,
				            dropdown			  :	chassis.associatedChassis.length > 1 ?'yes' : 'no',
				            certificateCheckEnabled	:	chassis.certificateCheckEnabled,
				            creationDate		:	chassis.creationDate,
				            referenceIndex  : refIndex,
				          };
	     this.chassisList.push(temp); 	
       var first:boolean = true;
        for(let entity of chassis.associatedChassis)
          {
              if(!first)
              {
                 var tmpChassis={ chassisIp : entity.chassisIp,
                       ss:'dd',
                       associtedChassis : entity,
                       chassisServiceTag:entity.chassisServiceTag,
                         reference : chassis.profileName+''+(chassis.associatedChassis[0].chassisServiceTag)? chassis.associatedChassis[0].chassisServiceTag : "",
                       referenceIndex:refIndex,child:1}
                 this.chassisList.push(tmpChassis);	
              }
              first = false;	
          }
       
		   refIndex++;	
	    }    
    } 

 }    
 
 checkFunction(id):void{
   if(!this.addProfile){
     if(id){
              this.dellChassisService.deleteChassisProfile(id).
                  subscribe((response) => {
                    this.refreshView();
                  },
                  (err)=>{
                  console.log(err);
              },
                ()=>console.log("chassis delete completed")
              );

           this.addProfile=true;  
     }
    
     }else{
       this.refreshView();
     }
    
   }        

  addData(event):void{ 
      this.addProfile=event;
      this.refreshView();
  }  
}



