import { Component, OnInit, OnDestroy, ViewChild, ViewChildren,NgZone } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellApplianceInfo, DellApplianceService, NavService } from "../../services/index";


@Component({
   selector: "dell-appliance",
   styleUrls: ["./dell-appliance.component.scss"],
   templateUrl: "./dell-appliance.component.html",
})
export class DellApplianceComponent implements OnInit, OnDestroy {
   appliances: DellApplianceInfo[];
   appliance: DellApplianceInfo;
   titleKey: string;
   private refreshViewSub: Subscription;
   instance: DellApplianceComponent;
   loading:boolean=true;

   constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellApplianceService: DellApplianceService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private zone: NgZone,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {
                 this.instance = this;
               //  this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                // () => this.refreshView());
   }

   ngOnInit(): void { 
     
    this.gs.getHtmlPlatform().event.onGlobalRefresh(() => {
      if (this.loading) {
         return;
      }

      this.zone.run(() => {
         this.refreshView();
      });
   });

    this.refreshView();
     
  
  }

   ngOnDestroy(): void { this.refreshViewSub.unsubscribe();}

   private refreshView(): void {
      this.loading=true;
      this.dellApplianceService.getDellAppliances()
            .then(response => {
                this.loading=false;
               this.appliances = response;
            });
   }
  
    public getStatusIcon(appliance:DellApplianceInfo ): string {
      if (appliance.selected === "true") {
         return "info";
      } else if (appliance.selected === "false") {
         return "warning";
      }
      return "error";
   }
}



