
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {Wizard} from "clarity-angular";
import {DellDeploymentService,DellVCenterService,NavService }  from "../../../services/index";

@Component({
   selector: "deployment-hardwareprofile",
   styleUrls: ["./deployment-hardwareprofile.component.scss"],
  templateUrl:"./deployment-hardwareprofile.component.html"
})

export class  DeploymentHardwareProfileComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() openWizard:boolean;
  @Output() closeWizard=new EventEmitter<boolean>();
  @ViewChild("wizard") wizard: Wizard;
  submitted:boolean=false;
  status:boolean=false;
  openModal:boolean=false;
  profiledescription:any;

  constructor( private location: Location,
               public  nav: NavService,
               private route: ActivatedRoute) {}

  ngOnInit(): void {}
   
  private refreshView(): void {}

  doCustomClick(event){
  if ("custom-previous" === event){
    this.wizard.previous();
  }  
	else if("custom-next" === event){
    if(this.status){
       this.wizard.next();
       this.submitted=false;
    }
    else{
        this.submitted=true;
    }
  }
 }

  pageCustomCancel(){
  this.openWizard=false;
  this.closeWizard.emit(this.openWizard);
  this.wizard.reset();
  this.profiledescription.reset();
  }

  deploymentprofileUpdate(event){ this.status=event;}

  EnableSelection(event){
    if(event.target.id=="checkrads_11"){
           document.getElementById('checkbox_12').classList.add('disabled');
            document.getElementById('checkbox_11').classList.remove('disabled','disabled');
    }
    else{ 
          document.getElementById('checkbox_12').classList.remove ('disabled','disabled');
          document.getElementById('checkbox_11').classList.add('disabled');
    }
  } 
  openmodal(){ this.openModal=true;  }  

  basicVal(event){ this.openModal=event;}    

  reset(){
    this.wizard.reset();
    this.profiledescription.reset();
  }

  formValue(event){ this.profiledescription=event;}  
}


