
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren,TemplateRef ,Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {FormGroup, FormControl, Validators,ReactiveFormsModule,FormsModule } from "@angular/forms";
import { GlobalsService, RefreshService, I18nService, APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ProfileRequest,Profile,TemplateRequest,DellDeploymentService, NavService,DellTaskschedulerService } from "../../services/index";

@Component({
   selector: "deployment-templates",
   styleUrls: ["./deployment-templates.component.scss"],
   templateUrl:"./deployment-templates.component.html"
})


export class DeploymentTemplatesComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() inputParam:string;
  gridVal:any;
  childNavStatus="";
  chassisStatus=false;
  instance: DeploymentTemplatesComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  templateObject:any=[];  
  tabval:string;
  service_name:string       = "profileService";
  HARDWARE_PROFILE:string   = "HARDWARE_PROFILE";
  HYPERVISOR_PROFILE:string = "HYPERVISOR_PROFILE";
  SYSTEM_PROFILE:string     = "SYSTEM_PROFILE";
  DEPLOYMENT_TEMPLATE:string = "DEPLOYMENT_TEMPLATE";
  HARDWARE_PROFILE_EXTRACT:string = "HARDWARE_PROFILE_EXTRACT";
  hardwareReturn:any;
  hypervisorReturn:any;
  selectedRowIndex:number;
  activeRow:any;
  profileDetails:any;
  actionType:string="add";
  
  hypervisorProfile:string[]=[];
  hardwareProfile:string[]=[];
  systemProfile:string[]=[];
  profileChecked:boolean=false;
  openModal:boolean=false;
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
               private _dellDeploymentService:DellDeploymentService
             ){
            this.instance = this;
            this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }


      
   ngOnInit(): void {  
      this.nav.setViewType('summary');
      this.getHypervisorProfiles();
      this.getHardwareProfiles();
      this.refreshView();
      if(this.inputParam){
            this.tabval=undefined;
      }
      
   }
 
   tagNav(param){ this.tabval=param;  }     
 
    private refreshView(): void {
        this.loading=true;
        let  request: TemplateRequest = new TemplateRequest();
        request.templateID   = 0;
        request.templateType = this.DEPLOYMENT_TEMPLATE;
        
        this._dellDeploymentService.getDeployTemplateData(request)  
            .subscribe( (response) => {
                this.loading=false;
                console.log(response);
                           // this.templateObject=response;
                            this.getCollectionProfile(response);
                            
                            },
                        (err) => console.error(err),
                        () => console.log("observable complete")
                    ); 

                console.log(this.templateObject);
                    
    } 

    getHardwareProfiles(){

        let  request: ProfileRequest = new ProfileRequest();
        request.profileType = []
        request.profileID   = 0;
        request.profileType.push(this.HARDWARE_PROFILE);
        this._dellDeploymentService.getDeploymentProfiles(request)  
            .subscribe( (response) => {
                            console.log(response);
                            this.hardwareReturn=response;
                       },
                        (err) => console.error(err),
                        () => console.log("observable complete")
                    );


                    let  request1: ProfileRequest = new ProfileRequest();
                    request1.profileType = []
                    request1.profileID   = 0;
                    request1.profileType.push(this.HARDWARE_PROFILE);
                    request1.profileType.push(this.HYPERVISOR_PROFILE);
                    this._dellDeploymentService.getDeploymentProfiles(request1)  
                        .subscribe( (response) => {
                                        console.log(response);
                                        this.profileDetails=response;              
                                   },
                                    (err) => console.error(err),
                                    () => console.log("complete complete")
                                );

        }               


    getCollectionProfile(data){
        
        this.templateObject=data;

       for(let i=0;i<=this.templateObject.length;i++){
            if(this.templateObject[i].attachedProfiles){
                for(let j=0;j<=this.templateObject[i].attachedProfiles.length;j++){
                    if(this.templateObject[i].attachedProfiles[j] !=undefined){
                        for(let k=0;k<=this.profileDetails.length;k++){
                            if(this.profileDetails[k]){
                               if(this.profileDetails[k].profileID==this.templateObject[i].attachedProfiles[j].profileID){
                                if(this.profileDetails[k].installerDetails){
                                    this.templateObject[i].hypervisor=this.profileDetails[k].profileName;
                                 
                                }else{
                                    this.templateObject[i].hardware=this.profileDetails[k].profileName;
                                }
                               }

                            }
                        }
                     }
                   }
                 console.log(this.templateObject[i]);
              
            
           }
          
       }

       

    }


    returnProfilename(profileId,templateval){
    //    alert(profileId);
     for(let i=0;i<=this.profileDetails.length;i++){
         if(this.profileDetails[i].profileID){
            if(this.profileDetails[i].profileID==profileId){
             if(this.profileDetails[i].installerDetails){
               return this.profileDetails[i].profileName;
              
             }else{
                return this.profileDetails[i].profileName;
             }
            }
         }
     }
        
    }
    getHypervisorProfiles(){
        
        let  request: ProfileRequest = new ProfileRequest();
        request.profileType = []
        request.profileID   = 0;
        request.profileType.push(this.HYPERVISOR_PROFILE);
        this._dellDeploymentService.getDeploymentProfiles(request)  
            .subscribe( (response) => {
                            console.log(response);
                            this.hypervisorReturn=response;
                        },
                        (err) => console.error(err),
                        () => console.log("observable complete")
                    );
    }
    
    openWizard(type){ this.actionType=type; this.openModal=true; console.log("test"); }
    
    editWizard(type){  
            if(this.activeRow !=undefined){
                this.actionType=type;
                this.openModal=true;
            } 
     }
    closemodel(event){ this.openModal=event; alert("close wizard"); console.log("close modales");}

    saveModal(){  
        this.openModal=false; this.profileChecked=false;
       
    }

    activateRow(obj,i){ 
        if(obj){

            this.activeRow=obj;
            this.selectedRowIndex = i;

            if(obj.id !==undefined){
                document.getElementById("edit-icon").classList.remove('disableIcon');
                document.getElementById("delete-icon").classList.remove('disableIcon');
             }else{
                document.getElementById("edit-icon").classList.add('disableIcon');
                document.getElementById("delete-icon").classList.add('disableIcon');
           }
        }
    }

    deleteTemplate(){
        if(this.activeRow !=undefined){
            if(this.activeRow.id !=undefined){
                this._dellDeploymentService.deleteTemplateProfile(this.activeRow.id)  
                        .subscribe( (response) => {
                                console.log(response);
                                this.refreshView();
                             },
                            (err) => console.error(err),
                            () => console.log("delete template complete")
                        );
            }
            
        }else{
            this.activeRow={};
            document.getElementById("edit-icon").classList.add('disableIcon');
            document.getElementById("delete-icon").classList.add('disableIcon');
        }
    } 

    
    
    getProfileName(obj,type){
          console.log(obj);
 
          if(type==='hardwareProfile'){
             if(this.hardwareProfile){
                for(var i=0;obj.length>i;i++){
                    this.hardwareProfile[obj[i].profileID]=obj[i].profileName
                    if(this.hardwareProfile[obj[i].profileID]==obj[i].profileID){
                      return (this.hardwareProfile[obj[i].profileID])?this.hardwareProfile[obj[i].profileID]:'None';    
                    }else{
                        return "None";
                    }
                 } 
             }else{
                 return "None";
             } 
          }
          if(type==='hypervisorProfile'){
             if(this.hypervisorProfile){
                for(var i=0;obj.length>i;i++){
                    if(this.hypervisorProfile[obj[i].profileID]==obj[i].profileID){
                            return (this.hypervisorProfile[obj[i].profileID])?this.hypervisorProfile[obj[i].profileID]:'None';   
                    }else{
                        return "None";
                    }
                 }
             }else{
                 return "None";
             }  
               
          }
          if(type==='systemProfile'){
            if(this.systemProfile){
            for(var i=0;obj.length>i;i++){
                    if(this.systemProfile[obj[i].profileID]===obj[i].profileID){
                        return (this.systemProfile[obj[i].profileID])?this.systemProfile[obj[i].profileID]:'None';   
                    
                    }else{
                        return "None";
                    }
                }
                
             }else{
                return 'None';
            }    
          }
    }
}


