import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { ServerJob,ServerJobDetail,Job,HostJob,VCenterDetail,ChassisInventoryJobDetails,ChassisWarrantyJobDetails} from "./index";

export const hostInventoryType = "spectre:JobDetail";
export const hostWarrantyJob  = "spectre:WarrantyJobDetail";
export const timezoneOffset   = new Date().getTimezoneOffset();
export const INVENTORY:string = "Inventory"; 

export interface DellTaskschedulerServiceBase 
{
   getDeployment(jobType:string): Promise<ServerJob>;
}

@Injectable()
export class DellTaskschedulerService implements DellTaskschedulerServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   private getHostJobsUrl(): string {
      let url: string;
      url = this.gs.getWebContextPath() + "/rest/data/list/?"
               + "targetType=" + hostInventoryType + "&properties=lastRun,nextRun,jobHistory,vCenterAddress,failed,passed";
      return url;
   }

   private getHostAllJobsUrl(): string {
    let url: string;
    let vcIp="100.96.20.239";

    url =  this.gs.getWebContextPath() + "/rest/services/getHostAllJobs" +
    "?type=" + INVENTORY +
       "&timezoneOffset=" + timezoneOffset+"&vcenterIp="+vcIp;
    return url;
 }

   private getHostWarrantyJobUrl(): string {
    let url: string;
    url = this.gs.getWebContextPath() + "/rest/data/list/?"
             + "targetType=" + hostWarrantyJob + "&properties=lastRun,nextRun,jobHistory,vCenterAddress,failed,passed";
    return url;
 }

   private getChassisInventoryUrl(): string {
     let url: string;
      url = this.gs.getWebContextPath() + "/rest/services/chassisInventoryJob";
      return url; 
    }
  private getChassisWarrantyJobUrl(): string {
     let url: string;
      url = this.gs.getWebContextPath() + "/rest/services/chassisWarrantyJob";
      return url; 
    }   
  private runJobNowForChassisUrl(){
    let url: string;
    url = this.gs.getWebContextPath() + "/rest/services/runJobNowForChassis";
    return url; 
  }

   private getDeploymentServiceUrl(jobType:string): string {
      let url: string;
      //let jobid:number = 2;
      url = this.gs.getWebContextPath() + "/rest/services/taskDeployment?"
               + "jobType=" + jobType;
      return url;
   }

   getDeployment(jobType:string): Promise<ServerJob> {
      let headers = this.gs.getHttpHeaders;
      return this.http.get(this.getDeploymentServiceUrl(jobType), headers)
            .toPromise()
            .then(response => (response.json()).deployment as ServerJob)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }   

    getHostJobs(): Promise<VCenterDetail> {
      let headers = this.gs.getHttpHeaders;
      return this.http.get(this.getHostJobsUrl(), headers)
            .toPromise()
            .then(response => (response.json()) as VCenterDetail)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

   getHostWarrantyJob():Promise<VCenterDetail>{
     let headers = this.gs.getHttpHeaders;
     return this.http.get(this.getHostWarrantyJobUrl(),headers)
            .toPromise()
            .then(response =>(response.json()) as VCenterDetail)
            .catch(error => this.errorHandler.httpPromiseError(error)); 
   }

   getChassisInventory(): Promise<ChassisInventoryJobDetails>
   {
          let headers = this.gs.getHttpHeaders;
             return this.http.get(this.getChassisInventoryUrl(), headers)
                    .toPromise()
                    .then(response => (response.json()).data as ChassisInventoryJobDetails)
                    .catch(error => this.errorHandler.httpPromiseError(error));
  
   }

   runJobNowForChassis(): Promise<any>
   {
          let headers = this.gs.getHttpHeaders;
             return this.http.get(this.runJobNowForChassisUrl(), headers)
                    .toPromise()
                    .then(response => (response.json()) as any)
                    .catch(error => this.errorHandler.httpPromiseError(error));
  
   }

   getChassisWarrantyJob(): Promise <ChassisWarrantyJobDetails> 
   {
          let headers = this.gs.getHttpHeaders;
             return this.http.get(this.getChassisWarrantyJobUrl(), headers)
                    .toPromise()
                    .then(response => (response.json()).data as ChassisWarrantyJobDetails)
                    .catch(error => this.errorHandler.httpPromiseError(error));
   }             

   getHostAllJobs():Observable<any[]>{
    let headers =this.gs.getHttpHeaders();
    return this.http.get(this.getHostAllJobsUrl(),headers)
         .map(response =>(response.json() as any[] ))
         .catch(error => this.errorHandler.httpPromiseError(error));     

}

}



