// A simple VCenterRootNode object model
export class VCenterRootNode {
   vcenterHostName: string;
   vcenterIp: string;
   rootNode: VCenterNode;
   errorMessage: string;
 
   constructor(clone: VCenterRootNode = null) {
      this.vcenterHostName = clone ? clone.vcenterHostName : "";
      this.vcenterIp = clone ? clone.vcenterIp : "";
      this.rootNode = clone ? clone.rootNode : null;
      this.errorMessage = clone ? clone.errorMessage : "";
   }
}

export class VCenterNode {

   name: string;
   managedObjectId: string;
   type: string;
   isVisible: boolean;
   serviceTag: string;
   children: VCenterNode[];
   CP:string;


   constructor(clone: VCenterNode = null) {
      this.name = clone ? clone.name : "";
      this.managedObjectId = clone ? clone.managedObjectId : "";
      this.type = clone ? clone.type : null;
      this.isVisible = clone ? clone.isVisible : false;
      this.serviceTag = clone ? clone.serviceTag : "";
      this.children = clone ? clone.children : [];
      this.CP = clone ? clone.CP : "";
   }
}



