import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { Observable } from "rxjs/Observable";

import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import {VCenterSettings,SettingRequest } from "./index";


export interface DellConfigSettingServiceBase 
{
 //  getDeployment(jobType:string): Promise<ServerJob>;
}

@Injectable()
export class DellConfigSettingService implements DellConfigSettingServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   getSettingUrl(request:SettingRequest):string{
    let url: string;
    let jsonString:string = JSON.stringify(request);
    url = this.gs.getWebContextPath() + "/rest/services/dellconfigSettings?"
            + "request=" + jsonString;
     
        return url;   
   }

   getSetting(request:SettingRequest):Observable<VCenterSettings>{
    let headers =this.gs.getHttpHeaders();
    return this.http.get(this.getSettingUrl(request),headers)
        .map(response =>(response.json().settings as VCenterSettings ))
        .catch(error => this.errorHandler.httpPromiseError(error));     

 }


}



