import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Inject,
    forwardRef, ChangeDetectorRef } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { WebPlatform }  from "../../shared/vSphereClientSdkTypes";    
import {DellHostService,DellChassisService,ChassisFanInfo,ChassisCredentialProfile, NavService,DellChassisEMCService,ChassisHealth} from "../../services/index";        
import{Observable}from "rxjs";

//import { TabLink } from "clarity-angular";

import { EchoService }  from "../../services/index";
import { DialogBoxComponent }  from "../../shared/dev/dialog-box.component";
import { UserSettingService } from "../../shared/user-settings.service";

 
@Component({
   selector: 'chassis-summary',
   styleUrls: ['./chassis-summary.component.scss'],
   templateUrl: './chassis-summary.component.html'
   })
  
   
export class DellChassisSummaryComponent implements OnInit {
    private refreshViewSub: Subscription;
    loading: boolean = true;
    chassisSummaryData:any;
    fanData=ChassisFanInfo;
    totalnormal:number=0;
    totalerror:number=0;
    totalwarning:number=0;
    totalnotpresent:number=0;
    chassisSummary:any;
    serviceTag:string;
    selectedChassis:any;
    private refreshSubscription: Subscription;
    private webPlatform: WebPlatform;
    Unknomn:string="Nothing";
	
    @ViewChild(DialogBoxComponent) dialogBox: DialogBoxComponent;
    
    constructor(
                 private refreshService: RefreshService,
                 private dellChassisService:DellChassisService,
                 private dellChassisEMCService:DellChassisEMCService,  
                 private hostService:DellHostService,
                 private route: ActivatedRoute,
                 public gs: GlobalsService,
                 private userSettingService: UserSettingService,
                 public  i18n: I18nService,
                 private changeDetector: ChangeDetectorRef){

                    this.webPlatform = this.gs.getWebPlatform();
                    
                          // Subscribe to refreshService to handle the global refresh action
                          this.refreshSubscription = refreshService.refreshObservable$.subscribe(
                                () => this.refreshView());
                 }


    ngOnInit(): void {
        this.getRouteObjectId();
        this.refreshView();this.getChassisData();
    }

    getRouteObjectId(){
        this.route.params.forEach((params: Params) => {
            const id = params["id"];
            console.log(id);
            let splitId=id.split(":");
            if(splitId.length != 0){
                let serviceTagArray=splitId[splitId.length-1].split("%252F");
                this.serviceTag=(serviceTagArray !=undefined)?serviceTagArray[0]:"";
                console.log(this.serviceTag);
            }else{
                this.serviceTag="";
            }
            
         });  
     
    }

    getChassisData(){
        this.hostService.getDellChassis()
        .then(response =>{
                    //   console.log(response);
                          if (response.length === 0) {
                          return;
                          }
                         this.getDataDisplay(response);                    },
                    error=>{
                          console.log("error");
                    }
              )      
    }

    getDataDisplay(res){
        console.log(res);
        for(var i=0;i<res.length;i++){
            console.log(res[i]);
            console.log(this.serviceTag);
            if(res[i].chassisServiceTag === this.serviceTag){
             this.selectedChassis=res[i];
             console.log(this.selectedChassis);
            }
        }
    }
     
    private refreshView() {
            this.dellChassisEMCService.getChassisHealth(this.serviceTag).subscribe((response) => {
                         this.chassisSummaryData=response;
                         console.log(this.chassisSummaryData);
                         this.countStatusVal(this.chassisSummaryData.modules);
                         this.loading=false; 
                },(err)=>{
                            console.log(err);
                            this.loading=false; 
                     }); 
     }

    countStatusVal(arry){
        if(arry !=undefined){
            for(var y=0;arry.length>y;y++){
                    let totalNormal=0;
                    let totalError=0;
                    let totalWarning=0; 
                    let totalNotpresent=0;
                    let final=arry[y].modules;
                    for(var x = 0; final.length >x; x++){
                    if(final[x].health==='NORMAL'){
                        totalNormal++;
                        this.totalnormal++;
                        }
                    else if(final[x].health==='WARNING'){
                        totalWarning++;
                        this.totalwarning++;
                    }
                    else if(final[x].health==='CRITICAL'){
                        totalError++;
                        this.totalerror++
                    }
                        else if(final[x].health==='NOT_APPLICABLE'){
                        totalNotpresent++;
                        this.totalnotpresent++;
                        }
                    }
                    arry[y]['normal']=totalNormal;
                    arry[y]['warning']=totalWarning;
                    arry[y]['critical']=totalError;
                    arry[y]['notpresent']=totalNotpresent;
                    this.chassisSummary=arry;
            }
        }    
     }

    getStatusIcon(health:string): string 
    { 
        var ret: string;
        switch(health) 
        { 
                case 'NORMAL':
                {
                    ret = 'ic-status-ok-32';
                    break; 
                } 
                case 'WARNING':
                {
                    ret = 'ic-status-warning-32';
                    break; 
                }
                case 'CRITICAL':
                {
                    ret = 'ic-status-error-32';
                    break; 
                }
                default: 
                { 
                    ret = 'unknown-icon24-status';
                    break; 
                } 
        } 
        
            return ret;		
        
    }

    getDialog(){
        this. webPlatform.setDialogSize(1000,400);
        let title="custom dialog modal";
        const url = this.gs.getWebContextPath() + "/index.html?view=echo-modal";
        this.webPlatform.openModalDialog(title, url, 288, 150, null);
     
        }

    
}



