
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {DellChassisService,ChassisCredentialProfile, NavService,DellChassisEMCService} from "../../services/index";

 @Component({
   selector: "dell-chassis",
   styleUrls: ["./dell-chassis.component.scss"],
   templateUrl:"./dell-chassis.component.html"
})

export class DellChassisComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() monitorParam:string;
  jobNavStatus="Overview";
  chassisStatus=false;
  TotalProfile:number=0;
  loading: boolean = true;
  tabval:string="monitor";
  MonitorData:any;
  serviceTag:string;
 
  constructor(private refreshService: RefreshService,
              private dellChassisService:DellChassisService,
              private dellChassisEMCService:DellChassisEMCService,  
              private route: ActivatedRoute )
  {
       this.refreshViewSub = refreshService.refreshObservable$.subscribe(
       () => this.refreshView());
  }
     
  ngOnInit(): void {this.getRouteObjectId(); this.refreshView();}

  getRouteObjectId(){
    this.route.params.forEach((params: Params) => {
        const id = params["id"];
       // console.log(id);
        let splitId=id.split(":");
        if(splitId.length != 0){
            let serviceTagArray=splitId[splitId.length-1].split("%252F");
            this.serviceTag=(serviceTagArray !=undefined)?serviceTagArray[0]:"";
         //   console.log(this.serviceTag);
        }else{
            this.serviceTag="";
        }
        
     });  
 
}
 
  connectionNav(linkVal){  this.jobNavStatus=linkVal; }
     
   private refreshView(): void {
     if(this.serviceTag){
           this.dellChassisEMCService.getChassisInventory(this.serviceTag).
                            subscribe((response) => {
                                this.MonitorData=response;                         
                            },    
                            (err)=>{
                              console.log(err);
                            },
                             ()=>console.log("chassis warranty get completed")
                            );  
        }                 
    }

   openComp(event){ this.jobNavStatus=event;}

   length(obj) { return Object.keys(obj).length;}

}


