import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {FormGroup,NgForm, FormControl, Validators,FormBuilder,ReactiveFormsModule} from "@angular/forms";		 

@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "firmware-wizard-UpdateSource",
   styleUrls: ["./firmware-wizard-UpdateSource.component.scss"],
   templateUrl: "./firmware-wizard-UpdateSource.component.html"

})

export class FirmwareWizardUpdateSourceComponent implements OnInit {
  showFileLocation:boolean=false;
  pwdValid:boolean=false;
  pwd:any;
  username:any;
  verifypwd:any;
  formPage:any;
  validUser:boolean;
  validPwd:boolean;
  validVerifyPwd:boolean;
  validfileLocation:boolean;
  validLocation:boolean=false;
  gridRadios:string="current_repo";
  @Input()verifypassword:boolean=false;
  @Input()password:boolean=false;
  @Input()Username:boolean=false;
  @Input()fileLocation:boolean=false;
  @Input() SingleDUP:boolean;
  firmware = {
      userName:"",
      fileLocation:"",
	  password:"",
	  verifyPwd:""};
  formpwd:any;
  @Output() firmwareUpdated = new EventEmitter();
  @Output() firmwareUserName = new EventEmitter();
  @Output() firmwarePassword = new EventEmitter();
  @Output() firmwareVerifyPassword = new EventEmitter();
  @Output() firmwareLocation = new EventEmitter();
  @Output() firmwarecheckrads1=new EventEmitter();
  
  FirmwareUpdate = new FormGroup({
        fileLocation: new FormControl('', Validators.required),
		userName: new FormControl('', Validators.required),
		firmwareBundle:new FormControl(''),
		driverRepository:new FormControl(''),
		firmwareRepository:new FormControl(''),
		password: new FormControl('', Validators.required),
		verifyPwd: new FormControl('', Validators.required)
        
    });
	
    constructor() {}

    ngOnInit() {}

    private refreshView(): void {}

    cTrig(checkboxId){
		if ((<HTMLInputElement> document.getElementById(checkboxId)).checked == true && checkboxId === 'checkrads_1'){
			this.showFileLocation=false;
			this.validLocation=false;
		}else {
			this.showFileLocation=true;
			if(this.firmware.fileLocation)
			this.validLocation=true;
			else
			this.validLocation=false;
			
		}
	}
	
	onBlurPswd(event,id){
      this.pwd= (<HTMLInputElement>document.getElementById(id)).value;
      if(this.pwd===""||event===""){
      		if(id=='Password'|| id=='verifyPassword')
           	this.pwdValid =false;
	   }
      if(event!=="" && this.pwd!==""){  
          if(this.pwd!==event){
		     
          	if(id=='password'|| id=='verifyPwd')
               this.pwdValid =true;
		  }
          else{
              this.pwdValid=false;
			  }
	 }
	
	}
	onBlurLocation(event){
	  if(this.firmware.fileLocation){
	     this.validLocation=true;
	   }
	   else{
	   this.firmware.userName="";
	   this.firmware.password="";
	   this.firmware.verifyPwd="";
	   this.validLocation=false;
	   }
	}

	formStatus(){
	 if((<HTMLInputElement> document.getElementById('checkrads_2')).checked == true){
	    if(this.FirmwareUpdate.valid && (this.firmware.password===this.firmware.verifyPwd)){
		    this.validVerifyPwd=false;
			this.firmwareUpdated.emit(true);
		}
		else{
		    
	        if(this.firmware.fileLocation.length==0){
			
			   this.validfileLocation=true;
			  
			 }
			 else this.validfileLocation=false;
		    
		     if(this.firmware.userName.length==0){
			 
			   this.validUser=true;
			  
			 }
			 else this.validUser=false;
		     if(this.firmware.password.length==0){
			 
			  this.validPwd=true;
			  			 }
			 else this.validPwd=false;
			 
			 if((this.firmware.verifyPwd.length!=0 && this.firmware.password!==this.firmware.verifyPwd) ||this.firmware.verifyPwd.length==0)
			 {
			
			  this.validVerifyPwd=true;
			 }
			 else
			  this.validVerifyPwd=false;
			
			 
			this.firmwareUpdated.emit(false);
			this.firmwareUserName.emit(this.validUser);
			this.firmwarePassword.emit(this.validPwd);
			this.firmwareLocation.emit(this.validfileLocation);
			this.firmwareVerifyPassword.emit(this.validVerifyPwd);
		 }
	 }
	 else if((<HTMLInputElement> document.getElementById('checkrads_1')).checked == true){
	 this.firmwareUpdated.emit(true);
	 this.firmwarecheckrads1.emit(true);
	}
	 
	}
}


