
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { NavService,DellTaskschedulerService, ChassisWarrantyJobDetails } from "../../services/index";

@Component({
   selector: "chassis-warranty",
   styleUrls: ["./chassis-warranty.component.scss"],
   templateUrl:"./chassis-warranty.component.html"
})

export class ChassisWarrantyComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() jobNavStatus:string;
  chassisStatus=false;
  instance: ChassisWarrantyComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  chassisData:ChassisWarrantyJobDetails;  
  
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
               private _dellTaskschedulerService: DellTaskschedulerService
              ) {
         this.instance = this;
         this.refreshViewSub = refreshService.refreshObservable$.subscribe(
          () => this.refreshView());
   }

   ngOnInit(): void 
   {  
      this.nav.setViewType('summary');
      this.refreshView();
   }
 
    private refreshView(): void {
       this._dellTaskschedulerService.getChassisWarrantyJob().
          then(response => 
          {
           this.chassisData=response;
          },
          error=>{
              console.log(error);
          });
    }

   runchassis(){
    this._dellTaskschedulerService.runJobNowForChassis().
           then(response=>{
                   this.refreshView();
                },error=>{
                  console.log(error);
                }
             ) 
    }
}


