import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService,EqualValidator } from "../../../services/index";
import {Wizard} from "clarity-angular";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";

@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "settings-wizard",
   styleUrls: ["./settings-wizard.component.scss"],
   templateUrl: "./settings-wizard.component.html",
   providers: [EqualValidator]
})
  


export class SettingsWizardComponent implements OnInit {
   @ViewChild("wizard") wizard: Wizard;
   @Input() openStatus:boolean;
   vcenterNodes: VCenterRootNode[];
   vcenters: string[];
   show = true;
   @Input() openModal: boolean;
   @Input() modalParam: string;
   @Output() modalStatus = new EventEmitter<boolean>();
   instance: SettingsWizardComponent;
   latestStatus:boolean   =true;
   inventoryStatus:boolean=true;
   warrantyStatus:boolean=true;
   check=true;
   valChanged:boolean=false;
   nameValidation=/^[^@"/[:;|=,+*<>\\\/\\\]]*$/;
   fileValidation = /^(([a-zA-Z0-9]+\:)([/])([a-zA-Z0-9]+)([/])([a-zA-Z0-9]+)+\.([xX][mM][lL]|[gG][zZ]))|((^\\\\)([a-zA-Z0-9]+)([\\\\])([a-zA-Z0-9]+)([\\\\])([a-zA-Z0-9]+)+\.([xX][mM][lL]|[gG][zZ]))$/;
   firmwarerepository:any;
   baremetal:any;
   public snmp: any;
   private refreshViewSub: Subscription;
   
  constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellVCenterService: DellVCenterService,
			         private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
              private equalValidator:EqualValidator) {

       this.instance = this;
       this.firmwarerepository = new FormGroup({
        cataloglocation: new FormControl('',[ Validators.required,Validators.pattern(this.fileValidation)]),  
        username:new FormControl('',[Validators.required,Validators.pattern(this.nameValidation),Validators.minLength(1),Validators.maxLength(64)] ), 
        password: new FormControl('',[Validators.required,Validators.minLength(1),Validators.maxLength(127)]),
        verifypassword: new FormControl('',[Validators.required,Validators.minLength(1)])
       });
      this.baremetal = new FormGroup({
        baremetalusername:new FormControl('',Validators.required),
        baremetalpassword: new FormControl('',Validators.required),
        baremetalverifypassword: new FormControl('',Validators.required)
      });
    this.snmp =new FormGroup({
        community:new FormControl('',Validators.required)
        
      });
  }

  ngOnInit(){  this.refreshView();  }

  private refreshView(): void {}

  onCancel(){ 
    this.openModal=false;
    this.modalStatus.emit(this.openModal);
    this.baremetal.reset();
    this.snmp.reset();
  }

  warrantyReset(){
    this.openModal=false;
    this.modalStatus.emit(this.openModal);
    var warrantystatus=<HTMLInputElement>document.getElementById("warrantycheckbox1");
    if(warrantystatus.checked){
      warrantystatus.checked=false;
    }
  }
  
  firmwareReset(){
    this.openModal=false;
    this.modalStatus.emit(this.openModal);
    this.firmwarerepository.reset();
    this.valChanged=false; 
    var firmwarestatus=<HTMLInputElement>document.getElementById("sharedfolder");
    if(firmwarestatus.checked){
          (<HTMLInputElement>document.getElementById("dellOnline")).checked=true;
          firmwarestatus.checked=false;
    }
  }
  eventformReset(){
    this.openModal=false;
    this.modalStatus.emit(this.openModal);  
    if(!(<HTMLInputElement>document.getElementById("Virtualization")).checked){
        (<HTMLInputElement>document.getElementById("Virtualization")).checked=true;
    } 
      (<HTMLInputElement>document.getElementById("enablecpu")).checked=false;
  }
  inventoryReset(target){
    this.openModal=false;
    this.modalStatus.emit(this.openModal);  
    if(target=="inventory"){
      var elements = document.getElementsByName("schedule");
      (<HTMLInputElement>document.getElementById("inventory")).checked=false;
      this.inventoryStatus=true;
      document.getElementById("time").nodeValue="";
    }
    if(target=="warranty"){
      var elements = document.getElementsByName("schedule");
      (<HTMLInputElement>document.getElementById("warranty")).checked=false;
      this.warrantyStatus=true;
      document.getElementById("warrantytime").nodeValue="";
    }
    if(target=="latest"){
      var elements = document.getElementsByName("schedule");
      (<HTMLInputElement>document.getElementById("latest")).checked=false;
      document.getElementById("latesttime").nodeValue="";
      this.latestStatus=true;   
    }
    
    if((<HTMLInputElement>document.getElementById("monday")).checked || (<HTMLInputElement>document.getElementById("Tuesday")).checked
    || (<HTMLInputElement>document.getElementById("friday")).checked ||(<HTMLInputElement>document.getElementById("Saturday")).checked
    ||(<HTMLInputElement>document.getElementById("wednesday")).checked){
      for(var i=0; i < elements.length ; i++){ 
       (<HTMLInputElement>elements[i]).checked=false;
       }
      (<HTMLInputElement>document.getElementById("Sunday")).checked=true;
    } 
      
      
  }

  enableSelection(event){
    if(event=="latest"){ 
      if((<HTMLInputElement>document.getElementById("latest")).checked ){
        this.latestStatus=false;
      }else{
        this.latestStatus=true;
      } 

    }
    if(event == "warranty"){
        if((<HTMLInputElement>document.getElementById("warranty")).checked){
          this.warrantyStatus=false;
        }else{
          this.warrantyStatus=true;
        }
    }

    if(event == "inventory"){
      if((<HTMLInputElement>document.getElementById("inventory")).checked){
        this.inventoryStatus=false;
      }else{
        this.inventoryStatus=true;
      }
    }
  }
  enableRepository(event){
    if(event.target.id=="sharedfolder"){
            document.getElementById('filelocation').classList.remove('disabled','disabled');
            this.valChanged=true;
    }
    else{
       document.getElementById('filelocation').classList.add('disabled','disabled');
      this.valChanged=false;
         
    }
  }   
}



