import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import {Wizard} from "clarity-angular";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";

@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "monitor-config-wizard",
   styleUrls: ["./monitor-config-wizard.component.scss"],
   templateUrl: "./monitor-config-wizard.component.html"

})

export class MonitorConfigWizardComponent implements OnInit {
   @ViewChild("wizard") wizard: Wizard;
   @Input() openStatus:boolean;
   show = true;
   @Input() openModal1: boolean;
   @Input() editFormVal: any;
   @Output() statusChange = new EventEmitter<boolean>();
   instance: MonitorConfigWizardComponent;
   private refreshViewSub: Subscription;
   selectedHosts:ConsoleEntity[] = [];
   disableNext = true;
   formstatus:boolean=false;
   vcenterformstatus:boolean=false;
   submitted:boolean=false;
   vCeneterFormValid:boolean=false;
   formval:any;
   vcenterIP:any;
   inventoryVal:any;
   configure:boolean=true;
   profile:any;
   dataExist:boolean;
   exit:boolean;
   hostEnable:boolean=true;
   hostsAssociated:any=[];
   enablelist:boolean=false;
   enable:boolean=false;
   
  
  constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellVCenterService: DellVCenterService,
			   private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {

      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

  ngOnInit() {	this.refreshView();	}

	  WizardFormData = new FormGroup({
       profileName: new FormControl('', Validators.required),
	   description: new FormControl('', ),
	   checkbox:new FormControl('',),
       userId: new FormControl('', Validators.required),
       password: new FormControl('', Validators.required),
       idracverifypassword: new FormControl('', Validators.required),
	   certificateCheck:new FormControl('', ),
	   useActiveDirectory:new FormControl('', ),
	   hostpassword: new FormControl('', Validators.required),
       hostusername: new FormControl('', ),
       hostverifypwdinput: new FormControl('', Validators.required),
       
  });

    private refreshView(): void {}
    onCancel()
	{
        this.openStatus=false;
		this.statusChange.emit(this.openStatus);
		this.wizard.reset();
		this.formval.reset();
     }
     



    doFinish()
	{
        this.formval.reset();
		this.openStatus=false;
		this.statusChange.emit(this.openStatus);
		this.wizard.reset();
		this.formstatus=false;
   }

    checkval()
	{
	  if(this.show==true && this.selectedHosts.length<1){
		 document.getElementById("modal-window").style.visibility="visible";
	  }
	  else 
		   document.getElementById("modal-window").style.visibility="hidden";
		   document.getElementById("gridData").style.visibility="visible";
           document.getElementById("gridDataheader").style.visibility="visible";
    }
	
	openmodal()
	{
	   this.show=true;
	   document.getElementById("modal-window").style.visibility="visible";
	  
    }
    
    onUpdate(arg):void{ }
	
	
	
   doCustomClick(event){
    if ("custom-previous" === event){
        this.wizard.previous();
    }  
	else if("custom-next" === event){
      if(this.formstatus){
		this.wizard.next();
        this.submitted=false;
       }
      else{
            this.submitted=true;
       }		   
    }
 }


doFinishCp(event){

    console.log(JSON.stringify(this.selectedHosts));
	
}

 doCustomVcenterClick(event){
    if ("custom-previous" === event){
        this.wizard.previous();
    }  
	else if("custom-next" === event){
      if(this.vcenterformstatus){
        this.wizard.next();
        this.vCeneterFormValid=false;
         }
      else{
            this.vCeneterFormValid=true;
       }		   
    }

 }
statusChanged(event){ this.formstatus=event;}

vcenterFromstatus(event){ this.vcenterformstatus=event};

formValue(event){
  if(event)
    this.formval=event;
 }	

 vCenterValue(event){   this.vcenterIP=event;   }
 
}



