import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter,OnChanges } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {FormGroup, FormControl, Validators} from "@angular/forms";
import { VCenterRootNode, DellDeploymentService, DellVCenterService, NavService } from "../../../services/index";
@Component({
   selector: "reference-server",
   styleUrls: ["./deployment-referenceservermodal.component.scss"],
   templateUrl:"./deployment-referenceservermodal.component.html"
})

export class ReferenceServerComponent  implements OnChanges, OnInit
{
  private refreshViewSub: Subscription; 
   referenceServers: any=[];
   selection=[];
   basic:boolean=true;
   @Input() openModal:boolean;
   @Output() basicVal=new EventEmitter<boolean>();
  constructor( private dellVCenterService: DellVCenterService,
			         private dellDeploymentService: DellDeploymentService, 
               private location: Location,
               public  nav: NavService,
               private route: ActivatedRoute){}

  ngOnInit(): void 
  { 
    this.refreshView();
  }
  ngOnChanges(){
    if(this.openModal)
      this.basic=this.openModal;
  }
  private refreshView(): void {
      this.dellDeploymentService.getReferenceServers()  
       .subscribe( (response) => {
        this.referenceServers=response;
      },
     (err) => console.error(err),
      () => console.log("observable complete"+JSON.stringify(this.referenceServers))
    ); 
  } 
    closeModal(){
     this.basic=false;
     this.basicVal.emit(this.basic);
    }      
}


