import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
    APP_CONFIG, AppAlertService }   from "../../shared/index";
import {DellServerFirmware, SelLog, ServerHardware, DellHostGroupObject,DellHostGroupListObject, DellHostHardwareService,DellDatacenterHardwareService,
   NavService, Controller, Connector, Enclosure, PhysicalDisk, VirtualDisk} from "../../services/index";
import{Observable}from "rxjs";

@Component({
   selector: 'cluster-hardware-virtualstorage',
   styleUrls: ['./cluster-hardware-virtualstorage.component.scss'],
   templateUrl: './cluster-hardware-virtualstorage.component.html'
   })
  
export class ClusterHardwareVirtualStorageComponent implements OnInit {
    private refreshViewSub: Subscription;
    virtualStorage:any=[];
    @Input() virtualObject: any;
    loading: boolean = true;
    activeRow:any;
    selectedRowIndex:number;
    searchableList = ['hostName','serviceTag','name','numberOfPhysicalDisks','capacity','layout'];
    searchVirtual:any="";

   constructor(
       private gs: GlobalsService,
       public  i18n: I18nService,
       private dellHardwareService: DellHostHardwareService,
       private dellHostGroupService:DellDatacenterHardwareService,
       public  nav: NavService,
       private refreshService: RefreshService,
       private route: ActivatedRoute){
           this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                    () => this.refreshView());
       }

    ngOnInit(): void {
      this.virtualStorage=this.virtualObject.dellHostVirtualDisk;
    }

    ngOnDestroy(): void { }

    private refreshView(): void { }

    activateRow(obj,i){ 
        if(obj){
               this.activeRow=obj;
               this.selectedRowIndex = i;
       }
   }
}



