import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellHostService,DellChassis,AssociatedDellChassis,DellChassisService,ChassisCredentialProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import {Wizard} from "clarity-angular";
import {Profile,ChassisTestResult} from "../../services/index";
import {FormGroup, FormControl, Validators,ReactiveFormsModule,FormsModule } from "@angular/forms";
import { ChangeDetectorRef ,OnChanges,SimpleChanges} from '@angular/core';	
import {ChasisAssociatedHost} from "../wizard-chassis/monitor-wizard-chasisassociatedhost.component";				
	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "monitor-wizard-chassis",
   styleUrls: ["./monitor-wizard-chassis.component.scss"],
   templateUrl: "./monitor-wizard-chassis.component.html"

})

export class MonitorWizardChassisComponent implements OnInit {
     @Input() openWizard: boolean;
     @ViewChild("wizard") wizard: Wizard;
     @Output() chassisStatus = new EventEmitter<boolean>();
     @Input() selectedObj:any;
     @Input() editchassis:string;
     @Output() changeStatus=new EventEmitter();
     @Output() associatedChassis = new EventEmitter();
     @ViewChild(ChasisAssociatedHost) chassisHost: ChasisAssociatedHost;
     showModal = true;
     private refreshViewSub: Subscription;
     submitted:boolean=false;
     selectedHosts =[];
     showMessage:boolean=false;
     showMsg:boolean;
     showgrid:boolean;
     selectedVal=[];
     cancelchassislist:boolean;
     @Input() chassisValue:any;
     loading:boolean;
     updateProfilesForRendering:any=[];
     chassisProfile:boolean=false;
     ProfileName:string;
     @Output()addData=new EventEmitter<boolean>();
     chassistestprofile:any;
     test:any;
     selectedObject:any;
     @Input() testVal :string;
     chassisprofile:ChassisCredentialProfile=new ChassisCredentialProfile();
     selectedChassis:AssociatedDellChassis[] = [];
     associatedchassis:boolean=false;
     selectedRow:any;
     chassiscredentialprofile:ChassisCredentialProfile=new ChassisCredentialProfile();
     testObjectCollection:ChassisCredentialProfile=new ChassisCredentialProfile();
     testresult:any;
     selectedchassisRow:any;
     enabled=false;
     isAbort:boolean=false;
     indexval=[];

     constructor(  private gs: GlobalsService,
        public  i18n: I18nService,
        private location: Location,
        public  nav: NavService,
        private refreshService: RefreshService,
        private dellChassisService:DellChassisService,
        private hostService:DellHostService,  
        private route: ActivatedRoute,
        private ref:ChangeDetectorRef  ) {
            this.refreshViewSub = refreshService.refreshObservable$.subscribe();
        }

     ChasisFormData = new FormGroup({
       profileName: new FormControl('', Validators.required),
	   description: new FormControl('', ),
	   username: new FormControl('', Validators.required),
       password: new FormControl('', Validators.required),
       verifypassword: new FormControl('', Validators.required),
	});   
      
    ngOnInit()  { this.chassisHost.getAssociatedHost(); }
  
    ngOnChanges(changes: SimpleChanges){
    if(this.editchassis=="edit"){
      if(this.selectedObj!==undefined){
        if(this.selectedObj.chassisObj !== undefined){
        this.selectedRow=(this.selectedObj.chassisObj)?this.selectedObj.chassisObj.associatedChassis:'';
        this.chassiscredentialprofile.profileName=(this.selectedObj.chassisObj)?this.selectedObj.chassisObj.profileName:'';
        this.chassiscredentialprofile.profileDesc=(this.selectedObj.chassisObj)?this.selectedObj.chassisObj.profileDesc:'';
        this.chassiscredentialprofile.credentials.userName=(this.selectedObj.chassisObj)?this.selectedObj.chassisObj.credentials.userName:'';
        this.chassiscredentialprofile.associatedChassis=(this.selectedObj.chassisObj)?this.selectedObj.chassisObj.associatedChassis:'';
        if(this.selectedObj.chassisObj.associatedChassis.length>0){
           this.associatedchassis=true;
        }
        else{
           this.associatedchassis=false;
           this.showModal=true;
           document.getElementById("modal-window1").style.visibility="visible";
        }
      } 
     } 
    }
    else{
       this.chassiscredentialprofile=new ChassisCredentialProfile();
       this.editchassis="add";
    }
   
  }
   
  openmodal()
   {  
       this.chassisHost.getAssociatedHost();
       this.showModal=true;
       document.getElementById("modal-window1").style.visibility="visible";
       this.selectedVal=this.selectedHosts;  
    }

  doCustomClick(event){
      
    this.chassisHost.getAssociatedHost();
    if ("custom-previous" === event){
        this.wizard.previous();
    }  
	else if("custom-next" === event){
      if(this.ChasisFormData.valid){
          if(this.editchassis=="add"){
            document.getElementById("modal-window1").style.visibility="visible";
             this.showModal=true;
            }
            else if(this.editchassis=="edit"){
               if(this.selectedObj.chassisObj.associatedChassis.length>0){
                  this.selectedChassis=this.selectedObj.chassisObj.associatedChassis;
                  this.selectedHosts=this.selectedObj.chassisObj.associatedChassis;
                  this.showModal=false;
                  this.showMsg=false;
                }
            }
        this.wizard.next();
        this.submitted=false;
     }
      else
        this.submitted=true;
     	   
    }
    }
    onCancel()
	{
	 this.openWizard=false;
	 this.chassisStatus.emit(this.openWizard);
	 this.ChasisFormData.reset();
     this.wizard.reset();
     this.selectedHosts.length=0;
     this.showgrid=false;
     this.changeStatus.emit("not defined");
     this.chassisHost.getAssociatedHost();
     this.testresult=[];
     }

     cancelchassis(event){ this.cancelchassislist=event; }

     onUpdate(arg)
	{
       if(!this.cancelchassislist){     
        this.selectedHosts = arg;
       }
    }
     checkval()
    {    
	  if(this.selectedHosts.length==0){
        this.showMsg=true;    
      }
      else{
        this.showMsg=false;
      }
    }
    showGrid(event){
       
        if(event){
        this.showgrid=event;
        this.showMsg=!event;
        }
       else{
        this.showMsg=event;
        this.showgrid=!event;
       
       }
    }    
	onNotify(message:boolean):void 
	{
      if(message==true){
	  		this.showModal=false;
      }
        
    }
    selectedchassis(selection){ this.selectedChassis=selection;}

    doFinish(){
        this.chassiscredentialprofile.credentials.userName=this.ChasisFormData.get('username').value;
        this.chassiscredentialprofile.credentials.password=this.ChasisFormData.get('password').value;
        this.chassiscredentialprofile.associatedChassis= this.selectedChassis;
        this.chassiscredentialprofile.profileName=this.ChasisFormData.get('profileName').value;
        this.chassiscredentialprofile.profileDesc=this.ChasisFormData.get('description').value;
        if(this.editchassis==="edit"){
           this.chassiscredentialprofile.profileID=this.selectedObj.chassisObj.profileID;
		}else{
          this.chassiscredentialprofile.profileID=0;
        
        }
      
        this.dellChassisService.createChassisProfile(this.chassiscredentialprofile).
                    subscribe(response => {
                                    if(this.chassiscredentialprofile.profileID > 0){
                                        this.closemodal(false);
                                    }else{
                                        this.closemodal(true);
                                    }
                                },error=>{  console.log();    }
                                
                             );           
        this.ProfileName=this.chassiscredentialprofile.profileName;
        this.openWizard=false;
        this.chassisStatus.emit(this.openWizard);
        this.ChasisFormData.reset();
        this.wizard.reset();
        this.showgrid=false;
        this.selectedHosts.length=0;
        this.testresult=[];
        this.chassisHost.getAssociatedHost();     
    }   
    closemodal(event){
     this.chassisProfile=event;
     this.addData.emit(event);
    }

    testconnection(){
     this.testresult=[];    
    for (var j = 0; j < this.selectedHosts.length; j++) { 
        if(this.selectedHosts[j].passed!=undefined){
        delete this.selectedHosts[j]['passed']; 
        delete this.selectedHosts[j]['message'];
        }
    }     
    
     document.getElementById("testchassislist").classList.add('switchimg');
     if(this.enabled)
        this.stopprocess();   
        this.enabled=true;
        this.ref.detectChanges();
       this.dellChassisService.testChassisProfile(this.testObjectCollection).
						subscribe(response => {
                            this.testConnectionRefresh(response);
						},error=>{
                            this.enabled = false;
                            this.ref.detectChanges();
                            document.getElementById("testchassislist").classList.remove('switchimg');
                             document.getElementById("testchassislist").classList.add('testchassislist'); 
              }); 
    }

    testConnectionRefresh(response){
        if(!this.isAbort)   {
             if(response){
                for (let profile of response.status) {
                    var temp = {
                        result: profile.passed,
                        message: profile.message,
                    };

                    this.testresult.push(temp);
                }
                
                
                for (var j = 0; j < this.selectedHosts.length; j++) {
                     if( this.selectedHosts[j].chassisIp===this.testObjectCollection.associatedChassis[0].chassisIp){
                       
                      this.selectedHosts[j]['passed'] = this.testresult[0].result;
                      this.selectedHosts[j]['message'] = this.testresult[0].message;
                    
                 }
                    else{
                       this.selectedHosts[j]['passed'] = '';
                      this.selectedHosts[j]['message'] = '';

                    }
                
                    

                }
                this.enabled = false;
                this.ref.detectChanges();
               document.getElementById("testchassislist").classList.remove('switchimg');
               document.getElementById("testchassislist").classList.add('disabled');
               }  
        }this.isAbort=false;    
            
          
    }
     stopprocess(){
       this.isAbort=true;
	   let result = confirm("Are your sure you want to abort test?");
      
     if (result == true ) {
        this.testObjectCollection['passed'] = 'Aborted';
        this.testObjectCollection['message'] = '';
       this.enabled=false;
       this.ref.detectChanges();
       document.getElementById("testchassislist").classList.remove('switchimg');
       document.getElementById("testchassislist").classList.add('test');   
       }
	} 
       
        selectedRowChassis(obj,index){
        
        document.getElementById("testchassislist").classList.remove('disabled'); 
        this.selectedObject=obj;
        this.selectedchassisRow = index;
        let temp=[];
        temp.push(this.selectedObject);
        this.testObjectCollection.credentials.userName=this.ChasisFormData.get('username').value;
        this.testObjectCollection.credentials.password=this.ChasisFormData.get('password').value;
        this.testObjectCollection.associatedChassis= temp;
        this.testObjectCollection.profileName=this.ChasisFormData.get('profileName').value;
        this.testObjectCollection.profileDesc=this.ChasisFormData.get('description').value;
         if(this.selectedObj){
           this.testObjectCollection.profileID=this.selectedObj.chassisObj.profileID;
         }
         else{
            this.testObjectCollection.profileID=0;
        }
        
     } 
  } 



