import { Directive,Component, Input,Output,ChangeDetectorRef, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import {Wizard} from "clarity-angular";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";

	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "monitor-wizard-test",
   styleUrls: ["./monitor-wizard-test.component.scss"],
   templateUrl: "./monitor-wizard-test.component.html"

})

export class MonitorWizardTestComponent implements OnInit {
	closeModal:boolean=false;
	@Input() openModal  :boolean;
	@Input() gridObject :any;
	@Input() vcenterHost:any;
	@Output() testWizardEmit = new EventEmitter<boolean>();
	private refreshViewSub: Subscription;
	pwd: any;
	hostpwdValid = false;
	iDRACpwdValid = false;
	profile: ConnectionProfile = new ConnectionProfile();
	rowActive = "";
	enable = false;
	enablelist = false;
	disableNext = true;
	result: boolean;
	eventId: string;
	element: any;
	testconnection = false;
	showIcon = false;
	selectedRow: number;
	timeout: any;
	editedVal:any;
	selectedHosts:ConsoleEntity[] = [];
	testResponse:any;
	isAbort:boolean=false;
	hostsAssociated=[];
	associatedHost=[];
	hostEnable:boolean=false;
	HostuserId:string="";
	profileVal=[];
	vcenterNodes:any;
	testResult:any=[];
	
	 constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellVCenterService: DellVCenterService,
			   private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
			   public  nav: NavService,
			   private ref:ChangeDetectorRef,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
           														 () => this.refreshView()); }

  	ngOnInit() { 	this.refreshView(); }

    onCancel(){ this.openModal=false;  this.testWizardEmit.emit(this.openModal); }
    		
	private refreshView(): void {

		this.dellVCenterService.getVCenterTree([], "true")
		.then(vcenterNodes => {
		   if (vcenterNodes.length === 0) {
			  return;
		   }
		   console.log(vcenterNodes);
		   this.vcenterNodes = vcenterNodes;
		   
			this.ref.markForCheck();
		});

	 }

	 
	 
	
	changeImg(event){
	
		console.log(this.gridObject);  
	
	  let  request: ConnectionTestRequest = new ConnectionTestRequest();
	  request.testHosts = []

	  if(this.gridObject.hostIpList){

		for(let host of this.gridObject.hostIpList){
			request.testHosts.push(host.name);
		}
  }
	//  request.testHosts.push(this.rowActive["name"]);
	  request.idracCredential = this.gridObject.idracObject;
	  request.hostCredential = this.gridObject.hostObject;
      console.log(request+"request data is passed");
	  this.eventId=event.currentTarget.id;

	  console.log(this.eventId);
	  if(this.enable){
	  		this.stopprocess()
	   }
	  else{
	   	if(this.eventId==="myImage"){
			document.getElementById(this.eventId).classList.add('switchimg');
			this.enable=true;
		}
	    
	   this.dellConnectionProfileService.testConnectionProfile(request).
		subscribe(response => {	this.testConnectionRefresh(event,response);	},
		   	 error	  =>  console.log("errors"+error));
	  }  
	}

	testConnectionRefresh(event,response){
		if(!this.isAbort){
              if(response){
                 console.log("test connection results");
				console.log(response);

              this.enable=false;
				
			  if(this.eventId==="myImage"){  
	          document.getElementById(this.eventId).classList.remove('switchimg');
			   this.testResult=response.status;
			   let i=0
				for(let checkResult of this.gridObject.hostIpList){
					if(checkResult.name == this.testResult[i].hostName){
						console.log(this.testResult[i]);	
						this.gridObject.hostIpList[i]['hostName'] = this.testResult[i].hostName;
						this.gridObject.hostIpList[i]["outOfBandTestSucceeded"] = response.status[i].outOfBandTestSucceeded;
						this.gridObject.hostIpList[i]["outOfBandTestStatus"]	= response.status[i].outOfBandTestStatus;
						this.gridObject.hostIpList[i]["inBandTestStatus"]		= response.status[i].inBandTestStatus 
						this.gridObject.hostIpList[i]["inBandTestSucceeded"]    = response.status[i].inBandTestSucceeded 		
                        this.ref.markForCheck;  
					}

			        	i++;
				}

			}
			
		}
		}
         this.isAbort=false;   
       
	}
	
	stopprocess(){
		this.isAbort=true;
	   this.result = confirm("Are your sure you want to abort test?");
       if (this.result == true) {
	   this.testResponse="";
       document.getElementById(this.eventId).classList.remove('switchimg');
	   
	   this.enable=false;
	
	   let i=0
	   for(let checkResult of this.gridObject.hostIpList){
		   if(checkResult.name){
			   this.gridObject.hostIpList[i]["outOfBandTestSucceeded"] = false;
			   this.gridObject.hostIpList[i]["outOfBandTestStatus"]	= "Process Aborted";
			   this.gridObject.hostIpList[i]["inBandTestStatus"]		= "Process Aborted";
			   this.gridObject.hostIpList[i]["inBandTestSucceeded"]    = "FAIL";
			   this.ref.markForCheck();  
		   }

		      i++;
	   }
	   clearTimeout(this.timeout);
	   this.disableNext=true;
	}
   }
	
}


