// Connection Profiles
export class InspectDataFromIdrac
{
    model: string;
    generation: number;
    hostCpName: string;
    chassisCpName: string;
    hostConnectionValid: boolean;
    wbemStatus: string;
    idracIp: string;
    idracConnectionValid: boolean;
    isLicenseCompliant: boolean ;
    lastInventoryDate: string;
    hypervisorVersion: string;
    hypervisorStatus: string;
    csiorStatus: string;
    snmpStatus: string;
    rebootRequired: string;
    lockDownStatus: string;
    
//  
    constructor(clone: InspectDataFromIdrac = null) 
    {
        this.model = clone ? clone.model : "";
        this.generation = clone ? clone.generation : 0;
        this.hostCpName = clone ? clone.hostCpName : "";
        this.chassisCpName = clone ? clone.chassisCpName : "";
        this.hostConnectionValid = clone ? clone.hostConnectionValid : false;
        this.wbemStatus = clone ? clone.wbemStatus : "";
        this.idracIp = clone ? clone.idracIp : "";
        this.idracConnectionValid = clone ? clone.idracConnectionValid : false;
        this.isLicenseCompliant = clone ? clone.isLicenseCompliant: false;
        this.lastInventoryDate = clone ? clone.lastInventoryDate : "";
        this.hypervisorVersion = clone ? clone.hypervisorVersion : "";
        this.hypervisorStatus = clone ? clone.hypervisorStatus : "";
        this.csiorStatus = clone ? clone.csiorStatus : "";
        this.snmpStatus = clone ? clone.snmpStatus : "";
        this.rebootRequired = clone ? clone.rebootRequired : "";
        this.lockDownStatus = clone ? clone.lockDownStatus : "";
     }
}

export class HostComplianceData
{

    hostIP:string;
    csiorStatus:string;
	omsaStatus:string;
	omsaVersion:string;
	connectionProfile:string;
    lastRunDate:string;
    hypervisorStatus:string;
    hypervisorVersion:string;
	hostModel:string;
    isModelDeprecated:boolean;
    isESXDeprecated:boolean;
    license:License;
    vcenterIp:string;
		
    
    constructor(clone: HostComplianceData = null) 
    {
        this.hostIP = clone ? clone.hostIP : "";
        this.csiorStatus = clone ? clone.csiorStatus : "";
        this.omsaStatus = clone ? clone.omsaStatus : "";
        this.omsaVersion = clone ? clone.omsaVersion : "";
        this.connectionProfile = clone ? clone.connectionProfile : "";
        this.lastRunDate = clone ? clone.lastRunDate : "";
        this.hypervisorVersion = clone ? clone.hypervisorVersion : "";
        this.hostModel = clone ? clone.hostModel : "";
        this.isModelDeprecated = clone ? clone.isModelDeprecated: false;
        this.isESXDeprecated = clone ? clone.isESXDeprecated : false;
        this.hypervisorStatus = clone ? clone.hypervisorStatus : "";
        this.license = clone ? clone.license :null;
        this.vcenterIp = clone ? clone.vcenterIp : "";
        
     }
}
    export class License
{
        hostAddress:string;
        idracAddress:string;;
        serviceTag:string;;
        serverType:string;;
        licenseComplianceStatus:string;;
        connectionProfile:string;;
        features:Feature;
        deviceLicense:DeviceLicense[];
    
    
    constructor(clone:License=null)
    {
        this.hostAddress       = clone ? clone.hostAddress : "";
        this.idracAddress    = clone ? clone.idracAddress : "";
        this.serviceTag         = clone ? clone.serviceTag : "";
        this.licenseComplianceStatus      = clone ? clone.licenseComplianceStatus : "";
        this.connectionProfile = clone ? clone.connectionProfile:"";
		this.features = clone ? clone.features:null;
		this.deviceLicense = clone ? clone.deviceLicense:[];
       
     
    }
}
export class Feature
{
    feature:string[];
    
    
    constructor(clone:Feature=null)
    {
        this.feature = clone ? clone.feature : [];
        
     
    }
}
export class DeviceLicense
{
    entitlementId:string;
	licenseType:string;
    licenseSoldDate:string;
    licenseInstallDate:string;
    licenseStartDate:string;
    licenseEndDate:string;
    licenseDuration:string;
    evalLicenseTimeRemaining:string;
    licenseDescription:string;
    licensePrimaryStatus:string;
    licenseStatusMessage:string;
    licenseAttributes:string;
    licenseFile:string;
    devices:LicensableDevice;
    
    
    constructor(clone:DeviceLicense=null)
    {
        this.entitlementId       = clone ? clone.entitlementId : "";
        this.licenseType    = clone ? clone.licenseType : "";
        this.licenseSoldDate         = clone ? clone.licenseSoldDate : "";
        this.licenseInstallDate      = clone ? clone.licenseInstallDate : "";
        this.licenseStartDate = clone ? clone.licenseStartDate:"";
		this.licenseEndDate       = clone ? clone.licenseEndDate : "";
        this.licenseDuration    = clone ? clone.licenseDuration : "";
        this.evalLicenseTimeRemaining         = clone ? clone.evalLicenseTimeRemaining : "";
        this.licenseDescription      = clone ? clone.licenseDescription : "";
        this.licensePrimaryStatus = clone ? clone.licensePrimaryStatus:"";
		this.licenseStatusMessage       = clone ? clone.licenseStatusMessage : "";
        this.licenseAttributes    = clone ? clone.licenseAttributes : "";
        this.licenseFile         = clone ? clone.licenseFile : "";
        this.devices      = clone ? clone.devices : null;
       
     
    }
}
export class LicensableDevice
{
        deviceId:string;
	    deviceStatus:string;
        deviceStatusMessage:string;
	    fqdd:string;
	    model:string;
    
    
    constructor(clone:LicensableDevice=null)
    {
        this.deviceId       = clone ? clone.deviceId : "";
        this.deviceStatus    = clone ? clone.deviceStatus : "";
        this.deviceStatusMessage         = clone ? clone.deviceStatusMessage : "";
        this.fqdd      = clone ? clone.fqdd : "";
        this.model = clone ? clone.model:"";
		
       
     
    }
}



















