
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellConnectionProfileService, NavService } from "../../services/index";
import { UserSettingService } from "../../shared/user-settings.service";

@Component({
   selector: "profile-monitor",
   styleUrls: ["./profile-monitor.component.scss"],
   templateUrl:"./profile-monitor.component.html"
})

export class ProfileMonitorComponent  implements OnInit,OnDestroy 
{
  private refreshViewSub: Subscription;
  @Input() monitorParam:string;
  profiles: ConnectionProfile[];
  jobNavStatus:string;
  chassisStatus=false;
  instance: ProfileMonitorComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  tabval:string;
  baseUrl:string;
 
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               private appAlertService: AppAlertService,
               private dellConnectionProfileService: DellConnectionProfileService,
               public  nav: NavService,
               private refreshService: RefreshService,
               private userSettingService: UserSettingService,
               private route: ActivatedRoute) {
    this.instance = this;
    this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void  { 
    this.baseUrl=this.gs.getWebContextPath();
    this.getTabNavCurrentStatus()  }
 
   connectionNav(linkVal){ this.jobNavStatus=linkVal; this.onTabSelected(linkVal,'vertical')}
   
   tagNav(param){ this.tabval=param; this.onTabSelected(param,'groupNav') }   

   private getTabNavCurrentStatus():void{
    let savedVertical: string = this.userSettingService.getSetting("verticalNav");
    let savedGroup: string = this.userSettingService.getSetting("groupNav");
    if (savedVertical !== "Firmware update" && savedVertical !=="system_jobs" && savedVertical !=="Inventory History" && savedVertical !=="Hosts Inventory" && savedVertical !=="Chassis Inventory" && savedVertical !=="Warranty History" && savedVertical !=="Hosts Warranty" && savedVertical !=="Chassis Warranty" ) {
      savedVertical = "Deployment Jobs";
    }
    if (savedGroup !== "job" ) {
      savedGroup = "log";
    }
    this.tabval       = savedGroup;
    this.jobNavStatus = savedVertical;
   }

   onTabSelected(id: string,type:string) {
    this.appAlertService.closeAlert();
    if(type == 'vertical'){
      this.userSettingService.setSetting("verticalNav", id);  
    }else{
      this.userSettingService.setSetting("groupNav", id);
    }
    
    this.refreshView();
 }

   private refreshView(): void {
      this.getTabNavCurrentStatus();
   }

   ngOnDestroy(): void {
    this.refreshViewSub.unsubscribe();
 }
 
}


