import { Injectable } from "@angular/core";
import { Http,Headers } from "@angular/http";
import "rxjs/add/operator/toPromise";


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { LogRequestData,LogMessage,} from "./index";


export interface DellLogServiceBase 
{
   getLog(request:LogRequestData): Promise<LogMessage>;
}

@Injectable()
export class DellLogService implements DellLogServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   
   private getLogServiceUrl(request:LogRequestData): string {
      let url: string;
      let jsonString:string = JSON.stringify(request);
      url = this.gs.getWebContextPath() + "/rest/services/monitorLog?"+ "logrequest=" + jsonString;
      //url = this.gs.getWebContextPath() + "/rest/services/monitorLog";
      return url;
   }

   getLog(requestJson:LogRequestData): Promise<LogMessage> {
      //let headers = this.gs.getHttpHeaders;
      //let headers = this.headersJson;
      //return this.http.post(this.getLogServiceUrl(requestJson),JSON.stringify(requestJson),{headers:this.gs.getHttpHeadersContent()})
      return this.http.get(this.getLogServiceUrl(requestJson),{headers:this.gs.getHttpHeadersContent()})
            .toPromise()
            .then(response => (response.json()).logs as LogMessage)
            .catch(error => this.errorHandler.httpPromiseError(error));


   }   


}



