// Connection Profiles
export class InspectDataFromVcenter
{
    clusterName: string;
    phaEnabled: boolean;
    vsanEnabled: boolean;
    drsEnabled: boolean;
    firewallStatus: boolean;
    userName: string;
    hasMinPriviliges: boolean;
    isInLDM: boolean;
    
//  
    constructor(clone: InspectDataFromVcenter = null) 
    {
        this.clusterName = clone ? clone.clusterName : "";
        this.phaEnabled = clone ? clone.phaEnabled : false;
        this.vsanEnabled = clone ? clone.vsanEnabled : false;
        this.drsEnabled = clone ? clone.drsEnabled : false;
        this.firewallStatus = clone ? clone.firewallStatus : false;
        this.userName = clone ? clone.userName : "";
        this.hasMinPriviliges = clone ? clone.hasMinPriviliges : false;
        this.isInLDM = clone ? clone.isInLDM : false;
     }
}



















