import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { ChassisHealth,WarrantyInfo,DellChassisInventoryDataObject,WarrantyRenewal,Warranty } from "../index";

/**
 * This HostServiceBase interface is optional for HostService but it helps
 * define a simple FakeHostService for testing purposes
 */
export interface DellChassisEMCServiceBase {
   getChassisHealth(servicetag:string): Observable<ChassisHealth>;
   getWarrantyInfo(servicetag:string): Observable<WarrantyInfo>;
   getChassisInventory(servicetag:string): Observable<DellChassisInventoryDataObject>;
   getWarrantyRenewal(servicetag:string): Observable<WarrantyRenewal>;
   getChassisWarranty(servicetag:string): Observable<Warranty>;
   
}


@Injectable()
export class DellChassisEMCService  implements DellChassisEMCServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService) {
   }

   private  SerivceUrl(){
    return "/rest/chassisProfiles/";
   } 
   /**
    * Build the REST url endpoint for retrieving a list of properties.
    * This is mapped to the DataAccessController on the java side.
    */
   private getChassisHealthUrl (servicetag:string): string {
      return this.gs.getWebContextPath() +this.SerivceUrl() +"dellchassishealth?servicetag="+ servicetag;
   };

    getChassisHealth(servicetag:string): Observable<ChassisHealth> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getChassisHealthUrl(servicetag), headers)
            .map(response => (response.json().data  as ChassisHealth))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
    
    private getWarrantyInfoUrl (servicetag:string): string {
      return this.gs.getWebContextPath() +this.SerivceUrl() +"dellchassiswarrantyinfo?servicetag="+ servicetag;
   };

    getWarrantyInfo(servicetag:string): Observable<WarrantyInfo> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getWarrantyInfoUrl(servicetag), headers)
            .map(response => (response.json().data  as WarrantyInfo))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

    private getChassisInventoryUrl (servicetag:string): string {
      return this.gs.getWebContextPath() +this.SerivceUrl() + "dellchassisinventory?servicetag="+ servicetag;
   };

    getChassisInventory(servicetag:string): Observable<DellChassisInventoryDataObject> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getChassisInventoryUrl(servicetag), headers)
            .map(response => (response.json().data  as DellChassisInventoryDataObject))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
    
   private getWarrantyRenewalUrl (servicetag:string): string {
      return this.gs.getWebContextPath() +this.SerivceUrl() + "dellchassiswarrantyrenewal?servicetag="+ servicetag;
   };

    getWarrantyRenewal(servicetag:string): Observable<WarrantyRenewal> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getWarrantyRenewalUrl(servicetag), headers)
            .map(response => (response.json().data  as WarrantyRenewal))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
   private getChassisWarrantyUrl (servicetag:string): string {
      return this.gs.getWebContextPath() +this.SerivceUrl() +"dellchassiswarranty?servicetag="+ servicetag;
   };

    getChassisWarranty(servicetag:string): Observable<Warranty> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getChassisWarrantyUrl(servicetag), headers)
            .map(response => (response.json().data  as Warranty))
            .catch(error => this.errorHandler.httpPromiseError(error));
   }   

  


  

}



