// A simple Host object model
export class LogMessage {
   
   totalPages: number;
   currentPage: number;
   pageSize: number;
   totalMessagesOverall: number;
   logMessage: LogActivityMessage[];
  
   constructor(clone: LogMessage = null) {
      this.totalPages            = clone ? clone.totalPages : 0;
      this.currentPage           = clone ? clone.currentPage :0;
      this.pageSize              = clone ? clone.pageSize : 0;
      this.totalMessagesOverall  = clone ? clone.totalMessagesOverall :null;
      this.logMessage            = clone ? clone.logMessage:[];
      
   }
}


export class LogActivityMessage
{
      id        : number;
      message   : string;
      category  : string;
      createdDate : string;

	constructor( clone: LogActivityMessage=null){
        this.id         = clone ? clone.id:0;
        this.message    = clone ? clone.message:"";
        this.category   = clone ? clone.category:"";
        this.createdDate= clone ? clone.createdDate:"";
    }

}


export class LogRequestData
{
    pageSize    : number;
	currentPage : number;
    sortBy      : string;
    sortOrder   : string;
	filterCategory : string;
	startDate      : string;
    endDate        : string;
    
    
    constructor(clone:LogRequestData=null)
    {
        this.pageSize       = clone ? clone.pageSize : 0;
        this.currentPage    = clone ? clone.currentPage : 0;
        this.sortBy         = clone ? clone.sortBy : "";
        this.sortOrder      = clone ? clone.sortOrder : "";
        this.filterCategory = clone ? clone.filterCategory:"";
        this.startDate      = clone ? clone.startDate:"";
        this.endDate        = clone ? clone.endDate:"";
    }
}


