import {Injectable} from "@angular/core";
import { GlobalsService, RefreshService, I18nService,APP_CONFIG, AppAlertService }   from "../shared/index";

export interface ModalConfig {
   url: string;
   title?: string;
   size?: {
      width: number,
      height: number
   };
   closable?: boolean;
   onClosed?: (result?: any) => void;
   contextObjects?: any[];
   customData?: any;
}

@Injectable()
export class ModalConfigService {

   constructor(public gs:GlobalsService ){}

   public createAddWizardConfig() {
      let addWizardAction: ModalConfig = {
         url: this.gs.getWebContextPath() +"/index.html?view=wizard",
         closable: false,
         size: {width: 900, height: 500}
      };
      return addWizardAction;
  }

  public openAssociatedWizard() {
    let addWizardAction: ModalConfig = {
       url: this.gs.getWebContextPath() +"/index.html?view=wizard",
       closable: false,
       size: {width: 400, height: 200}
    };
    return addWizardAction;
}


   public createDeleteConfig() {
      let deleteAction: ModalConfig = {
         url: "html-sample/index.html?view=delete",
         size: {width: 400, height: 120},
         closable: false
      };
      return deleteAction;
   }

   public createEditConfig() {
      let editAction: ModalConfig = {
         url: "html-sample/index.html?view=edit",
         size: {width: 700, height: 400}
      };
      return editAction;
   }
}



