// A simple Host object model
export class ComponentStatus {
   componentName: string;
   health: string;
   constructor(clone: ComponentStatus = null) {
      this.componentName = clone ? clone.componentName : "";
      this.health = clone ? clone.health : "";
   }
}

export class ServerHealth {
    components: ComponentStatus[];
    serverOverallStatus: string;
    chassisOverallStatus: string;
    sourceOfHealth: string; //iDRAC or OMSA
    hasChassisStatus: string;

   constructor(clone: ServerHealth = null) {
      this.components = clone ? clone.components : [];
      this.serverOverallStatus = clone ? clone.serverOverallStatus : "";
      this.chassisOverallStatus = clone ? clone.chassisOverallStatus : "NA";
      this.sourceOfHealth = clone ? clone.sourceOfHealth : "";
      this.hasChassisStatus = clone ? clone.hasChassisStatus : "";
   }
}



