import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
    APP_CONFIG, AppAlertService }   from "../../shared/index";
import {DellServerFirmware, SelLog, ServerHardware, DellHostGroupObject,DellHostGroupListObject, DellHostHardwareService,DellDatacenterHardwareService,
   NavService, Controller, Connector, Enclosure, PhysicalDisk, VirtualDisk} from "../../services/index";
import{Observable}from "rxjs";

@Component({
   selector: 'cluster-hardware-storage',
   styleUrls: ['./cluster-hardware-storage.component.scss'],
   templateUrl: './cluster-hardware-storage.component.html'
})
  

export class ClusterHardwareStorageComponent implements OnInit {
    hostId: string;
    hardwareStatus:string="Physical Disks";
    hardware:any=[];
    private refreshViewSub: Subscription;
    hostGroupData:DellHostGroupListObject;
    dataCollection:DellHostGroupObject;
    errorMessage:any;
    instance=ClusterHardwareStorageComponent;
    serviceRole:string[];
    displayMode:string;
    loading: boolean = true;
    activeRow:any;
    selectedRowIndex:number;
    details:boolean=false;
    searchphycial:any="";
    searchableList = ['hostName','serviceTag','capacity','diskStatus','modelNum'];
 
      constructor(private gs: GlobalsService,
                private appAlertService: AppAlertService,
                public  i18n: I18nService,
                private location: Location,
                public  nav: NavService,
                private dellHardwareService: DellHostHardwareService,
                private dellHostGroupService:DellDatacenterHardwareService,
                private refreshService: RefreshService,
                private route: ActivatedRoute) {
                    this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                    () => this.refreshView());
        }

        ngOnInit(): void {
            this.route.params.forEach((params: Params) => {
                this.hostId = params["id"];
            });
            this.nav.setViewType('monitor');
            this.refreshView();
         }

        ngOnDestroy(): void { }

        private refreshView() {
                this.dellHostGroupService.getDellDatacenterHardware(this.hostId)
                    .then(hostlist=>{
                       this.hostGroupData = hostlist;
                        if(this.hostGroupData != undefined){
                            this.getHostLists(this.hostGroupData);
                        }
                    },
                    error=>{
                        console.log(error);
                    });
        }

        getHostLists(obj){
            this.dellHostGroupService.getDellDatacenterHostGroup(this.hostId,obj.hostlist.dellHostList)
            .subscribe(hostgroupData=>{
              this.dataCollection=hostgroupData; 
            },
            error=>{
                console.log(error);
            }    
            );
        }

        activateRow(obj,i){ 
            if(obj){
                    this.activeRow=obj;
                    this.selectedRowIndex = i;
            }
        }

        storageChanged(selected){ 
            this.hardwareStatus=selected;
        }
}



