export  class Profile {
    profileID  : number;	
    profileName: string;
    profileDesc: string;
    createdDate: string;
    modifiedDate:string;
    modifiedBy  :string;

    constructor(clone: Profile = null) 
    {
      this.profileID       = clone ? clone.profileID:0;
      this.profileName     = clone ? clone.profileName:"";
      this.profileDesc     = clone ? clone.profileDesc:"";
      this.createdDate     = clone ? clone.createdDate:(new Date().getTime()).toString();
      this.modifiedDate    = clone ? clone.modifiedDate:(new Date().getTime()).toString();
      this.modifiedBy      = clone ? clone.modifiedBy : "";
    }
}

export class ProfileRequest
{
    profileType : string[];
    profileID   : number;
    
    constructor(clone:ProfileRequest = null)
    {
        this.profileType   =  clone ? clone.profileType:[];
        this.profileID     =  clone ? clone.profileID : 0;
    }
} 
export class ReferenceServer
{
    referenceServerAddress : string;
    serviceTag   : string;
    modelNumber   : string;
    hostAddress   : string;
    serverType   : string;
    credentails   : ProfileCredentials;
    status   : number;
    statusName   : string;
    constructor(clone:ReferenceServer = null)
    {
        this.referenceServerAddress   =  clone ? clone.referenceServerAddress:"";
        this.serviceTag     =  clone ? clone.serviceTag :"";
        this.modelNumber   =  clone ? clone.modelNumber:"";
        this.hostAddress     =  clone ? clone.hostAddress :"";
        this.serverType   =  clone ? clone.serverType:"";
        this.credentails     =  clone ? clone.credentails :null;
        this.status   =  clone ? clone.status:0;
        this.statusName     =  clone ? clone.statusName :"";
    }
}   

export class ProfileCredentials
{
    userName : string;
    password   : string;
    domain:string; 
    constructor(clone:ProfileCredentials = null)
    {
        this.userName   =  clone ? clone.userName:"";
        this.password     =  clone ? clone.password :null;
        this.domain   =  clone ? clone.domain:null;
     

    }
}
export class ServerContainer
{
    name : string;
    type   : string;
    referenceServers:ReferenceServer;
    serverContainers:ServerContainer;
    constructor(clone:ServerContainer = null)
    {
        this.name   =  clone ? clone.name:"";
        this.type     =  clone ? clone.type :"";
        this.referenceServers   =  clone ? clone.referenceServers:null;
        this.serverContainers   =  clone ? clone.serverContainers:null;
    }
}


export class TemplateRequest{
    templateID  :   number;
    templateType:   string;

    constructor(clone:TemplateRequest = null){
        this.templateID    =    clone ? clone.templateID:0;
        this.templateType  =    clone ? clone.templateType:""; 
    }
}


export class DeploymentBase
{
    ID      :       number;	
    name    :       string;
    description :   string;
    createdDate :   string;
    modifiedDate:   string;
    modifiedBy  :   string;

    constructor(clone:DeploymentBase = null){
        this.ID        = clone ? clone.ID:0;
        this.name      = clone ? clone.name:"";
        this.description    =   clone ? clone.description:"";
        this.createdDate    =   clone ? clone.createdDate:(new Date().getTime()).toString();
        this.modifiedDate   =   clone ? clone.modifiedDate:(new Date().getTime()).toString();
        this.modifiedBy     =   clone ? clone.modifiedBy  :"";
    }
}


export  class SystemProfileDetail
{

    spId        :number;
    spName      :string;
    spDescription:string;
    creationTime:string;
    updationTime:string;
    updatedBy   : string;
    idracip     : string;
    macAddress  : string;
    serviceTag  : string;
    hostIPOrName: string;
    idracModel  : string;
    systemModel : string;


    constructor(clone:SystemProfileDetail   =   null){

        this.spId       =   clone ? clone.spId : 0;
        this.spName     =   clone ? clone.spName : "";
        this.spDescription = clone ? clone.spDescription : "";
        this.creationTime  = clone ? clone.creationTime : "";
        this.updationTime  = clone ? clone.updationTime : "";
        this.updatedBy     = clone ? clone.updatedBy : "";
        this.idracip       = clone ? clone.idracip : "";
        this.macAddress    = clone ? clone.macAddress : "";
        this.serviceTag    = clone ? clone.serviceTag : "";
        this.hostIPOrName  = clone ? clone.hostIPOrName : "";
        this.idracModel    = clone ? clone.idracModel : "";
        this.systemModel   = clone ? clone.systemModel : ""; 

    }
}
export class Template extends DeploymentBase
{
    attachedProfiles:Profile[];
    vCenterAddress  : string;
    attachedSystemProfileDetails:SystemProfileDetail[];
}

