
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";

import {DellDeploymentService,DellVCenterService,NavService }  from "../../../services/index";
import {Wizard} from "clarity-angular";


@Component({
   selector: "deployment-systemprofile",
   styleUrls: ["./deployment-systemprofile.component.scss"],
  templateUrl:"./deployment-systemprofile.component.html"
})

export class  DeploymentSystemProfileComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() openWizard:boolean; 
  status:boolean=false;
  openModal:boolean=false;
  @ViewChild("wizard") wizard: Wizard;
  @Output() wizardStatus=new EventEmitter<boolean>();
  submitted:boolean=false;
  profiledescription:any;
  
  constructor( private dellVCenterService: DellVCenterService,
			         private dellDeploymentService: DellDeploymentService, 
               private location: Location,
               public  nav: NavService,
               private route: ActivatedRoute) {}

  ngOnInit(): void {}
   
  private refreshView(): void {}

  doCustomClick(event){
    if ("custom-previous" === event){
        this.wizard.previous();
   }  
	 else if("custom-next" === event){
       if(this.status){
           this.wizard.next();
           this.submitted=false;
     }
    else{
        this.submitted=true;
      }
    }
 }
deploymentprofileUpdate(event){ this.status=event;}

openmodal(){ this.openModal=true;}

basicVal(event){ this.openModal=event;}

pageCustomCancel(){
    this.openWizard=false;
    this.wizardStatus.emit(this.openWizard);
    this.wizard.reset();
    this.profiledescription.reset();
   }

  reset(){
   this.openWizard=false;
   this.wizardStatus.emit(this.openWizard);
   this.wizard.reset();
   this.profiledescription.reset();
  }
  formValue(event){ this.profiledescription=event;}
}


