import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { VCenterRootNode, ConsoleEntity, ConnectionProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import {Wizard} from "clarity-angular";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";
	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "monitor-config-event",
   styleUrls: ["./monitor-config-event.component.scss"],
   templateUrl: "./monitor-config-event.component.html"

})

export class MonitorConfigEventComponent implements OnInit {
   @ViewChild("wizard") wizard: Wizard;
   show = true;
   instance: MonitorConfigEventComponent;
   latestStatus:boolean   =true;
   inventoryStatus:boolean=true;
   warrantyStatus:boolean=true;
   activeStatus:string="Virtualization";
   Virtualization:string;
   anyEvents:string;
   post:string;
   Critical:string;
   private refreshViewSub: Subscription;
   
   constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellVCenterService: DellVCenterService,
			         private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {

      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
      () => this.refreshView());
   }

  ngOnInit(){ this.refreshView(); }

  private refreshView(): void {}

  selectedOption(event){ this.activeStatus=event;}
  
}



