import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { ConnectionProfile,	ConnectionTestRequest, ConnectionTestResult} from "./index";


export interface DellConnectionProfileServiceBase 
{
   getConnectionProfiles(): Observable<ConnectionProfile[]>;
   addConnectionProfile(cp:ConnectionProfile): Observable<string>;
   testConnectionProfile(request:ConnectionTestRequest): Observable<ConnectionTestResult>;
}

@Injectable()
export class DellConnectionProfileService implements DellConnectionProfileServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   // Object type
   type = "dellVsphere:ConnectionProfile";

   private  SerivceUrl(){
      return "/rest/connectionProfiles/";
   }
   private getConnectionProfilesUrl(): string {
      let url: string;
      url = this.gs.getWebContextPath() + "/rest/data/list/?"
               + "targetType=" + this.type + "&properties=profileId,profileName," +
                 "description,creationDate,updatedDate," +
                 "createdBy,updatedBy,hostCredential,idracCredential,console";
      return url;
   }

   private getAddConnectionProfilesUrl(cp:ConnectionProfile): string {
      let url: string;
      let jsonString:string = JSON.stringify(cp);
      //url = this.gs.getWebContextPath() + this.SerivceUrl()+"createCP?"+ "CP=" + jsonString;
      url = this.gs.getWebContextPath() + this.SerivceUrl()+"createCP";
	return url;
   }
   
   private getTestConnectionProfilesUrl(request:ConnectionTestRequest): string {
      let url: string;
      let jsonString:string = JSON.stringify(request);
      url = this.gs.getWebContextPath() +this.SerivceUrl()+"testconnection?"
               + "testrequest=" + jsonString;
      return url;
   }

   private getDeleteConnectionProfilesUrl(profileId:number,vcenterIp:string){
      let url: string;
      url = this.gs.getWebContextPath() + this.SerivceUrl() +"deleteconnection?"
               + "profileId=" + profileId + "&vcenterIp=" + vcenterIp;
      return url;
   }

  private getUpdateConnectionProfilesUrl(cp:ConnectionProfile[] ,hostList:string[]): string {
      let url: string;
      let hosts=hostList.toString();
     // console.log("cp to string"+cp.toString());
      let jsonString:string = JSON.stringify(cp);
      
      url = this.gs.getWebContextPath() + this.SerivceUrl() +"updateCP?"
               + "CP=" + jsonString + "&hostList=" + hostList;
	console.log("getUpdateConnectionProfileUrl"+url);
      return url;
   }


   getConnectionProfiles(): Observable<ConnectionProfile[]> {
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();

      return this.http.get(this.getConnectionProfilesUrl(), headers)
            .map(response => (useLiveData ? response.json().data : response.json()) as ConnectionProfile[])
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

   addConnectionProfile(cp:ConnectionProfile): Observable<string> {
      return this.http.post(this.getAddConnectionProfilesUrl(cp),JSON.stringify(cp),{headers:this.gs.getHttpHeadersContent()})
            .map(response => (response.json().status) as string)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }


   updateConnectionProfile(cp:ConnectionProfile[],hosts:string[]):Observable<string>{
   console.log("value of cp is"+JSON.stringify(cp));
        
       let headers = this.gs.getHttpHeaders;
      return this.http.put(this.getUpdateConnectionProfilesUrl(cp,hosts), headers)
            .map(response => (response.json()) as string)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

   testConnectionProfile(request:ConnectionTestRequest): Observable<ConnectionTestResult> {
      let headers = this.gs.getHttpHeaders;
      return this.http.get(this.getTestConnectionProfilesUrl(request), headers)
            .map(response => (response.json()) as ConnectionTestResult)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }   

   deleteConnectionProfile(profileId:number,vcenterIp:string): Observable<string>{
      let headers = this.gs.getHttpHeaders;
      return this.http.put(this.getDeleteConnectionProfilesUrl(profileId,vcenterIp), headers)
            .map(response => (response.json()) as string)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }    


}



