import { Component, Input,Output, OnInit, ViewChild,EventEmitter,OnChanges,SimpleChanges } from "@angular/core";
import { Wizard } from "clarity-angular";
import {AssociatedDellChassis,DellChassisService,ChassisCredentialProfile, ConnectionTestRequest, DellConnectionProfileService, DellVCenterService, NavService } from "../../services/index";
import { ChangeDetectorRef} from '@angular/core';
@Component({
   selector: "monitor-wizard-testchassisprofile",
   styleUrls: ["./monitor-wizard-testchassisprofile.component.scss"],
   templateUrl: "./monitor-wizard-testchassisprofile.component.html"  
})

    export class TestChassisProfile implements OnInit {
    @Input()Testchassis:boolean;
    @Output() closetestmodal=new EventEmitter<boolean>();
     @Input() selectedObj:any;
     @Input() testVal:any;
     enabled:boolean=false;
     testresult:any;
     isAbort:boolean=false;
     chassisProfile=[];
     
    constructor( private dellChassisService:DellChassisService,
     private ref:ChangeDetectorRef) {}

    ngOnInit() {} 

    closeModal(){
        this.Testchassis=false;
        this.closetestmodal.emit(this.Testchassis);
        this.enabled=false;
        this.testresult=[];
        for (var j = 0; j < this.testVal.length; j++) { 
        delete this.testVal[j]['passed']; 
        delete this.testVal[j]['message'];
        }
        document.getElementById("testchassisicon").classList.remove('switchimg');
        document.getElementById("testchassisicon").classList.add('test');
    }

    testconnection(){
    this.testresult=[];
    document.getElementById("testchassisicon").classList.add('switchimg'); 
    for (var j = 0; j < this.testVal.length; j++) { 
        if(this.testVal[j].passed!=undefined){
        delete this.testVal[j]['passed']; 
        delete this.testVal[j]['message'];
        }
    } 
     if(this.enabled)
        this.stopprocess();   
        this.enabled=true;
        this.dellChassisService.testChassisProfile(this.selectedObj.chassisObj).
						subscribe(response => {
                          this.testConnectionRefresh(response);
						},error=>{
                            this.enabled=false;
                             document.getElementById("testchassisicon").classList.remove('switchimg');
                             document.getElementById("testchassisicon").classList.add('test'); 
                            this.testresult=[];
              }); 
    }

    testConnectionRefresh(response){
        if (!this.isAbort) {
            if (response) {
               
                for (let profile of response.status) {
                    var temp = {
                        result: profile.passed,
                        message: profile.message,
                    };

                    this.testresult.push(temp);
                }

                for (var j = 0; j < this.testVal.length; j++) {

                    this.testVal[j]['passed'] = this.testresult[j].result;
                    this.testVal[j]['message'] = this.testresult[j].message;
                } 
                this.enabled = false;
             }
        }
        document.getElementById("testchassisicon").classList.remove('switchimg');
       this.isAbort=false;    
          
    }
     stopprocess(){
       this.isAbort=true;
	   let result = confirm("Are your sure you want to abort test?");
       if (result == true ) {
         for (var j = 0; j < this.testVal.length; j++) {  
         this.testVal[j]['passed'] = "Aborted";
         this.testVal[j]['message'] = "";
         }
       this.enabled=false;
       document.getElementById("testchassisicon").classList.remove('switchimg');
       document.getElementById("testchassisicon").classList.add('test');   
       }
	} 
}


