
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription, Observable } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellConnectionProfileService, NavService,HostComplianceData,DellComplianceService } from "../../services/index";

@Component({
   selector: "vsphere-hosts",
   styleUrls: ["./vsphere-hosts.component.scss"],
  templateUrl:"./vsphere-hosts.component.html"
})

export class vSpherehostsComponent  implements OnInit 
{
  private  refreshViewSub:Subscription;
  open:boolean=false; 
  vsphere:HostComplianceData;
  nonCompliantServersCount:number = 0;
  csiorONCount:number = 0;
  snmpUnconfiguredCount:number = 0;
  snmpConfiguredCount = 0;
  wbemNonCompliantCount = 0;
  wbemCompliantCount = 0;
  iDRACCompliantCount = 0;
  iDRACNonCompliantCount = 0;
  loading: boolean = true;
  errorNotification:string;
  Servercount:number=0;
  csiorcount:number =0;
  snmpcongfcount:number=0;
  snmpuncongfcount:number=0;
  wbemnoncomplcount:number=0;
  wbemcomplcount:number=0;
  iDRACcomplcount:number=0;
  iDRACnoncomplcount:number=0;
  profileData:any;
  vsphereVal="";
  selectedRowNum:number;
  details:boolean=false;
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
               private complianceService:DellComplianceService) {
     this.refreshViewSub=refreshService.refreshObservable$.subscribe(
      ()=>this.refreshView());
   }
   ngOnInit(): void {  this.refreshView();}
   private refreshView(): void {
      this.loading = true;
      this.complianceService.getHostComplianceData()  
      .subscribe( (response) => {
         this.vsphere=response;
         this.countVal(this.vsphere);
         this.loading = false;
      },
     (err) => {
      console.error(err)
      this.errorNotification=err;
      this.loading=false;
     },
      () => console.log("observable complete")
    );
    
  }
   countVal(data){
     data.hostData.forEach(element => {
        if(element.hostIP){
          this.Servercount=this.Servercount+1;
          this.nonCompliantServersCount=this.Servercount;
       }
       if(element.csiorStatus=="ON"){
           this.csiorcount++;
           this.csiorONCount=this.csiorcount;
       }
       if(element.license.licenseComplianceStatus){
          if(element.license.licenseComplianceStatus=="COMPLIANT")
          {
            this.iDRACcomplcount++;
             this.iDRACCompliantCount=this.iDRACcomplcount;
          }
          if(element.license.licenseComplianceStatus=="NONCOMPLIANT")
          {
             this.iDRACnoncomplcount++;
             this.iDRACNonCompliantCount=this.iDRACnoncomplcount;
           }
      }    
    });
   }
  openNonCompliance(){ this.open=true; }
  editRow(obj,i){ 
    if(obj){
      this.vsphereVal=obj;
      this.selectedRowNum = i;
      this.details=true;
      }
    else{
     this.details=false;
   }

  }
  wizardStatus(status:boolean){ this.open=status;}    
}


