
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellTaskschedulerService, DellConnectionProfileService, NavService } from "../../services/index";

@Component({
   selector: "hosts-warranty",
   styleUrls: ["./hosts-warranty.component.scss"],
   templateUrl:"./hosts-warranty.component.html"
})

export class HostsWarrantyComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() jobNavStatus:string;
  profiles: ConnectionProfile[];
  chassisStatus=false;
  instance: HostsWarrantyComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  inventoryData=[];  
  selected_job=[];  
 
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private _dellTaskschedulerService:DellTaskschedulerService,
               private route: ActivatedRoute) {
      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void {  
      this.nav.setViewType('summary');
      this.refreshView();
   }
   
   connectionNav(linkVal){ this.jobNavStatus=linkVal;}
   
   private refreshView(): void {
          this._dellTaskschedulerService.getHostJobs().
                  then(response =>  {
                        console.log(response);  
                  },
                  error=>{
                      console.log(error);
                  });

   }
}


