import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,APP_CONFIG, AppAlertService }   from "../../shared/index";
import { SettingRequest,VCenterSettings,DellConfigSettingService, NavService } from "../../services/index";




@Component({
   selector: "manage-settings",
   styleUrls: ["./settings.component.scss"],
  templateUrl:"./settings.component.html"
})

export class SettingsComponent  implements OnInit 
{
 openmodal=false;
 modalParam:string;
 openconfig=false;
 private refreshViewSub: Subscription;
 

 constructor(private gs: GlobalsService,
  public  i18n: I18nService,
  private location: Location,
  public  nav: NavService,
  private refreshService: RefreshService,
  private _dellConfigSettingService:DellConfigSettingService,
  private route: ActivatedRoute) {
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
      () => this.refreshView());
   }
  openModal(param){
     this.modalParam=param;
     this.openmodal=true;
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove("scrolling"); 
  }
   openWizard(){
    let body = document.getElementsByTagName('body')[0];
    body.classList.remove("scrolling");
    this.openconfig=true;
   }
   statusChange(val){
     console.log(val);
     this.openconfig=val;
   }
   ngOnInit(): void { 
    let body = document.getElementsByTagName('body')[0];
    body.classList.add("scrolling"); 
    this.refreshView(); 
  } 
   
   modalStatus(status){this.openmodal=status;  }
   
   private refreshView(): void {
        var date = new Date();
        var settingRequest:SettingRequest = new SettingRequest();
        settingRequest.timezoneOffset = date.getTimezoneOffset();
        settingRequest.settingsEnum.push("INVENTORY");
        settingRequest.settingsEnum.push("WARRANTY");
        settingRequest.settingsEnum.push("UPDATEAVAILABILITY");
        settingRequest.settingsEnum.push("EVENTS");
        settingRequest.settingsEnum.push("ALARMS"); 
        settingRequest.settingsEnum.push("OMSA_URL"); 
        
       this._dellConfigSettingService.getSetting(settingRequest).subscribe(
                  (response) => { console.log(response);  },
                  (err)     =>console.log(err),
                  ()        =>console.log("completed the config settings")
            );
   

       
   } 


  
}


