
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ProfileRequest,Profile,DellDeploymentService, NavService,DellTaskschedulerService } from "../../services/index";

@Component({
   selector: "deployment-hypervisor",
   styleUrls: ["./deployment-hypervisor.component.scss"],
   templateUrl:"./deployment-hypervisor.component.html"
  })

export class DeploymentHypervisorComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() inputParam:string;
  childNavStatus="";
  chassisStatus=false;
  instance: DeploymentHypervisorComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  hypervisor:any=[];
  tabval:string; 
  selectedRowIndex:number;
  activeRow:any;

  opened:boolean=false;
  service_name:string       = "profileService";
  HYPERVISOR_PROFILE:string = "HYPERVISOR_PROFILE";

  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
               private _dellDeploymentService : DellDeploymentService
             ) {
            this.instance = this;
            this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void{  
      this.nav.setViewType('summary');
      this.refreshView();
   }

   tagNav(param){ this.tabval=param;}   
 
   private refreshView(): void {
    let  request: ProfileRequest = new ProfileRequest();
        request.profileType = []
        request.profileType.push(this.HYPERVISOR_PROFILE);
        
        this._dellDeploymentService.getDeploymentProfiles(request)  
            .subscribe( (response) => {
                            console.log(response);
                            this.hypervisor=response
                        },
                        (err) => console.error(err),
                        () => console.log("hypervisor completed")
                    ); 
   } 

   editGridrow(){ this.opened=true;} 
   
   closemodal(event){ this.opened=event;}

    activateRow(obj,i){ 
            if(obj){
                this.activeRow=obj;
                this.selectedRowIndex = i;
        }
    }
}


