import "./rxjs-extensions";

import { NgModule, ErrorHandler }      from "@angular/core";
import {HttpClientModule} from '@angular/common/http';
import {HttpClient} from "@angular/common/http";
import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule }    from "@angular/http";
import { ClarityModule } from "clarity-angular";
import {ClarityIcons} from 'clarity-icons';

//import { AlertIconAndTypesService } from "clarity-angular/emphasis/alert/providers/icon-and-types-service";
import { AlertIconAndTypesService } from "clarity-angular/emphasis/alert/providers/icon-and-types-service";
import { AppRoutingModule, routedComponents, nonRoutedComponents } from "./app-routing.module";

//import { EchoService, HostService, NavService }  from "./services/index";
import { Globals } from "./shared/globals.service";
import { EchoService, HostService, DellHostService, DellHealthService, DellVCenterService, DellHostHardwareService, DellConnectionProfileService,
    DellDatacenterHardwareService, DellComplianceService, DellConsoleSDKService,DellLogService,    
    DellHostOverviewService, DellApplianceService,DellTaskschedulerService,DellConfigSettingService,NavService, MaxLengthPipe,FilterPipe,DellDeploymentService,EqualValidator,DellChassisService,DellChassisEMCService,DellBareMetalService,DellFirmwareUpdateService,ModalConfigService}  from "./services/index";
import { GlobalsService, I18nService,
         AppAlertService, RefreshService }   from "./shared/index";
import { ActionDevService }   from "./services/testing/action-dev.service";
import { DialogBoxComponent } from "./shared/dev/dialog-box.component";
import { DynamicDialogComponent }   from "./shared/dev/dynamic-dialog.component";
import { AppErrorHandler } from "./shared/appErrorHandler";
import { AppComponent }     from "./app.component";
import { SettingsModule } from "./views/settings/settings.module";
import { SharedModule } from "./shared/shared.module";

// [removable-chassis-code]
import { ChassisService } from "./services/chassis/chassis.service";
import { InMemoryWebApiModule, InMemoryBackendConfigArgs } from "angular-in-memory-web-api";
import { InMemoryDataService } from "./services/chassis/in-memory-data.service";
// [end-chassis-code]
import { UserSettingService } from "app/shared/user-settings.service";
//for formgroup
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import {TranslateModule, TranslateLoader} from "@ngx-translate/core";
import {TranslateHttpLoader} from '@ngx-translate/http-loader';


 


@NgModule({
   imports: [
      BrowserModule,
      BrowserAnimationsModule,
      ClarityModule.forRoot(),
      HttpModule,
      HttpClientModule,
      AppRoutingModule,
      SettingsModule,
      SharedModule,
	  FormsModule,
      ReactiveFormsModule,
	
      // [removable-chassis-code]
      // InMemoryDataService config: forward unrecognized requests + remove the default 500ms delay
      InMemoryWebApiModule.forRoot(InMemoryDataService, <InMemoryBackendConfigArgs>{
         passThruUnknownUrl: true,
         delay: 0
      })
      // [end-chassis-code]
   ],
   declarations: [
      AppComponent,
      DialogBoxComponent,
      DynamicDialogComponent,
      routedComponents,
      nonRoutedComponents,
      MaxLengthPipe,
      FilterPipe,
	  EqualValidator
   ],
   providers: [
            ActionDevService,
            AppAlertService,
            AppErrorHandler,
            ChassisService, // [removable-chassis-line]
            EchoService,
            Globals,
            GlobalsService,
            HostService,
            DellHostService,
            DellApplianceService,
            DellHealthService,
            DellHostOverviewService,
            DellHostHardwareService,
            DellDatacenterHardwareService,
            DellVCenterService,
            I18nService,
            DellConnectionProfileService,
            DellLogService,
            DellTaskschedulerService,
            NavService,
            RefreshService,
            DellConsoleSDKService,
            UserSettingService,
            DellComplianceService,
            DellDeploymentService,
            DellChassisService,
            DellChassisEMCService,
            DellBareMetalService,
            DellConfigSettingService,
            DellFirmwareUpdateService,
            ModalConfigService,
    //        AlertIconAndTypesService,
      {provide: ErrorHandler, useClass: AppErrorHandler}],
   bootstrap: [AppComponent]
})

export class AppModule {
   
}



