
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {FormGroup, FormControl, Validators} from "@angular/forms";
import { EqualValidator } from "../../../services/index";

@Component({
   selector: "deployment-hypervisorprofile",
   styleUrls: ["./deployment-hypervisorprofile.component.scss"],
  templateUrl:"./deployment-hypervisorprofile.component.html"
})

export class DeploymentHypervisorProfileComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() opened:boolean;
  @Output() closemodal=new EventEmitter();
  valChanged:boolean=false;
  elements:any;
  i:number;
  userdata:boolean=false;
  isoValidation=/^(([a-zA-Z0-9]+\:)(([/])([a-zA-Z0-9]+))?([/])([a-zA-Z0-9]+)+\.([iI][sS][oO]))$|((^\\\\)([a-zA-Z0-9]+)(([\\\\])([a-zA-Z0-9]+))?([\\\\])([a-zA-Z0-9]+)+\.([iI][sS][oO]))$/;
  userdetails=/((^\\\\)([a-zA-Z0-9]+)(([\\\\])([a-zA-Z0-9]+))?([\\\\])([a-zA-Z0-9]+)+\.([iI][sS][oO]))$/;
  profiledescription = new FormGroup({
        profileName: new FormControl('', Validators.required),
        profiledescription:new FormControl('', ),
        installationVersion: new FormControl('',[Validators.required,Validators.pattern(this.isoValidation)]),
        username:new FormControl('', Validators.required),
        password:new FormControl('', Validators.required),
        verifypassword:new FormControl('', Validators.required),
        ESXiVersion: new FormControl('', Validators.required)
  });

  constructor() {}

  ngOnInit(): void {}
   
  private refreshView(): void {}

  show(event){
   if(event!="Default")
    this.valChanged=true;
  }
  closeModal(){
    this.opened=false;
    this.valChanged=false;
    this.closemodal.emit(this.opened);
    this.profiledescription.reset();
    this.elements = document.getElementsByTagName("select")
    for(this.i=0; this.i < this.elements.length ; this.i++){
    this.elements[this.i].selectedIndex= 0;
   }  
  }
  valentered(event){
   if(this.userdetails.test(event)){
     this.userdata=true;
   }
    else{
       this.userdata=false;
    }
  
  }
}


