import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../../shared/index";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";
import { ChangeDetectorRef ,OnChanges,SimpleChanges} from '@angular/core';	
import { Template,ProfileRequest,Profile,SystemProfileDetail,TemplateRequest,DellDeploymentService, NavService,DellTaskschedulerService } from "../../../services/index";

	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "deployment-wizard-template",
   styleUrls: ["./deployment-wizard-template.component.scss"],
   templateUrl: "./deployment-wizard-template.component.html"

})

export class DeploymentWizardTemplateComponent implements OnInit {
	closeModal:boolean=false;
	@Input() openModal: boolean;
	@Input() hardwareData:any;
	@Input() hypervisorData:any;
	@Input() selectedRow:any;
	@Input() action:string;
	@Output() closeWizardTemplate = new EventEmitter<boolean>();
	private refreshViewSub: Subscription;
	enable			= 	false;
	isAbort:boolean	=	false;
	valChanged:boolean		=	false;
	profileChecked:boolean	=	false;
	HARDWARE_PROFILE:string   = "HARDWARE_PROFILE";
	HYPERVISOR_PROFILE:string = "HYPERVISOR_PROFILE";
	createTemplate:Template=new Template();
	updateTemplate:Template=new Template();

	selectedHardwareOptions=[];
	selectedHypervisorOptions=[];
	
	deploymenttemplateForm = new FormGroup({
		name: new FormControl('', Validators.required),
		profileDescription: new FormControl('', ),
		templateProfile	  : new FormControl('',),
		systemOption	  : new FormControl({value: 203},),
		hardwareOption	  : new FormControl({value:22},),	
		hypervisorProfile: new FormControl('', Validators.required),
	 });
	

	 constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private _dellDeploymentService: DellDeploymentService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
           														 () => this.refreshView()); }

	  ngOnInit() { 	this.refreshView();  
	           if(this.selectedHardwareOptions.length ==0){
					this.selectedHardwareOptions.push(0);
					this.selectedHypervisorOptions.push(0);
				}
	}
	
   
	ngOnChanges(changes: SimpleChanges){
			if(this.action=="edit"){ 
				  
				
			//	alert(JSON.stringify(this.selectedRow.attachedProfiles)); 
			//	console.log(JSON.stringify(this.selectedRow));
				this.updateTemplate.ID	=(this.selectedRow.id)?this.selectedRow.id:0;
				this.updateTemplate.name=(this.selectedRow.name)? this.selectedRow.name:"";
				this.updateTemplate.description=(this.selectedRow.description)? this.selectedRow.description:"";
				this.updateTemplate.createdDate=(this.selectedRow.createdDate)? this.selectedRow.createdDate:"";
				this.updateTemplate.vCenterAddress =(this.selectedRow.vCenterAddress)? this.selectedRow.vCenterAddress:"";
				this.updateTemplate.attachedProfiles=(this.selectedRow.attachedProfiles)?this.selectedRow.attachedProfiles:[];
				this.updateTemplate.attachedSystemProfileDetails=(this.selectedRow.attachedProfiles !=undefined)?this.selectedRow.attachedProfiles:[];
                if(this.selectedRow.attachedProfiles !=undefined){
					for(let profile in this.selectedRow.attachedProfiles){
						  // alert(profile);

						   let hardwareTemp=(this.hardwareData[profile] !=undefined)?this.hardwareData[profile].profileID:0;
						   let hypervisorTemp=(this.hypervisorData[profile] !=undefined)?this.hypervisorData[profile].profileID:0;
						   if(hardwareTemp == this.selectedRow.attachedProfiles[profile].profileID){
							   this.selectedHardwareOptions.push(hardwareTemp);
							}else{
								if(this.selectedHardwareOptions.length ==0){
									this.selectedHardwareOptions.push(0);
								}
							}
                           if(hypervisorTemp == this.selectedRow.attachedProfiles[profile].profileID){
								this.selectedHypervisorOptions.push(hypervisorTemp);
							}else{
								if(this.selectedHardwareOptions.length ==0){
									this.selectedHypervisorOptions.push(0);
								}
								
							}
						  
						
					}
				}
				
			}

	}			
    onCancel(){ 
	this.openModal=false;  this.closeWizardTemplate.emit(this.openModal);
	this.selectedHardwareOptions=[];
	this.selectedHypervisorOptions=[];
	
	}
    		
	private refreshView(): void { }
	
	show(event){
		if(event!="Default")
		 this.valChanged=true;
	   }
	 
	   enableSelection(event){
			 if((<HTMLInputElement>document.getElementById("checkrads_9")).checked ||(<HTMLInputElement>document.getElementById("checkrads_10")).checked   ){
			   this.profileChecked=true;
			 }
			 if(event=="checkrads_9"){
			   document.getElementById('sel').removeAttribute('disabled');
			   document.getElementById('sel1').setAttribute('disabled','disabled');
			 }else{
				 document.getElementById('sel1').removeAttribute('disabled');
				 document.getElementById('sel').setAttribute('disabled','disabled');
			 }
	   }

	saveForm(){

		
		let tempArray:any=[];
		let systemArray:any=[];
		if(this.deploymenttemplateForm.get('templateProfile').value !=null){
			 if(this.deploymenttemplateForm.get('templateProfile').value=='systemprofile'){
				let  request: SystemProfileDetail = new SystemProfileDetail();
				console.log(this.deploymenttemplateForm.get('systemOption').value);
				request.spId=this.deploymenttemplateForm.get('systemOption').value;	
				systemArray.push(request);	
			 }
		}
		if(this.deploymenttemplateForm.get('templateProfile').value != null){
			if(this.deploymenttemplateForm.get('templateProfile').value=='hardwareprofile'){
				let profileID=this.deploymenttemplateForm.get('hardwareOption').value;
				if(this.hardwareData){
					this.hardwareData.forEach(function (data) {
						//console.log(data);
						//console.log(profileID+'live data formats'+data.profileID);
							 if(data.profileID==profileID){
								 tempArray.push(data)
							 }
						});
				}	
			}
		}
		if(this.deploymenttemplateForm.get('hypervisorProfile').value !=null){
		   let profileID=this.deploymenttemplateForm.get('hypervisorProfile').value;
		    if(this.hypervisorData){
				this.hypervisorData.forEach(function (data) {
					//console.log(data);
					//console.log(profileID+'live data formats'+data.profileID);
				     	if(data.profileID==profileID){
							 tempArray.push(data)
						 }
					});
			}  
		   
	//	   tempArray.push(request);
		}
		
		if(this.action=="edit"){
			this.createTemplate.ID=(this.selectedRow.id)?this.selectedRow.id:0;	
			this.createTemplate.createdDate=(this.selectedRow.createdDate)? this.selectedRow.createdDate:"";
		}
		this.createTemplate.name=(this.deploymenttemplateForm.get('name').value)?this.deploymenttemplateForm.get('name').value:'';
		this.createTemplate.description=(this.deploymenttemplateForm.get('profileDescription').value)?this.deploymenttemplateForm.get('profileDescription').value:'';
		this.createTemplate.attachedProfiles=tempArray;
		this.createTemplate.attachedSystemProfileDetails=systemArray;
		 
	 console.log(this.createTemplate);
    
	//    if(this.createTemplate){
	// 				this._dellDeploymentService.getCreateTemplateProfile(this.createTemplate)
	// 				.subscribe( (response) => {
	// 						console.log(response);
	// 						if(response){
	// 							this.closeWizard();
	// 						}
	// 						},
	// 						(err) => console.error(err),
	// 						() => console.log("observable complete")
	// 					);
	//    }
	}

	closeWizard(){

		this.deploymenttemplateForm.reset();
		this.closeWizardTemplate.emit(false)
   }
	
		
	
   
}


