import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellHost, DellHostService, NavService } from "../../services/index";
         
@Component({
   selector: 'chassis-manage',
   styleUrls: ['./chassis-manage.component.scss'],
   templateUrl: './chassis-manage.component.html'
   })
  
export class ChassisManageComponent implements OnInit,OnDestroy{
    private refreshViewSub: Subscription;
    hostId:string;
    hostObject:any=[];
	
    constructor(private gs: GlobalsService,
        private appAlertService: AppAlertService,
        private hostService: DellHostService,
        public  i18n: I18nService,
        private location: Location,
        public  nav: NavService,
        private refreshService: RefreshService,
        private route: ActivatedRoute) {
            // Subscribe to refreshService to handle the global refresh action
            this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                () => this.refreshView());
        }


    ngOnInit(): void { 
        this.route.params.forEach((params: Params) => {
            this.hostId=params["id"];
            console.log(this.hostId);
            
         });
        
        this.refreshView(); }

    ngOnDestroy(): void {
        this.refreshViewSub.unsubscribe();
     }
  
    private refreshView(): void {
        if(this.hostId){
            this.hostService.getHostChassis(this.hostId)   
            .then(response => {
                     //console.log("chassis manage");
                    //this.hostObject=response;   
                     this.getFormatResponse(response);
                   //  console.log(JSON.stringify(response));
                  });
        }
        
    }

    getFormatResponse(response){
        let temp={}
        for(let res of response){
           if(res.propertyName=='hostName'){
              temp["hostName"]=res.value;
           }
            if(res.propertyName=='serviceTag'){
                temp["serviceTag"]=res.value;
            }   
            if(res.propertyName=='model'){
                temp["model"]=res.value;     
            }
            if(res.propertyName=='idracIp'){
                temp["idracIp"]=res.value;     
            }
            if(res.propertyName=='slotLocation'){
                temp["slotLocation"]=res.value;     
            }
            if(res.propertyName=='lastInventory'){
                temp["lastInventory"]=res.value;     
            }
        }
        this.hostObject.push(temp);
        //console.log(temp);
    }

}



