import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import {Observable } from "rxjs/Observable"; 


import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { UpdateSettings} from "./index";

/**
 * This HostServiceBase interface is optional for HostService but it helps
 * define a simple FakeHostService for testing purposes
 */
export interface DellFirmwareUpdateServiceBase {
   //getDellAppliances(): Promise<DellApplianceInfo[]>;
   //getBuildPhaEvent():Promise<PHAEventsUI[]>;
}


@Injectable()
export class DellFirmwareUpdateService implements DellFirmwareUpdateServiceBase {
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService) {
   }

  
  
   getUpdateSettingsUrl (): string {
      let url: string;
      url = this.gs.getWebContextPath() + "/rest/services/getUpdateSettings";
      return url;
   };

   createFirmwareUpdateUrl():string{
      let url:string;
      url   = this.gs.getWebContextPath() + "/rest/services/createFirmware";
      return url;
   }

   getUpdateSettings(): Observable<UpdateSettings> {
      let headers = this.gs.getHttpHeaders();
      return this.http.get(this.getUpdateSettingsUrl(), headers)
            .map(response => (response.json() as UpdateSettings))
            .catch(error => this.errorHandler.httpPromiseError(error));
  }


  createFirmwareUpdate(){

    let books:UpdateSettings[]= [
        { updateMethod  : 'test',
            sharePath     : 'https',
            shareUser     : 'sasdsad',
            sharePassword : 'sdsdsadsa',
            shareDomain   : 'string',
            url           : 'string',
            protocol      : 'string', }
      ];


      let headers = this.gs.getHttpHeaders();
      return this.http.post(this.createFirmwareUpdateUrl(),books,headers)
            .map(response => (response.json() as UpdateSettings))
            .catch(error => this.errorHandler.httpPromiseError(error));
  }

  


   

}



