import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from "@angular/router";
import { GlobalsService, RefreshService, I18nService }   from "../../shared/index";
import { NavService } from "../../services/index";
import { MonitorHardwareComponent } from "../hardware/monitor-hardware.component";
import { SelLog, DellServerFirmware, DellHostHardwareService} from "../../services/index";
import{Observable}from "rxjs";

 const hardwareChild = [
  { "hardwareName": "FRUs"},
  { "hardwareName": "Processors"},
  { "hardwareName": "Power Supplies"},
  { "hardwareName": "Memory"},
  { "hardwareName": "NICs"},
  { "hardwareName": "PCI Slots"},
  { "hardwareName": "Remote Access Card"}
];

@Component({
   selector: 'monitor',
   styleUrls: ['./monitor.component.scss'],
   templateUrl: './monitor.component.html'
   })
  
export class MonitorComponent implements OnInit {
  hostId: string;
  hardwareObject = hardwareChild;
  status=false;
  loader=false;
  hardwareStatus=true;
  hardwareParam="FRUs";
  firmware: DellServerFirmware;
  selLog: SelLog;
  error:any;
  
  constructor(public gs: GlobalsService,
               public  i18n: I18nService,
               private dellHardwareService: DellHostHardwareService,
               public  nav: NavService,
               private route: ActivatedRoute){  }
  
  
        private getFirmware(): void {
            this.dellHardwareService.getDellHostFirmware(this.hostId)
                    .then(firmware => 
                    {
                    this.firmware = firmware;
                    }
                    ,error=>{
                        this.error=error;
                        }            
                    );
        }

       private getSelLog(): void {
                this.dellHardwareService.getDellHostSelLog(this.hostId)
                        .then(selLog => 
                        {
                        this.selLog = selLog;
                        }
                        ,error=>{
                            this.error=error;
                            
                            }            
                        );
        }
            
        hardware(val){
                this.hardwareParam = val;
                this.hardwareStatus = true;
                this.loader = true;
                this.status = false;
                if(this.hardwareStatus){
                    this.loader = false;
                }
                if(this.hardwareParam === "Firmware"){
                    this.getFirmware();
                }
                else if(this.hardwareParam === "System Event Log"){
                    this.getSelLog();
                }
         } 

            ngOnInit() {
                this.route.params.forEach((params: Params) => {
                    this.hostId = params["id"];
                });
                this.nav.setViewType('monitor');
            }
}


