import {VCenter} from  "../index";

export class SettingRequest 
{
    timezoneOffset:number;
    settingsEnum  :string[];
    
    constructor(clone: SettingRequest = null) {
        this.timezoneOffset   = clone ? clone.timezoneOffset : 0;
        this.settingsEnum     = clone ? clone.settingsEnum:[];
        
      
   }
}
export class VCenterSettings
{

     inventoryJobSame           :boolean;
     warrantyJobSame            :boolean;
	 updateAvailabilityJobSame  : boolean;
	 eventsAlarmSame            : boolean;
     omsaUrlSame                : boolean;
     vCenters                   :VCenter[];  
     
         
    constructor(clone: VCenterSettings = null) 
    {
      this.inventoryJobSame             = clone ? clone.inventoryJobSame: true;
      this.warrantyJobSame              = clone ? clone.warrantyJobSame : true;
      this.updateAvailabilityJobSame    = clone ? clone.updateAvailabilityJobSame : true;
      this.eventsAlarmSame              = clone ? clone.eventsAlarmSame : true;
      this.omsaUrlSame                  = clone ? clone.omsaUrlSame : true;
      this.vCenters                     = clone ? clone.vCenters :[];
    }
}






