import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {FormGroup, FormControl, Validators} from "@angular/forms";

@Component({
   selector: "deployment-baremetalserver",
   styleUrls: ["./deployment-baremetalserver.component.scss"],
  templateUrl:"./deployment-baremetalserver.component.html"
})

export class DeploymentBareMetalServerComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() opened:boolean;
  @Output() closemodal=new EventEmitter();
   ipValidation=/^([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})[.]([0-9]{1,3})$/;
   nameValidation=/^[^""///\@~`.]*$/;
   passwordValidation=/^[^~^\s]*$/;
   baremetalform = new FormGroup({
        ip: new FormControl('', [ Validators.required,Validators.pattern(this.ipValidation)]),
        username: new FormControl('',[Validators.required,Validators.pattern(this.nameValidation),Validators.minLength(1),Validators.maxLength(16)]),
        password: new FormControl('',[Validators.required,Validators.pattern(this.passwordValidation),Validators.minLength(1),Validators.maxLength(20)])
    });
   

  constructor() {}

  ngOnInit(): void {}
   
  private refreshView(): void {}

  closeModal(){
    this.opened=false;
    this.closemodal.emit(this.opened);
   }

}


