import { Directive,Component, Input,Output, OnInit,SimpleChanges,ChangeDetectorRef, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Wizard } from "clarity-angular";
import { VCenterRootNode,ConnectionProfile, ConsoleEntity,DellVCenterService,DellConnectionProfileService} from "../../services/index";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {FormGroup, FormControl, Validators,ReactiveFormsModule} from "@angular/forms";		 
	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "firmware-wizard-selectServer",
   styleUrls: ["./firmware-wizard-selectServer.component.scss"],
   templateUrl: "./firmware-wizard-selectServer.component.html"

})

export class FirmwareWizardSelectServer implements OnInit {
 
  selectedComponents:any=[];
  //vcenterNodes:VCenterRootNode[];
  //profiles:ConnectionProfile[];
  selection:any[]=[];

  vcenterNodes=[];
  profiles=[];
  hostProfiles:ConnectionProfile[];
  hostVcenterNodes:VCenterRootNode[]; 
  @ViewChild("wizard") wizard: Wizard;
  @Output() selectHostServer = new EventEmitter();
  warningNotification:boolean=false;
  

  constructor(  private dellVCenterService: DellVCenterService,
                private dellConnectionProfileService: DellConnectionProfileService,
                private ref:ChangeDetectorRef  
              
              ) {
                this.getConnectionProfile();
              }
  ngOnInit() { 
    this.getConnectionProfile();
    this.getAssociatedHost();
    this.refreshView();
  
  }

  
  ngOnChanges(changes: SimpleChanges){
    console.log("trigger the host  changes");
    this.getConnectionProfile();
    this.getAssociatedHost();

   //  alert("sadsdsadasd");
     if(this.selection){
     //  alert("this is selection block");
     }
    this.selectHostServer.emit(this.selection);
    this.ref.detectChanges();

}

  public refreshView(): void { 
    console.log(this.vcenterNodes);
    console.log(this.profiles);
    
  }
   getAssociatedHost(){
           this.dellVCenterService.getVCenterTree([], "true")
              .then(vcenterNodes => {
                 if (vcenterNodes.length === 0) {
                     this.ref.detectChanges(); 
                    return;
                 }
                 this.vcenterNodes = vcenterNodes;
                 console.log(this.vcenterNodes);
                 this.ref.detectChanges();
           });
          
   }

   

    getConnectionProfile(){
      this.dellConnectionProfileService.getConnectionProfiles()
            .subscribe(profiles => {
              this.profiles = profiles;
              this.ref.detectChanges(); 
              
              console.log(this.profiles);
            },error=>{
                  console.log(error);
                 
            });
  }
       
   getProfileName(host){
   //   alert("this is the getting the profile name");
           if(this.profiles){
              for(let profileName of this.profiles){
                for(let profile of profileName.console.consoleEntity){
                    if(profile.name===host){
                        return profileName.profileName;
                    }
                
                }  
              }
           }
        }

}


