
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";


@Component({
   selector: "monitor-compliance",
   styleUrls: ["./monitor-compliance.component.scss"],
  templateUrl:"./monitor-compliance.component.html"
})

export class MonitorComplianceComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() monitorParam:string;
  jobNavStatus="Deployment Jobs";
  tabval:string="compliance";
  constructor() {}
  ngOnInit(): void {}
  connectionNav(linkVal){ this.jobNavStatus=linkVal;}
  tagNav(param){ this.tabval=param;}   
   private refreshView(): void {}
}


