// Taskscheduler
export class Taskscheduler 
{
    serverJob: ServerJob[];
    chassisInventoryJobItem:ChassisInventoryJobItem[];
    chassisWarrantyJobItem :ChassisWarrantyJobItem[];
    hostJob  : HostJob[];
    vCenter  : VCenter[];
  
    constructor(clone: Taskscheduler = null) {
      this.serverJob        = clone ? clone.serverJob : [];
      this.chassisInventoryJobItem = clone ? clone.chassisInventoryJobItem:[];
      this.chassisWarrantyJobItem  = clone ? clone.chassisWarrantyJobItem : [];
      this.hostJob          = clone ? clone.hostJob: [];
      this.vCenter          = clone ? clone.vCenter:[];
   }
}
export class Job
{
    name                    : string;
    consoleNetworkAddress   : string;
    jobTarget               : JobTarget[];    
    type                    : string;
    schedule                : string;
    id                      : number;
    description             : string;
    disabled                : boolean;
    nextRun                 : string;
    lastRun                 : string;
    owner                   : string;
    consoleId               : number;
    constructor(clone: Job = null) 
    {
      this.name                     = clone ? clone.name: "";
      this.consoleNetworkAddress    = clone ? clone.consoleNetworkAddress : "";
      this.jobTarget                = clone ? clone.jobTarget : null;
      this.type                     = clone ? clone.type : "";
      this.schedule                 = clone ? clone.schedule : "";
      this.id                       = clone ? clone.id       : 0;
      this.description              = clone ? clone.description: "";
      this.disabled                 = clone ? clone.disabled :false;
      this.nextRun                  = clone ? clone.nextRun : "";
      this.lastRun                  = clone ? clone.lastRun : "";
      this.owner                    = clone ? clone.owner : "";
      this.consoleId                = clone ? clone.consoleId : 0;
    }
}
export class ChassisInventoryJobDetails
{
    lastRun : string;
    nextRun : string;
    disabled: boolean;
	chassisJobItems:ChassisInventoryJobItem[];

    constructor(clone:ChassisInventoryJobDetails=null)
    {
        this.lastRun = clone ? clone.lastRun : "";
        this.nextRun = clone ? clone.nextRun : "";
        this.disabled= clone ? clone.disabled: false;
        this.chassisJobItems = clone ? clone.chassisJobItems : [];
    }
}

export class ChassisWarrantyJobDetails
{
    lastRun : string;
    nextRun : string;
    disabled: boolean;
	chassisJobItems:ChassisWarrantyJobItem[];

    constructor(clone:ChassisWarrantyJobDetails=null)
    {
        this.lastRun = clone ? clone.lastRun : "";
        this.nextRun = clone ? clone.nextRun : "";
        this.disabled= clone ? clone.disabled: false;
        this.chassisJobItems = clone ? clone.chassisJobItems : [];
    }

}

export  class JobTarget
{
    jobTargetId : number;
    type        : string;
    name        : string;
    entityId    : string;    

    constructor(clone : JobTarget =null)
    {
        this.jobTargetId    = clone ? clone.jobTargetId: 0;
        this.type           = clone ? clone.type: "";
        this.name           = clone ? clone.name: "";
        this.entityId       = clone ? clone.entityId: "";
    }
}

export class ServerJob
{
    id              :number;
    progress        : string;
    status          : string;
    startTime       : string;
    endTime         :string;
    message         : string;
    lastUpdatedTime : string;
    executedBy      : string;
    collectionSize  : number;
    name            : string;
    description     : string;
    vCenterAddress  : string;
    job             : Job[];

    constructor(clone: ServerJob=null)
    {
        this.id         = clone ? clone.id: 0;
        this.progress   = clone ? clone.progress:"";
        this.status     = clone ? clone.status : "";
        this.startTime  = clone ? clone.startTime: "";
        this.endTime    = clone ? clone.endTime : "";
        this.message    = clone ? clone.message : "";
        this.lastUpdatedTime= clone ? clone.lastUpdatedTime: "";
        this.executedBy     = clone ? clone.executedBy : "";
        this.collectionSize = clone ? clone.collectionSize:0;
        this.name           = clone ? clone.name:"";
        this.description    = clone ? clone.description : "";
        this.vCenterAddress = clone ? clone.vCenterAddress : "";
        this.job            = clone ? clone.job:null;         
    }

}


export class  VCenterDetail
{
    disabled :boolean;
	lastRun  : string;
	nextRun  : string;	
	jobHistory:HostJob;
	vCenterAddress:string;
	failed   : number;
	passed   : number;
	
 
   constructor(clone:VCenterDetail=null)
   {
        this.disabled           = clone ? clone.disabled:false;
        this.lastRun            = clone ? clone.lastRun :"";
        this.nextRun            = clone ? clone.nextRun : "";
        this.jobHistory         = clone ? clone.jobHistory:null;
        this.failed             = clone ? clone.failed : 0;
        this.passed             = clone ? clone.passed : 0;
   }
}
export  class ServerJobDetail {

	  id        :  number;
	  progress  :  string;
	  status    :  string;
	  startTime :  string;
	  endTime   :  string;
	  executionSummary : string;
	  executionDetail  : string;
	  host             : string;
	  name             : string;
	  ip               : string;
      constructor(clone:ServerJobDetail=null)
      {
          this.id          = clone ? clone.id:0;
          this.progress    = clone ? clone.progress:"";
          this.endTime     = clone ? clone.endTime : "";
          this.startTime   = clone ? clone.startTime :"" ;
          this.status      = clone ? clone.status : "";
          this.executionDetail = clone ? clone.executionDetail:"";
          this.executionSummary= clone ? clone.executionSummary:"";
          this.host        = clone ? clone.host : "";
          this.name        = clone ? clone.name : "";
          this.ip          = clone ? clone.ip   : "";

      }
}
