import { Output,EventEmitter,Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {DellServerFirmware, SelLog, ServerHardware, DellHostGroupObject,DellHostGroupListObject, DellHostHardwareService,DellDatacenterHardwareService,
        NavService, Controller, Connector, Enclosure, PhysicalDisk, VirtualDisk} from "../../services/index";
import{Observable}from "rxjs";
         
 
const units = ['KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

@Component({
   selector: 'monitor-overview',
   styleUrls: ['./monitor-overview.component.scss'],
   templateUrl: './monitor-overview.component.html'
   })

 export class ClusterOverviewComponent implements OnInit {
    private refreshViewSub: Subscription;
    hostId: string;
    hardware:any=[];
    @Input() status:string;
    @Input() hardwareObj;
    hardwareStatus=true;
    hostGroupData:DellHostGroupListObject;
    dataCollection:DellHostGroupObject;
    errorMessage:any;
    serviceRole:string[];
    displayMode:string;
    loading: boolean = true;
    activeRow:any;
    selectedRowIndex:number;
    details:boolean=false;
    overview:any;
    TEC_reading:number=0;
    TEC_unit:string='KB';
    TotalEnergyConsumption=[];
    memory:number=0;
    TotalMemory:number=0;
    arr=[];
    _reading:number=0;
    _unit:string='';
    virtual_reading:number=0;
    energyconsumption_reading:number=0;
    totalActive:number=0;
    totalWarranties:number=0;
    searchableList = ['hostName','serviceTag','modelName','assetTag','cmcServiceTag','operatingSystemVersion','bladeSlot'];
        
    constructor(
                private gs: GlobalsService,
               public  i18n: I18nService,
               private dellHardwareService: DellHostHardwareService,
               private dellHostGroupService:DellDatacenterHardwareService,
               public  nav: NavService,
               private route: ActivatedRoute){}
                     

   ngOnInit(): void { 
       this.route.params.forEach((params: Params) => {
            this.hostId = params["id"];
       });
        this.nav.setViewType('monitor');
        this.refreshView();
        this.status="Overview";
        this.serviceRole = this.hostId.split(':');
        if(this.serviceRole[2]=="ClusterComputeResource"){
                this.displayMode="ClusterComputeResource";
        }else{
                this.displayMode="Datacenter";
        }
   }

    ngOnDestroy(): void {}

    private refreshView() {
            this.dellHostGroupService.getDellDatacenterHardware(this.hostId)
                .then(hostlist=>{
                    this.hostGroupData = hostlist;
                    if(this.hostGroupData != undefined){
                        this.getHostLists(this.hostGroupData);
                    }
                },
                error=>{
                    console.log(error);
                });
 }

    getHostLists(obj){
    
        this.dellHostGroupService.getDellDatacenterHostGroup(this.hostId,obj.hostlist.dellHostList)
        .subscribe(hostgroupData=>{
           this.dataCollection=hostgroupData; 
               this.computeTotalMemory(this.dataCollection.dellHostOverview);
               this.computeTotalEnergyConsumption(this.dataCollection.dellHostPowerMonitoring)
               this.computeTotalVirtualDisk(this.dataCollection.dellHostVirtualDisk)
               this.totalActive= this.dataCollection.totalHealthy + this.dataCollection.totalWarning + this.dataCollection.totalCritical;
               this.totalWarranties = this.totalActive + this.dataCollection.totalExpired + this.dataCollection.totalUnknown;
        },
        error=>{
            console.log(error);
        });
    }
    computeTotalMemory(overviewCollection){
       if ( overviewCollection != null  ) {
              for(var i=0 ;i<overviewCollection.length;i++){
                       this.ComputeData(overviewCollection[i].installedMemory,"memory");
              }
       }
     }
      computeTotalEnergyConsumption(monitorCollection){
       if ( monitorCollection != null  ) {
              for(var i=0 ;i<monitorCollection.length;i++){
                  
                       this.computeEnergy(monitorCollection[i].energyConsumption);
              }
       }
     }
     computeEnergy(data){
         if ( data != null && data.length > 2) {
                this.arr = data.split(' ');
                 if ( this.arr.length == 2) {
                    this._reading = Number(this.arr[0]);
                      this._unit = this.arr[1];
                }
                this.energyconsumption_reading+=this._reading;
         }
     }
     computeTotalVirtualDisk(diskCollection){
       if ( diskCollection != null  ) {
              for(var i=0 ;i<diskCollection.length;i++){
                       this.ComputeData(diskCollection[i].capacity,"virtual");
              }
       }
     }
    ComputeData(data:String,type:String)
    {  
           if ( data != null && data.length > 2) {
                this.arr = data.split(' ');
                 if ( this.arr.length == 2) {
                    this._reading = Number(this.arr[0]);
                    if (isNaN(this._reading)){
                        this._reading = 0;
                    }
                   this. _unit = this.arr[1];
                   if(this._unit!=='KB'){
                       if(this._unit=='MB')
                       this._reading=this._reading*1024;
                       else if(this._unit=='GB')
                        this._reading=this._reading*1024*1024;
                         else if(this._unit=='TB')
                        this._reading=this._reading*1024*1024*1024;
                   }
                     if (this._unit == null) {
                       this. _unit = '';
                    }
                }
                      if(type=="memory")
                      this.TEC_reading+=this._reading;
                      else if(type=="virtual")
                      this.virtual_reading+=this._reading;
                }
        
    }
     
   computeVal(x){
    let l = 0, n = parseInt(x, 10) || 0;
    while(n >= 1024){
        n = n/1024;
        l++;
    }
   return(n.toFixed(n %1==0 ? 0 : 2) + ' ' + units[l]);
  }
 
  activateRow(obj,i){ 
         if(obj){
                this.activeRow=obj;
                this.selectedRowIndex = i;
        }
    }
}



