import { Directive,Component, Input,Output, OnInit, ViewChild,EventEmitter,ChangeDetectionStrategy} from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";

	
@Component({
   changeDetection: ChangeDetectionStrategy.OnPush,
   selector: "firmware-wizard-firmwareUpdate",
   styleUrls: ["./firmware-wizard-firmwareUpdate.component.scss"],
   templateUrl: "./firmware-wizard-firmwareUpdate.component.html"

})

export class FirmwareWizardUpdateComponent implements OnInit {
   currDate:any;
   now:any;
   formatedDate:any;
   currentDate:any=new Date().getTime();

   
   currTime:any;
    model = {
        name: ""
    }; 
	validjobName:boolean=false;
	validcheckboxes:boolean=false;
	ischecked_method:boolean=false;

	@Output() firmwareUpdatedComplete = new EventEmitter();
	@Output() firmwareJobName = new EventEmitter();
	@Output() firmwareCheckboxes = new EventEmitter();
	@Input() Checkboxes:boolean;
	@Input() jobName:boolean;
     
		 constructor() {}

     ngOnInit() {

       this.now = new Date();
       this.formatedDate = this.now.getFullYear() + '-' + (this.now.getMonth() + 1) + '-' + this.now.getDate();​
       this.currTime=new Date(new Date().getTime()).toLocaleTimeString(); 
       this.currTime = this.currTime.substring(0, this.currTime.length - 2);
	   this.currDate = this.formatedDate;
	   
    }

    private refreshView(): void {}
	  
    enableCheckbox(elementId){
        if ((<HTMLInputElement> document.getElementById(elementId)).checked == true && elementId === 'checkrads_3'){
			   (<HTMLInputElement> document.getElementById('checkbox_1')).checked = true;
			   (<HTMLInputElement> document.getElementById('checkbox_2')).checked = true;
			   (<HTMLInputElement> document.getElementById('checkbox_3')).checked = false;
			   (<HTMLInputElement> document.getElementById('checkbox_4')).checked = false;
			   
			   document.getElementById("checkbox1_enb").classList.remove('disabled');
			 	 document.getElementById("checkbox2_enb").classList.remove('disabled');
				 document.getElementById("checkbox3_enb").classList.add('disabled');
			   document.getElementById("checkbox4_enb").classList.add('disabled');
				 document.getElementById("DateTimeRow").classList.add('disableRow');

            }
		   	else if ((<HTMLInputElement> document.getElementById(elementId)).checked == true && elementId === 'checkrads_4'){
			   (<HTMLInputElement> document.getElementById('checkbox_3')).checked = true;
			   (<HTMLInputElement> document.getElementById('checkbox_4')).checked = true;
         (<HTMLInputElement> document.getElementById('checkbox_1')).checked = false;
			   (<HTMLInputElement> document.getElementById('checkbox_2')).checked = false;
			    document.getElementById("DateTimeRow").classList.remove('disableRow');
			    document.getElementById("checkbox3_enb").classList.remove('disabled');
			 	  document.getElementById("checkbox4_enb").classList.remove('disabled');
				  document.getElementById("checkbox1_enb").classList.add('disabled');
			    document.getElementById("checkbox2_enb").classList.add('disabled');
         }
		  	else{
			   document.getElementById("DateTimeRow").classList.add('disableRow');
			   document.getElementById("checkbox1_enb").classList.add('disabled');
			   document.getElementById("checkbox2_enb").classList.add('disabled');
			   document.getElementById("checkbox3_enb").classList.add('disabled');
			   document.getElementById("checkbox4_enb").classList.add('disabled');
			   (<HTMLInputElement> document.getElementById('checkbox_1')).checked = false;
			   (<HTMLInputElement> document.getElementById('checkbox_2')).checked = false;
			   (<HTMLInputElement> document.getElementById('checkbox_3')).checked = false;
			   (<HTMLInputElement> document.getElementById('checkbox_4')).checked = false;
		   	}
    }
		
    formStatusChange(){
      this.ischecked_method = (<HTMLInputElement> document.getElementById('checkrads_3')).checked || (<HTMLInputElement> document.getElementById('checkrads_4')).checked
          || (<HTMLInputElement> document.getElementById('checkrads_5')).checked || (<HTMLInputElement> document.getElementById('checkrads_6')).checked 
	       if(this.ischecked_method && this.model.name){
			    	this.firmwareUpdatedComplete.emit(true);
		    	}
		     else{
			   	if(this.model.name.length==0){
			      this.validjobName=true;
			   }
		      else  this.validjobName=false;
		         if(! this.ischecked_method)	 
		          	this.validcheckboxes=true;
		        else
		          this.validcheckboxes=false;
			        this.firmwareJobName.emit(this.validjobName);
		        	this.firmwareCheckboxes.emit(this.validcheckboxes);
		      }
		}
		
     ngAfterViewInit() {
       // this.formStatusChange()
	}
	
	private dateChanged(newDate) {
		this.currentDate.EndDate= new Date(newDate);
		console.log(this.currentDate.EndDate); // <-- for testing
	  }
}


