import { Component, OnInit, OnDestroy, ViewChild, ViewChildren } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ServerHealth, DellHealthService, NavService } from "../../services/index";


@Component({
   selector: 'summary-health',
   styleUrls: ['./summary-health.component.scss'],
   templateUrl: './summary-health.component.html'
})
  

export class SummaryHealthComponent implements OnInit, OnDestroy {
   private refreshViewSub: Subscription;
   hostId: string;
   healthObject: ServerHealth;
   leftSide;
   rightSide;
   error:any;
   titleKey: string;
   instance: SummaryHealthComponent;

   constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellHealthService: DellHealthService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {

      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void {
      this.route.params.forEach((params: Params) => {
         this.hostId = params["id"];
      });
      this.nav.setViewType('summary');
      this.refreshView();
   }

   ngOnDestroy(): void { this.refreshViewSub.unsubscribe();}
   
   getStatusIcon(health:number): string 
   { 
    	var ret: string;
		switch(health) 
		{ 
	   		case 1:
	   		case 5:
	   		{
	   			ret = 'success-icon-status';
	      		break; 
	   		} 
	   		case 2:
	   		case 10:
	   		case 15:  
	   		{ 
	      		ret = 'warning-icon-status';
	      		break; 
	   		} 
	   		case 3:
	   		case 20:
	   		case 25: 
	   		case 30:  
	   		{ 
	      		ret = 'error-icon-status';
	      		break; 
	   		} 
	   		default: 
	   		{ 
	      		ret = 'unknown-icon-status';
	      		break; 
	   		} 
		} 
		
   		return ret;		
		
   }
   
   getStatusIconLarge(health:number): string 
   { 
    	var ret: string;
		switch(health) 
		{ 
	   		case 1:
	   		case 5:
	   		{
	   			ret = 'success-icon24-status';
	      		break; 
	   		} 
	   		case 2:
	   		case 10:
	   		case 15:  
	   		{ 
	      		ret = 'warning-icon24-status';
	      		break; 
	   		} 
	   		case 3:
	   		case 20:
	   		case 25: 
	   		case 30:  
	   		{ 
	      		ret = 'error-icon24-status';
	      		break; 
	   		} 
	   		default: 
	   		{ 
	      		ret = 'unknown-icon24-status';
	      		break; 
	   		} 
		} 
		
   		return ret;		
		
   }
   
   getStatus(health:number): string 
   { 
		var ret: string;
		switch(health) 
		{ 
	   		case 1:
	   		case 5:
	   		{
	   			ret = 'Healthy';
	      		break; 
	   		} 
	   		case 2:
	   		case 10:
	   		case 15:  
	   		{ 
	      		ret = 'Warning';
	      		break; 
	   		} 
	   		case 3:
	   		case 20:
	   		case 25: 
	   		case 30:  
	   		{ 
	      		ret = 'Error';
	      		break; 
	   		} 
	   		default: 
	   		{ 
	      		ret = 'Unknown';
	      		break; 
	   		} 
		} 
		
   		return ret;		
		
   }
   
   
   getStatusColor(health:number): string 
   { 
		var ret: string;
		switch(health) 
		{ 
	   		case 1:
	   		case 5:
	   		{
	   			ret = 'text-success';
	      		break; 
	   		} 
	   		case 2:
	   		case 10:
	   		case 15:  
	   		{ 
	      		ret = 'text-warning';
	      		break; 
	   		} 
	   		case 3:
	   		case 20:
	   		case 25: 
	   		case 30:  
	   		{ 
	      		ret = 'text-danger';
	      		break; 
	   		} 
	   		default: 
	   		{ 
	      		ret = 'text-info';
	      		break; 
	   		} 
		} 
		
   		return ret;		
		
   }
 
   private refreshView(): void {
      this.dellHealthService.getDellHealth(this.hostId)
            .then(healthObject => 
            {
               this.healthObject = healthObject;
               let components = healthObject.components;  
               if(components)
               {
               		let length = components.length;
               		let half = Math.ceil(length / 2);    
			   		this.leftSide = components.slice(0, half);
               		this.rightSide = components.slice(half);
               }
            }
			,error=>{
                  this.error=error;
                 } 
			);
   }

}



