// A simple Host object model
export class DellApplianceInfo {
   applianceIp: string;
   version: string;
   versionStatus: string;
   connectionStatus: string;
   associatedVcenters: string[];
   selected: string;

   constructor(clone: DellApplianceInfo = null) {
      this.applianceIp = clone ? clone.applianceIp : "";
      this.version = clone ? clone.version : "";
      this.versionStatus = clone ? clone.versionStatus : "NA";
      this.connectionStatus = clone ? clone.connectionStatus : "";
      this.associatedVcenters = clone ? clone.associatedVcenters : [];
      this.selected = clone ? clone.selected : "";
   }
}


export class PHAEventsUI {
    
   eventId          : number;
   eventDescription : string;
   componentType    : string;
   currentSeverity  : string; 
   overriddenSeverity : string;

   constructor(clone:PHAEventsUI = null){
       this.eventId             =  clone ? clone.eventId:0;
       this.eventDescription    =  clone ? clone.eventDescription :"";
       this.componentType       =  clone ? clone.componentType : "";
       this.currentSeverity     =  clone ? clone.currentSeverity: "";
       this.overriddenSeverity  =  clone ? clone.overriddenSeverity : "";
       
   }

}


