import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";

import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { ServerHardware, DellServerFirmware, SelLog } from "./index";

/**
 * This HostServiceBase interface is optional for HostService but it helps
 * define a simple FakeHostService for testing purposes
 */
export interface DellHostHardwareServiceBase {
   getDellHostHardware(objectId: string): Promise<ServerHardware>;
   getDellHostFirmware(objectId: string): Promise<DellServerFirmware>;
   getDellHostSelLog(objectId: string): Promise<SelLog>;
}




@Injectable()
export class DellHostHardwareService implements DellHostHardwareServiceBase {
   
   propListHardware = [ "spectreServerHardware"];
   propListFirmware = [ "spectreServerFirmware"];
   propListSelLog = [ "spectreServerSelLog"];

   
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   /**
    * Build the REST url endpoint for retrieving a list of properties.
    * This is mapped to the DataAccessController on the java side.
    */
   private getDellHostHardwareUrl(objectId: string): string {
      let propStr = this.propListHardware.toString();
      let dataUrl = this.gs.getWebContextPath() +
            "/rest/data/properties/" + objectId +
            "?properties=" + propStr;
      return dataUrl;
   };
  
   private getDellHostFirmwareUrl(objectId: string): string {
      let propStr = this.propListFirmware.toString();
      let dataUrl = this.gs.getWebContextPath() +
            "/rest/data/properties/" + objectId +
            "?properties=" + propStr;
      return dataUrl;
   };
  
   private getDellHostSelLogUrl(objectId: string): string {
      let propStr = this.propListSelLog.toString();
      let dataUrl = this.gs.getWebContextPath() +
            "/rest/data/properties/" + objectId +
            "?properties=" + propStr;
      return dataUrl;
   };
  
   /**
    * Get all hosts with some default properties.
    * Note: this is only used for populating the sidenav component used in dev mode.
    *
    * @returns {Promise<Host[]>}
    */
   getDellHostHardware(objectId: string): Promise<ServerHardware> {
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();

      return this.http.get(this.getDellHostHardwareUrl(objectId), headers)
            .toPromise()
            // Normal response has a data field, mock response from db.json doesn't
            .then(response => (useLiveData ? response.json().spectreServerHardware : response.json()) as ServerHardware)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
  
   getDellHostFirmware(objectId: string): Promise<DellServerFirmware> {
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();

      return this.http.get(this.getDellHostFirmwareUrl(objectId), headers)
            .toPromise()
            // Normal response has a data field, mock response from db.json doesn't
            .then(response => (useLiveData ? response.json().spectreServerFirmware : response.json()) as DellServerFirmware)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }
  
   getDellHostSelLog(objectId: string): Promise<SelLog> {
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();

      return this.http.get(this.getDellHostSelLogUrl(objectId), headers)
            .toPromise()
            // Normal response has a data field, mock response from db.json doesn't
            .then(response => (useLiveData ? response.json().spectreServerSelLog : response.json()) as SelLog)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

}



