// A simple Host object model
export class Server {
    serverId    :  number;
    svcTag      :  string;
    model       :   string;
    platformGuid:   string;
    status      :   number;
    statusName  :   string;
    template    :   string;
    hostName    :   string;
    ipAddr      :   string;
    hostLOMMAC  :   string; 
    idracip     :   string;
    idracmac    :   string;
    idracUsername:  string;
    idracPassword:  string;
    CSIOREnabled :  boolean;
    availableFRWRVersions   :   FirmwareVersions[];
    installedFRWRVersions   :   FirmwareVersions[];
	idracnicDetails         :   IDRACNICDetail[];
    sdCardAvailable         :   boolean;
    lockdownStatus      :   string;    
	licenseCurrent      :   boolean;
	system12G           :   boolean;

   constructor(clone: Server = null) {
        this.serverId    =  clone ? clone.serverId:0;
        this.svcTag      =  clone ? clone.svcTag :"";
        this.model       =  clone ? clone.model:"";
        this.platformGuid=  clone ? clone.platformGuid:"";
        this.status      =  clone ? clone.status:0;
        this.statusName  =  clone ? clone.statusName:"";
        this.template    =  clone ? clone.template:"";
        this.hostName    =  clone ? clone.hostName:"";
        this.ipAddr      =  clone ? clone.ipAddr:"";
        this.hostLOMMAC  =  clone ? clone.hostLOMMAC:"";
        this.idracip     =  clone ? clone.idracip:"";
        this.idracmac    =  clone ? clone.idracmac:"";
        this.idracUsername=  clone ? clone.idracUsername:"";
        this.idracPassword= clone ? clone.idracPassword:"";
        this.CSIOREnabled = clone ? clone.CSIOREnabled:false;
        this.availableFRWRVersions  = clone ? clone.availableFRWRVersions:[];
        this.installedFRWRVersions  = clone ? clone.installedFRWRVersions:[];
	    this.idracnicDetails        = clone ? clone.idracnicDetails:[];
        this.sdCardAvailable        = clone ? clone.sdCardAvailable:false;
        this.lockdownStatus         = clone ? clone.lockdownStatus:"";
	    this.licenseCurrent         = clone ? clone.licenseCurrent:false;
	    this.system12G              = clone ? clone.system12G:false;
   }
}


export class FirmwareVersions {
    
    idracVersion    : string;
    lcVersion       : string;
    biosVersion     : string;
   
   constructor(clone:FirmwareVersions = null){
       this.idracVersion        =  clone ? clone.idracVersion:"";
       this.lcVersion           =  clone ? clone.lcVersion :"";
       this.biosVersion         =  clone ? clone.biosVersion : "";
       
   }

}

export class IDRACNICDetail {
    
    
    iDRACNicDetailsId    : number;
    currentMacAddress    : string;
    permanentMacAddress  : string;
    flexMacAddress       : string;
    productName          : string;
    fqdd                 : string;
    displayOrder         : number     
   
   constructor(clone:IDRACNICDetail = null){
       this.iDRACNicDetailsId   =  clone ? clone.iDRACNicDetailsId:0;
       this.currentMacAddress   =  clone ? clone.currentMacAddress :"";
       this.permanentMacAddress =  clone ? clone.permanentMacAddress : "";
       this.flexMacAddress      =  clone ? clone.flexMacAddress : "";
       this.productName         =  clone ? clone.productName :"";
       this.fqdd                =  clone ? clone.fqdd:"";
       this.displayOrder        =  clone ? clone.displayOrder:0;
   }

}

export class NonProvisionedServer{

    serverAddress : string;
    userName      : string;
    password      : string;
    
    constructor(clone:NonProvisionedServer = null){
        this.serverAddress  =   clone ? clone.serverAddress :"";
        this.userName       =   clone ? clone.userName : "";
        this.password       =   clone ? clone.password :"";
    }
}

export class ProvisioningRequest{
    serverList :Server[];
    actions    :any [];

    constructor(clone:ProvisioningRequest = null){
        this.serverList  =  clone ? clone.serverList:[];
        this.actions     =  clone ? clone.actions:[];
    }
}


