@Component({
   selector: "deployment-baremetal",
   styleUrls: ["./deployment-baremetal.component.scss"],
   templateUrl:"./deployment-baremetal.component.html"
  })




export class DeploymentBaremetalComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() inputParam:string;
  childNavStatus="";
  chassisStatus=false;
  instance: DeploymentBaremetalComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  collections=[];
  tabval:string;  
  FIND_DISCOVERED_NON_PROVISIONING_SERVER :string ="FIND_DISCOVERED_NON_PROVISIONING_SERVER";
  DISCOVER_NEW_NON_PROVISIONING_SERVER:string     ="DISCOVER_NEW_NON_PROVISIONING_SERVER"
  DELETE_NON_PROVISIONING_SERVER      :string     ="DELETE_NON_PROVISIONING_SERVER";
  IDRAC_LICENSE_COMPLIANT     :string             ="COMPLIANT";
  IDRAC_LICENSE_NONCOMPLIANT  :string             ="NONCOMPLIANT";
  IDRAC_LICENSE_UNKNOWN       :string             ="UNKNOWN";
  opened:boolean=false;
  compliantCount:number=0;
  nonCompliantCount:number=0;
  serverQuarantinedCount:number=0;
  serverWithoutLicenceCount:number=0;
  serverWithLicenceCount:number=0;
  deployMentsData:any=[];
  details:boolean=false;

  
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private _dellBareMetalService:DellBareMetalService,
               private route: ActivatedRoute) {
     this.instance = this;
     this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void 
   {  
      this.nav.setViewType('summary');
      this.refreshView();
   }

   openNonCompliance(){}

   private refreshView(): void { this.loading=false;
    var request:ProvisioningRequest = new ProvisioningRequest();
    request.actions.push(this.FIND_DISCOVERED_NON_PROVISIONING_SERVER);
    
    this._dellBareMetalService.getDellBareMetals(request).subscribe(
              (response) => {
                console.log(response);
                this.deployMentsData=response;
                this.getTopStatus(response);
              },
              (err)=>console.log(err),
              ()=>console.log("completed the bare metals")
          
          );  

    }

   editGridrow(){ this.opened=true;} 

   closemodal(event){ this.opened=event; }


   getComplianceCount(data){
       if(data.statusName=='Quarantined'){
         this.serverQuarantinedCount +=1
       }
       if(data.license.licenseComplianceStatus==="UNKNOWN"){
        this.nonCompliantCount +=1;
      }else{
         this.compliantCount +=1;
      }
   }

   getTopStatus(dataCollection){
    let tempnonCompliantCount0;
    let tempserverQuarantinedCount;
    let serverWithoutLicenceCount;
    let serverWithLicenceCount;
      for(let i=0;i<=dataCollection.length;i++){
        let nonTempCount=0
          if(this.getLicenseComplianceStatus(dataCollection[i])=='Non compliant'){
            dataCollection.tempcompliantCount0 =nonTempCount+1;
          }else{
            dataCollection.tempcompliantCount0 +=1;
          }
      }

      console.log(dataCollection);
    }
    
    getLicenseComplianceStatus(item): string {
      console.log(item);
      let status="Unknown";
      if(item){
        if(item.license && item.license.licenseComplianceStatus.toLowerCase() == "unknown") 
          return status='Non compliant';
        else if(item.license && item.license.licenseComplianceStatus==this.IDRAC_LICENSE_COMPLIANT)
          return status= 'compliant';
        else if (item.license && item.license.licenseComplianceStatus==this.IDRAC_LICENSE_NONCOMPLIANT){
          return status= 'Non compliant';  //this.nononCompliantLicenseCount++;
       }
       
      }
      return status;
      
  }
  
   
}


