
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellConnectionProfileService, NavService } from "../../services/index";
import { UserSettingService } from "../../shared/user-settings.service";

@Component({
   selector: "monitor-profile",
   styleUrls: ["./monitor-profile.component.scss"],
   templateUrl:"./monitor-profile.component.html"
})

export class MonitorProfileComponent  implements OnInit,OnDestroy 
{
  private refreshViewSub: Subscription;
  @Input() monitorParam:string;
  openval=false;
  mainNavStatus:string;
  instance: MonitorProfileComponent;
  loading: boolean = true;
  error:any;
  testStatus:boolean=false; 
  demoCollapsible:boolean=true;
  

  
  constructor(private gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellConnectionProfileService: DellConnectionProfileService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private userSettingService: UserSettingService,
               private route: ActivatedRoute) {
				  this.instance = this;
				  this.refreshViewSub = refreshService.refreshObservable$.subscribe(
				  () => this.refreshView());
			  }

   ngOnInit(): void {
    this.getNavCurrentStatus() 
    this.nav.setViewType('summary'); }
   
   connectionNav(linkVal):any{ 
     this.mainNavStatus=linkVal;
     this.onTabSelected(linkVal);
  }

  private getNavCurrentStatus():void{
    let savedTab: string = this.userSettingService.getSetting("manageNav");
    if (savedTab !== "Deployment Information" && savedTab !== "Deployment Templates" && savedTab !== "Bare Metal Servers" && savedTab !== "Credential Profiles" && savedTab !== "HA" && savedTab !== "Settings" ) {
       savedTab = "Compliance";
    }
    this.mainNavStatus = savedTab;
   }

   statusChange(val):any{   this.openval=val; }

   testWizard():any{  this.testStatus=true; }

   testWizardEmit(status):any{  this.testStatus=status;  }

   onTabSelected(id: string) {
    this.appAlertService.closeAlert();
    this.userSettingService.setSetting("manageNav", id);
    this.refreshView();
 }

   private refreshView(): void {
      this.getNavCurrentStatus();
   }

   ngOnDestroy(): void {
    this.refreshViewSub.unsubscribe();
 }


   

}


