import { Component, Input,Output, OnInit, ViewChild,EventEmitter,OnChanges,SimpleChanges } from "@angular/core";
import { Wizard } from "clarity-angular";

@Component({
   selector: "monitor-wizard-chassisprofilecreated",
   styleUrls: ["./monitor-wizard-chassisprofilecreated.component.scss"],
   templateUrl: "./monitor-wizard-chassisprofilecreated.component.html"  
})

export class ChassisProfileCreated implements OnInit {
    @Input()chassisProfile:boolean;
    @Output() closemodal=new EventEmitter<boolean>();
    @Input() ProfileName:boolean;
    chassisDelete:boolean=false;
    @Input() chassiscredentialprofile:any;
    chassisprofileVal:any;
    constructor() {}
    ngOnInit() { } 
    
    closeModal(){
        this.chassisProfile=false;
        this.closemodal.emit(this.chassisProfile);
        this.chassisDelete=true;
        this.chassisprofileVal=this.chassiscredentialprofile;

    }
   closedeletemodal(event){
       this.chassisDelete=event;
   }
}


