import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import {FormGroup, FormControl, Validators} from "@angular/forms";

@Component({
  selector: "deployment-description",
  styleUrls: ["./deployment-description.component.scss"],
  templateUrl:"./deployment-description.component.html"
})

export class DeploymentDescriptionComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Output() deploymentprofileUpdate = new EventEmitter<boolean>();
  @Output() formValue = new EventEmitter<any>();
  @Input() submitted:boolean;
  model = {
        name: "",
        description: ""
  };
   deploymentprofile = new FormGroup({
        profileName: new FormControl('', Validators.required),
        profileDescription:new FormControl()
   });
   
  constructor(){}

  ngOnInit(): void{}
   
  private refreshView(): void {}

  formStatusChanged(){
    if(this.deploymentprofile.valid && this.model.name.length!=0){
      this.deploymentprofileUpdate.emit(true);
      this.formValue.emit(this.deploymentprofile);
    }
    else
      this.deploymentprofileUpdate.emit(false);
  }
}


