
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";


@Component({
   selector: "hardware-bootorder",
   styleUrls: ["./hardware-bootorder.component.scss"],
  templateUrl:"./hardware-bootorder.component.html"
})

export class HardwarebootorderComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  filterCategory=[{'boot':0,'name':'permanent device','status':false,'type':'bios'},{'boot':1,'name':'permanent device1','status':false,'type':'bios'}];
  bios=[{'boot':0,'name':'permanent device','status':false,'type':'bios'},{'boot':1,'name':'permanent device1','status':false,'type':'bios'}];
  uefi=[{'boot':0,'name':'permanent device','status':false,'type':'uefi'},{'boot':1,'name':'permanent device1','status':false,'type':'uefi'}];
  hardDrive=[{'boot':0,'name':'permanent device','status':false,'type':'BCV','instanceId':'BCV-BIOS setup1'},{'boot':1,'name':'permanent device1','status':false,'type':'BCV','instanceId':'BCV-BIOS setup1'}];
  constructor() {}

  ngOnInit(): void {}
   
  private refreshView(): void {}

  selectionChange(event){
      switch(event){
            case 'bios':						 
              this.filterCategory = this.bios;
            break;					 
            case 'uefi':
              this.filterCategory =this.uefi;						 
            break;
    } 
  }
}


