import { Injectable } from "@angular/core";
import { Http } from "@angular/http";
import "rxjs/add/operator/toPromise";
import {Observable} from 'rxjs/Observable';

import { GlobalsService, AppAlertService, APP_CONFIG } from "../../shared/index";
import { AppErrorHandler } from "../../shared/appErrorHandler";
import { ServerHardware,DellHostGroupObject, DellServerFirmware, DellHostGroupListObject, SelLog } from "../index";

/**
 * This HostServiceBase interface is optional for HostService but it helps
 * define a simple FakeHostService for testing purposes
 */
export interface DellDatacenterHardwareServiceBase {
   getDellDatacenterHardware(objectId: string): Promise<DellHostGroupListObject>;
   getDellDatacenterHostGroup(objectId:string,hostList:string[]):Observable<DellHostGroupObject>;
}




@Injectable()
export class DellDatacenterHardwareService implements DellDatacenterHardwareServiceBase {
   
   propListHardware = [ "spectreServerHardware"];
   propListFirmware = [ "spectreServerFirmware"];
   propListSelLog = [ "spectreServerSelLog"];

   
   constructor(private http: Http,
               private errorHandler: AppErrorHandler,
               private gs: GlobalsService,
               private appAlertService: AppAlertService) {
   }

   private  SerivceUrl(){
      return "/rest/clusters/";
   }

   /**
    * Build the REST url endpoint for retrieving a list of properties.
    * This is mapped to the DataAccessController on the java side.
    */
   private getDellDatacenterHardwareUrl(objectId: string):string {
      let  dataUrl = this.gs.getWebContextPath() +this.SerivceUrl()+"hostlist/"+ objectId;
      return dataUrl;
  
   };

   private getDellDatacenterHostGroupUrl(objectId: string,hostList:string[]):string {
      
      let hostsParams   = hostList.toString();
    //  var objId   = objectId.toString();
      let  dataUrl = this.gs.getWebContextPath() +this.SerivceUrl()+"hostgroupdata"+
                    "?objectId="+ objectId +"&hosts=" +hostsParams;
      return dataUrl;
  
   };
  
   private getDellDatacenterFirmwareUrl(objectId: string): string {
      let propStr = this.propListFirmware.toString();
      let dataUrl = this.gs.getWebContextPath() +
            "/rest/data/properties/" + objectId +
            "?properties=" + propStr;
      return dataUrl;
   };
  
   private getDellDatacenterSelLogUrl(objectId: string): string {
      let propStr = this.propListSelLog.toString();
      let dataUrl = this.gs.getWebContextPath() +
            "/rest/data/properties/" + objectId +
            "?properties=" + propStr;
      return dataUrl;
   };
  
   /**
    * Get all hosts with some default properties.
    * Note: this is only used for populating the sidenav component used in dev mode.
    *
    * @returns {Promise<Host[]>}
    */
   getDellDatacenterHardware(objectId: string): Promise<DellHostGroupListObject> {
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();

      return this.http.get(this.getDellDatacenterHardwareUrl(objectId), headers)
            .toPromise()
            // Normal response has a data field, mock response from db.json doesn't
            .then(response => (response.json()) as DellHostGroupListObject)
            .catch(error => this.errorHandler.httpPromiseError(error));
   }

   
   getDellDatacenterHostGroup(objectId: string,hostList:string[]): Observable<DellHostGroupObject> {
      let headers = this.gs.getHttpHeaders();
      let useLiveData = this.gs.useLiveData();
      
      return this.http.get(this.getDellDatacenterHostGroupUrl(objectId,hostList), headers)
            .map(response  =>(response.json()).hostDetails as DellHostGroupObject)
            .catch(error=>this.errorHandler.httpPromiseError(error));      

            // Normal response has a data field, mock response from db.json doesn't
            //.then(response => (response.json()) as DellHostGroupObject)
//             .then(response => console.log(response.json())) 
            //.catch(error => this.errorHandler.httpPromiseError(error));
  //          .catch(error => this.errorHandler.httpPromiseError(error));
   }

  
}



