import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import {DellServerFirmware, SelLog, ServerHardware, DellHostHardwareService,
        NavService, Controller, Connector, Enclosure, PhysicalDisk, VirtualDisk
       } from "../../services/index";

@Component({
   selector: 'hardware-storage',
   styleUrls: ['./hardware-storage.component.scss'],
   templateUrl: './hardware-storage.component.html'
   })
  
export class HardwareStorageComponent implements OnInit {
    private refreshViewSub: Subscription;
	hostId: string;
	hardware: ServerHardware
	@Input() status:string;
    @Input() hardwareObj;
    public hardwares;
	selected = 'Virtual Disks';
    filterValue = 'All Physical Disks';
    controllers: Controller[];
    virtualDisks: VirtualDisk[];
    enclosures: Enclosure[];
    physicalDisks: PhysicalDisk[];
    dedicatedHotSpares: PhysicalDisk[];
    globalHotSpares: PhysicalDisk[];
    physicalDiskFilter: string[];
    tempPhysicalDisks: PhysicalDisk[];
    physicalDisksForVirtualDisks;
    titleKey: string;
    rowActive="";
    instance: HardwareStorageComponent;
    errorMessage="";
    loading: boolean = true;
    

    constructor(private gs: GlobalsService,
                private appAlertService: AppAlertService,
                private dellHardwareService: DellHostHardwareService,
                public  i18n: I18nService,
                private location: Location,
                public  nav: NavService,
                private refreshService: RefreshService,
                private route: ActivatedRoute) {
           this.instance = this;
           this.refreshViewSub = refreshService.refreshObservable$.subscribe(
                () => this.refreshView());
    }

   storageChanged(selected)
   { 
      this.selected = selected;
     if(selected !=='Physical Disks'){
        this.filterValue="All Physical Disks";
     }
	  this.rowActive="";
    }

    selectRow(selectedrow){ this.rowActive=selectedrow; }
	  
	filterChanged(filter)
    { 
        this.rowActive="";
		this.filterValue = filter;
        if(filter==='Global Hot Spares' || filter==='Dedicated Hot Spares'){
                   this.selected='Physical Disks';     
        }

         if(filter == 'All Physical Disks')
            {
                this.tempPhysicalDisks = this.physicalDisks;
            }
            else if(filter == 'Global Hot Spares')
            {
                this.tempPhysicalDisks = this.globalHotSpares;
            }
             else if(filter == 'Dedicated Hot Spares')
            {   
                this.tempPhysicalDisks = this.dedicatedHotSpares;
            }
            else
            {
                this.tempPhysicalDisks = this.physicalDisksForVirtualDisks[filter];
            }
        }
        
        
    ngOnInit(): void {
        this.route.params.forEach((params: Params) => {
            this.hostId = params["id"];
        });

        this.nav.setViewType('summary');
        this.status="Storage";
        this.refreshView();
    }

    ngOnDestroy(): void { this.refreshViewSub.unsubscribe();}

    private refreshView(): void {
          if(this.hardwareObj){
              this.hardware = this.hardwareObj;
               this.updateStorageDetails();
          }else{
              this.dellHardwareService.getDellHostHardware(this.hostId)
                .then(hardware => 
                {
                this.hardware = hardware;
                 this.updateStorageDetails();
                },error=>{
                  this.errorMessage=error;
                 
                });  
          }   
         this.updateStorageDetails();
               
}
 private updateStorageDetails(): void
 {		        
                var hotSpareDiskKeys:string[] = [];
                var unconfiguredDisks:string[] = [];
                var configuredDisks:string[] = [];
                var controllers: Controller[] = [];
                var virtualDisks: VirtualDisk[] = [];
                var enclosures: Enclosure[] = [];
                var physicalDisks: PhysicalDisk[] = [];
                var dedicatedHotSpares: PhysicalDisk[] = [];
                var globalHotSpares: PhysicalDisk[] = [];
                var physicalDisksForVirtualDisks: { [id: string] : PhysicalDisk[]; } = {};
                var DEDICATED_HOT_SPARES:String ="DEDICATED";
                var GLOBAL_HOT_SPARES:String ="GLOBAL";
               if(this.hardware){
                        for(let controller of this.hardware.controllers) 
                        {
                            controllers.push(controller);

                            for (let virtualDisk of controller.virtualDisks) 
                            {
                                virtualDisk.physicalDiskKeySize = virtualDisk.physicalDiskKey.length;
                                virtualDisks.push(virtualDisk);					
                            }

                            for (let connector of controller.connectors)
                            {
                                for (let physicalDisk of connector.physicalDisks) {
                                    //  Physical disks may exist directly in the connectors
                                    physicalDisk.connectorId=connector.connectorId;
                                    physicalDisk.controllerId=controller.controllerId;
                                    physicalDisks.push(physicalDisk);
                                    if(physicalDisk.hotSpare == DEDICATED_HOT_SPARES) { 
                                        // Check hotspare status
                                        dedicatedHotSpares.push(physicalDisk);
                                        hotSpareDiskKeys.push(physicalDisk.physicalDiskKey);
                                    } else if(physicalDisk.hotSpare == GLOBAL_HOT_SPARES) {
                                        globalHotSpares.push(physicalDisk);
                                        hotSpareDiskKeys.push(physicalDisk.physicalDiskKey);
                                    } 
                            }
                                
                            for (let enclosure of connector.enclosures) {
                                    enclosure.connectorKey = connector.connectorKey;
                                    enclosure.connectorId = connector.connectorId;
                                    enclosures.push(enclosure);
                                    for (let physicalDisk of enclosure.physicalDisks) {
                                        physicalDisk.controllerId=controller.controllerId;
                                        physicalDisk.connectorId=connector.connectorId;
                                        physicalDisk.enclosureId=enclosure.enclosureId;
                                        // Same as the physical disks in Connectors, just in Enclosures this time.
                                        physicalDisks.push(physicalDisk);
                                        if(physicalDisk.hotSpare == DEDICATED_HOT_SPARES) {
                                            dedicatedHotSpares.push(physicalDisk);
                                            hotSpareDiskKeys.push(physicalDisk.physicalDiskKey);
                                        } else if(physicalDisk.hotSpare == GLOBAL_HOT_SPARES) {
                                            globalHotSpares.push(physicalDisk);
                                            hotSpareDiskKeys.push(physicalDisk.physicalDiskKey);
                                        } 
                                    }
                                }
                            }
                        }
                        
                        for (let pd of physicalDisks) {
                            if(hotSpareDiskKeys)
                            {
                                if(hotSpareDiskKeys.indexOf(pd.physicalDiskKey) > -1) { 						
                                    // Hot spare should not show as unconfigured
                                    unconfiguredDisks.push(pd.physicalDiskKey);
                                }
                            }
                            else
                            {
                                unconfiguredDisks.push(pd.physicalDiskKey);
                            }
                        }
                        
                        // Create the arraycollections of physical disks for all virtual disks.
                        for (var vd of virtualDisks) {
                            var pds:PhysicalDisk[]= [];
                            // Create the arraycollections of physical disks for all virtual disks.
                            for (let pdID of vd.physicalDiskKey) {
                                
                                // Disks that are part of a VD are considered 'configured'
                                if(!(configuredDisks.indexOf(pdID) > -1)) 
                                {    
                                    configuredDisks.push(pdID);
                                    //need to ensure that PD is actually in the unconfiguredDisks list before trying to remove  
                                    if(unconfiguredDisks.indexOf(pdID)!=-1)
                                        unconfiguredDisks.splice(unconfiguredDisks.indexOf(pdID),1);
                                }
                                
                                for (let pd of physicalDisks) {
                                    if(pd.physicalDiskKey == pdID) {
                                        pds.push(pd);
                                        pd.virtualDisk= vd.name;
                                    }
                                }					
                            }		
                            
                            physicalDisksForVirtualDisks[vd.deviceName] = pds;
                        }	
                        
                        for (let pDisk of physicalDisks){
                            if(configuredDisks.indexOf(pDisk.physicalDiskKey) > -1 || hotSpareDiskKeys.indexOf(pDisk.physicalDiskKey)){
                                pDisk.configured='YES';
                            }
                            else
                            {
                                pDisk.configured='NO';
                            }
                        }
                        
                        for (var ghs of globalHotSpares){
                            if(configuredDisks.indexOf(ghs.physicalDiskKey)  > -1 || hotSpareDiskKeys.indexOf(ghs.physicalDiskKey)  > -1){
                                ghs.configured='YES';
                            }else{
                                ghs.configured='NO';
                            }
                        }
                        
                        for (let dhs of dedicatedHotSpares){
                            if(configuredDisks.indexOf(dhs.physicalDiskKey)  > -1 || hotSpareDiskKeys.indexOf(dhs.physicalDiskKey)  > -1){
                                dhs.configured='YES';
                            }else{
                                dhs.configured='NO';
                            }
                        }
                        

                        for (var key in physicalDisksForVirtualDisks){
                            var vdArrayCollection = physicalDisksForVirtualDisks[key];
                            for (var vdPDs of vdArrayCollection)
                            {
                                if(configuredDisks.indexOf(vdPDs.physicalDiskKey)  > -1 || hotSpareDiskKeys.indexOf(vdPDs.physicalDiskKey)  > -1)
                                {
                                    vdPDs.configured='YES';
                                }
                                else
                                {
                                    vdPDs.configured='NO';
                                }
                            }
                        }
                    
               } 
                

                this.controllers = controllers;
                this.virtualDisks = virtualDisks;
                this.enclosures = enclosures;
                this.physicalDisks = physicalDisks;
                this.dedicatedHotSpares = dedicatedHotSpares;
                this.globalHotSpares =  globalHotSpares;
                this.tempPhysicalDisks = this.physicalDisks;

                
                this.physicalDiskFilter = [];
                this.physicalDiskFilter.push("All Physical Disks");
                this.physicalDiskFilter.push("Global Hot Spares");
                this.physicalDiskFilter.push("Dedicated Hot Spares");
                this.physicalDisksForVirtualDisks = physicalDisksForVirtualDisks;

                for (var virtualDisk of virtualDisks)
                {
                    if ( virtualDisk.name != null && virtualDisk.name != "" ) 
                    {
                        this.physicalDiskFilter.push(virtualDisk.name);
                    }
                    else 
                    {
                        this.physicalDiskFilter.push(virtualDisk.userFriendlyFQDD);
                    }

                }
            }

}



