// Server Hardware
export class ServerHardware 
{
    frus: FRU[];
    memoryInfo: MemoryInfo;
    nicCount: string;
    powerSupplyCount: string;
    processorCount: string;
    pciSlotsTotal: string;
    pciSlotsUsed: string;
    nics: NIC[];
    ipAddressList: string[] ;
    pciSlots: PCISlot[] ;
    powerSupplies:  PowerSupply[] ;
    processors: Processor[];
    remoteAccessCards: RemoteAccessCard ;
    warranty: Warranty[] ;
    warrantyRenewal: WarrantyRenewal;
    vcenterAddress: string;
    controllers: Controller[];
    powerMonitoring: PowerMonitoring;  
//  
  constructor(clone: ServerHardware = null) {
      this.frus = clone ? clone.frus : [];
      this.memoryInfo = clone ? clone.memoryInfo : null;
      this.nicCount = clone ? clone.nicCount : "";
      this.powerSupplyCount = clone ? clone.powerSupplyCount : "";
      this.processorCount = clone ? clone.processorCount : "NA";
      this.pciSlotsTotal = clone ? clone.pciSlotsTotal : "";
      this.pciSlotsUsed = clone ? clone.pciSlotsUsed : "";
      this.nics = clone ? clone.nics : [];
      this.ipAddressList = clone ? clone.ipAddressList : [];
      this.pciSlots = clone ? clone.pciSlots : [];
      this.powerSupplies = clone ? clone.powerSupplies : [];
      this.processors = clone ? clone.processors : [];
      this.remoteAccessCards = clone ? clone.remoteAccessCards : null;
      this.warranty = clone ? clone.warranty : [];
      this.warrantyRenewal = clone ? clone.warrantyRenewal: null;
      this.vcenterAddress = clone ? clone.vcenterAddress : "";
      this.controllers = clone ? clone.controllers : [];
      this.powerMonitoring = clone ? clone.powerMonitoring : null;
   }
}
