import { Component, OnInit, OnDestroy, ViewChild, ViewChildren } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { DellHostOverview, DellHostOverviewService, NavService } from "../../services/index";

@Component({
   selector: 'summary-info',
   styleUrls: ['./summary-info.component.scss'],
   templateUrl: './summary-info.component.html'
})
  
export class SummaryInfoComponent implements OnInit, OnDestroy {
   private refreshViewSub: Subscription;
   hostId: string;
   overview: DellHostOverview;
   hypervisor: string;
   titleKey: string;
   error:any;
   instance: SummaryInfoComponent;
  
   constructor(public gs: GlobalsService,
               private appAlertService: AppAlertService,
               private dellOverviewService: DellHostOverviewService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute) {

      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void {
      this.route.params.forEach((params: Params) => {
         this.hostId = params["id"];
      });
      this.nav.setViewType('summary');
      this.refreshView();
   }

   ngOnDestroy(): void { this.refreshViewSub.unsubscribe();}

   private refreshView(): void {
      this.dellOverviewService.getDellHostOverview(this.hostId)
            .then(overview => 
            {
               this.overview = overview;
               this.hypervisor = this.overview.operatingSystem +" "+ this.overview.operatingSystemVersion;
            },error=>{
                  this.error=error;
            });
   }

}



