
import { Component, OnInit, OnDestroy, ViewChild, ViewChildren, Input,Output,EventEmitter } from "@angular/core";
import { Location }     from "@angular/common";
import { ActivatedRoute, Params } from "@angular/router";
import { Subscription } from "rxjs";
import { GlobalsService, RefreshService, I18nService,
         APP_CONFIG, AppAlertService }   from "../../shared/index";
import { ConnectionProfile, DellConnectionProfileService, NavService , DellTaskschedulerService } from "../../services/index";

@Component({
   selector: "hosts-inventory",
   styleUrls: ["./hosts-inventory.component.scss"],
   templateUrl:"./hosts-inventory.component.html"
})

export class HostsInventoryComponent  implements OnInit 
{
  private refreshViewSub: Subscription;
  @Input() jobNavStatus:string;
  profiles: ConnectionProfile[];
  childNavStatus="";
  chassisStatus=false;
  instance: HostsInventoryComponent;
  TotalProfile:number=0;
  loading: boolean = true;
  hostsData=[];  
  selected_job=[];  
  date:any=new Date().getTime().toString()
  WARRANTY:string = "Warranty"; 
  INVENTORY:string = "Inventory";
	UPDATEAVAILABILITY:string = "UpdateAvailability";
 
  constructor(private gs: GlobalsService,
               public  i18n: I18nService,
               private location: Location,
               public  nav: NavService,
               private refreshService: RefreshService,
               private route: ActivatedRoute,
               private _dellTaskschedulerService:DellTaskschedulerService
            ) {
      this.instance = this;
      this.refreshViewSub = refreshService.refreshObservable$.subscribe(
            () => this.refreshView());
   }

   ngOnInit(): void 
   {  
      this.nav.setViewType('summary');
      this.refreshView();
   }
 
   connectionNav(linkVal)
   {
		  this.jobNavStatus=linkVal;
		  this.childNavStatus="";
   }
   
   private refreshView(): void {
          this._dellTaskschedulerService.getHostJobs().
          then(response => 
          {
           console.log(response);  
          },
          error=>{
              console.log(error);
          });

          this._dellTaskschedulerService.getHostAllJobs().subscribe(
              (response) => {
                console.log(response);
              //  this.deployMentsData=response;
              },
              (err)=>console.log(err),
              ()=>console.log("completed the bare metals"));

   }
}


